import React from "react";
import VInput from "Components/common/VInput";
import { Link } from "react-router-dom";

const Login = props => {
  const loginLink = {
    fontSize: "18px",
    textDecoration: "underline"
  };

  let classobj = {};
  if (props.path == "/warehouse/login") {
    classobj = {
      wrapper: "regWarpper1",
      buttonClass: "button-action",
      fillColor: "#3ab54a",
      registerUrl: "/warehouse/register"
    };
  } else {
    classobj = {
      wrapper: "",
      buttonClass: "button-primary",
      fillColor: "#0070bb",
      registerUrl: "/findspace/register"
    };
  }
  //   const emialLink = Link.state(this, "Email").check(x => x, "Email Required");
  //   const passwordLink = Link.state(this, "Password").check(
  //     x => x,
  //     "Password Required"
  //   );

  return (
    <div className={"regWarpper" + " " + classobj.wrapper}>
      <span className="regShape regShape1">
        <svg
          className="svg1 active"
          x="0px"
          y="0px"
          width="2000px"
          height="2000px"
          viewBox="0 0 2000 2000"
          enableBackground="new 0 0 2000 2000"
        >
          <path
            id="mPath1"
            fill="#ffffff"
            d="M2000,0c0,0-906,252.556-1999,321v1679h1999V0z"
          />
        </svg>
        <svg
          className="svg2 translateY"
          x="0px"
          y="0px"
          width="2000px"
          height="2000px"
          viewBox="0 0 2000 2000"
          enableBackground="new 0 0 2000 2000"
          xmlSpace="preserve"
        >
          {/*xml:space="preserve"*/}
          <path
            id="mPath2"
            fill={classobj.fillColor}
            d="M2000,0c0,0-906,252.556-1999,321v1679h1999V0z"
          />
        </svg>
        <div className="svg1 hidden-md hidden-lg active" />
        <div className="svg2 hidden-md hidden-lg translateY" />
        <div className="buttonWarp">
          {/*<a href="javascript:void(0);" className="button button-border button-fade button-uppercase SW-Button SW-Button1">sign up</a>*/}
        </div>
      </span>
      <div className="container">
        <div className="row">
          <div className="col-xs-12 col-md-5 col-lg-4">
            <div id="signIn" className="signInForm formOuterWarp">
              <h2>Sign In</h2>
              <div className="formWarp">
                <div className="form-group">
                  <VInput
                    type="text"
                    className="form-control"
                    name="Email"
                    placeholder="Email"
                    onKeyPress={props.onKeyPress}
                    valueLink={props.links.emialLink}
                  />
                </div>
                <div className="form-group">
                  <VInput
                    type="password"
                    className="form-control"
                    name="Password"
                    placeholder="Password"
                    onKeyPress={props.onKeyPress}
                    valueLink={props.links.passwordLink}
                  />
                </div>
                <div className="form-group">
                  <input
                    type="button"
                    className={
                      "button button-border" +
                      " " +
                      classobj.buttonClass +
                      " button-uppercase margin-top-3x"
                    }
                    onClick={props.onClick}
                    value="LogIn"
                  />
                </div>
                <div className="register-here">
                  <p>
                    Not Registered?{" "}
                    <Link
                      to={classobj.registerUrl}
                      style={loginLink}
                      className="SW-Button SW-Button1"
                    >
                      Click here
                    </Link>
                  </p>
                </div>
                <div className="form-group">
                  <a
                    href="javascript:void(0);"
                    className="forgotLink"
                    className="forgotLink"
                    className="forgotLink"
                    onClick={props.ForgotPassword}
                    style={{ color: "blue" }}
                  >
                    Forgot Password ?
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div className="col-xs-12 col-md-6 col-md-offset-1 col-lg-6 col-lg-offset-2 rightPart">
            <div class="row">
              <div class="col-xs-12 col-sm-6 form-group">
                <div class="cInfo">
                  <i class="icon-email-o" />
                  <h5>INFO</h5>
                  <a href="mailto:Serafina@LogistCompare.com">
                    Serafina@LogistCompare.com
                  </a>
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 form-group">
                <div class="cInfo">
                  <i class="icon-phone-o" />
                  <h5>PHONE</h5>
                  <span>(+44) 07956 664569</span>
                </div>
              </div>
            </div>
            <div className="__video">
              <h3>
                {" "}
                How to{" "}
                {props.path == "/warehouse/login" ? "list space" : "find space"}
              </h3>
              <iframe
                id="findframe"
                width="100%"
                height="300"
                src={props.State.Videos}
                frameBorder="0"
                allowFullScreen
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = Login;



