import React from "react";
import Details from "./details.js";
import EditDetails from "./detailsEdit.js";
import VInput from "Components/common/VInput";
import MetaTags from 'react-meta-tags';
const add = props => {
  let details = props.model.IsEdit ? (
    <EditDetails {...props} />
  ) : (
      <Details {...props} />
    );
  return (
    <section id="MainWarpper">
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="container">
        <div class="row">
          <div class="col-xs-12">
            <div class="addWhWarpper">
              <div class="Rowhead">
                <div class="col-head-6">
                  <h2 class="h2Title margin-bottom-6x">Add Details</h2>
                </div>
                <div class="col-head-6">
                  <div class="backBtn">
                    <a href="/warehouse/list">
                      <i class="fa fa-arrow-left" aria-hidden="true" />
                      Go to Back Dashboard
                    </a>
                  </div>
                </div>
              </div>
              {details}

              <div class="row">
                <div class="col-xs-12 form-group">
                  <a>
                    <input
                      type="button"
                      class="mrgn-right button button-border button-action button-uppercase button-min-width fw600  margin-top-4x"
                      value={props.model._id ? "Update" : "Submit"}
                      onClick={props.onSubmit}
                    />
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
module.exports = add;



