import { http } from "Helpers/axios.helper.js";
import Helper from "Helpers/utility.helper.js";
export default class NotificationService {
  static Save(data) {
    const url = "/api/notification/save";
    return http.post(url, data);
  }

  static GetAll(data) {
    const url = "/api/notification/get?skip=" + data;
    return http.get(url);
  }
  static GetCount() {
    const url = "/api/notification/count";
    return http.get(url);
  }
}



