import React, { Component } from "react";
import SearchService from "../../services/search.service.js";
import WarehouseService from "Services/warehouse.service.js";
import SavedSearch from "./savedSearch.js";
import Loader from "Components/common/loader.js";

const viewModel = {
  Name: "",
  Capacity: "",
  CapacityType: "",
  Address: "",
  City: "",
  PostCode: "",
  Website: "",
  Starthrs: "",
  Endhrs: "",
  About: "",
  StorageType: "Pallet",
  Services: {},
  WarehouseType: {},
  Security: {},
  Images: [],
  PendingSpace: "",
  UserId: "",
  lat: "",
  lng: "",
  Bulk: {
    MaxLength: "",
    MaxLengthType: "",
    MaxWidth: "",
    MaxWidthType: "",
    MaxWeight: "",
    MaxWeightType: "",
    MaxHeight: "",
    MaxHeightType: "",
    AvailabilityFrom: "",
    AvailabilityTo: "",
    ApxPrice: "",
    ApxPriceType: "",
    TotalSpace: "",
    Description: ""
  },
  Pallet: {
    Type: { Name: "", Value: "" },
    Total: 0,
    MaxHeight: "",
    MaxHeightType: "",
    ApxPrice: 0,
    AvailabilityTo: "",
    AvailabilityFrom: "",
    Images: ""
  }
};
const reviewModel = {
  Id: "",
  Rating: "",
  Description: ""
};
export default class searchContainer extends Component {
  constructor() {
    super();

    this.state = {
      Result: [],
      Detail: { ...viewModel },
      Reviews: [],
      isShowMap: false,
      isShowDetail: false,
      isLoading: true,
      center: {
        lat: -25.363882,
        lng: 131.044922
      }
    };

    this.closeMap = this.closeMap.bind(this);
    this.closeDetail = this.closeDetail.bind(this);
    this.onMapView = this.onMapView.bind(this);
    this.getRating = this.getRating.bind(this);
    this.getDetail = this.getDetail.bind(this);
  }
  closeMap() {
    this.setState({ isShowMap: false });
  }
  closeDetail() {
    this.setState({ isShowDetail: false });
    $("body").removeClass("modal-open");
  }
  onMapView(id) {
    const detail = this.state.Result.find(x => x.WarehouseId == id)
      .warehouse_detail[0];
    if (detail.Location.coordinates.length > 0) {
      this.setState({
        center: {
          lat: detail.Location.coordinates[0],
          lng: detail.Location.coordinates[1]
        },
        isShowMap: true
      });
    }
  }
  getDetail(id) {
    const valobj = this.state.Result.filter(x => x.WarehouseId == id);
    this.getReview(id);
    this.setState({
      Detail: { ...valobj[0].warehouse_detail[0] },
      RatingModel: {
        Id: id,
        Rating: "",
        Description: ""
      },
      isShowDetail: true,
      center: {
        lat: -25.363882,
        lng: 131.044922
      }
    });
    $("body").addClass("modal-open");
  }
  getReview(id) {
    let reviews = [];
    WarehouseService.GetReviews(id)
      .then(respose => {
        this.setState({ Reviews: respose.data });
      })
      .catch(err => {
        console.log(err);
      });
  }
  getRating(id) {
    const valobj = this.state.Result.filter(x => x.WarehouseId == id)
      .warehouse_detail;
    this.getReview(id);
    this.setState({
      Detail: valobj[0]
    });
  }
  componentWillMount() {
    SearchService.GetSearch()
      .then(response => {
        this.setState({ Result: response.data, isLoading: false });
      })
      .catch(err => {
        console.log(err);
      });
  }
  componentDidUpdate() {
    $(".slickSingle").not(".slick-initialized").slick({
      slidesToShow: 1,
      slidesToScroll: 1,
      dots: false,
      infinite: false,
      focusOnSelect: true,
      nextArrow:
        '<a href="javascript:void(0);" class="slick-next"><i class="fa fa-angle-right"></i></a>',
      prevArrow:
        '<a href="javascript:void(0);" class="slick-prev"><i class="fa fa-angle-left"></i></a>'
    });
  }

  render() {
    const model = {
      model: this.state,
      closeMap: this.closeMap,
      onMapView: this.onMapView,
      getDetail: this.getDetail,
      getRating: this.getRating,
      closeDetail: this.closeDetail
    };
    if (this.state.isLoading) return <Loader />;
    else return <SavedSearch {...model} />;
  }
}



