import React from "react";
import VInput from "Components/common/VInput";

const EditProfile = props => {
  return (
    <div class="mainContent boxScroll">
      <div class="sStorageWarp profileWrap">
        <div class="row margin-top-20">
          <div class="col-xs-12">
            <div class="editKey">
              <a href="javascript:void(0)" onClick={props.onCancel}>
                Closing Editing <i class="fa fa-times" aria-hidden="true" />
              </a>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-12">
            <div class="ProfileBanner">
              <img
                src="/Content/images/warehouse/profile_banner.jpg"
                alt="Logist Compare"
                class="img-responsive"
              />
            </div>
          </div>
          <div class="col-xs-12 col-md-2 ProfileContent">
            <div class="ProfileImg">
              <img
                src={props.userDetail.ProfilePicture}
                onError={e => {
                  e.target.src = "/Content/images/default-user.jpg";
                }}
                alt="Logist Compare"
                class="img-responsive"
              />
            </div>
            <input id="profileImage" type="file" onChange={props.UploadImage} />
            <div class="ProfileName">
              {props.userDetail.FirstName + " " + props.userDetail.LastName}
            </div>
          </div>
          <div class="col-xs-12 col-md-10 col-lg-10">
            <div id="signIn" class="ProfileForm">
              <div class="profileformWarp editProfile">
                <div class="col-xs-12 col-md-5 col-lg-5 form-group">
                  <label for="name" class="control-label">
                    First Name
                  </label>
                  <VInput
                    class="form-control"
                    name="FirstName"
                    placeholder="First Name"
                    maxLength="20"
                    value={props.userDetail.FirstName}
                    valueLink={props.links.firstNameLink}
                    type="text"
                  />
                </div>
                <div class="col-xs-12 col-md-5 col-lg-5 form-group">
                  <label for="name" class="control-label">
                    Last Name
                  </label>
                  <VInput
                    class="form-control"
                    name="LastName"
                    placeholder="Last Name"
                    type="text"
                    maxLength="20"
                    value={props.userDetail.LastName}
                    valueLink={props.links.lastNameLink}
                  />
                </div>
                <div class="col-xs-12 col-md-5 col-lg-5 form-group">
                  <label for="name" class="control-label">
                    Email
                  </label>
                  <input
                    class="form-control"
                    name="Email"
                    placeholder="Email Address"
                    type="email"
                    value={props.userDetail.Email}
                    readOnly
                  />
                </div>
              </div>
            </div>
            <div class="col-xs-12 col-md-12 col-lg-12">
              <input
                class="button button-border button-action button-uppercase margin-top-3x"
                value="Update Profile"
                type="button"
                onClick={props.onSubmit}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = EditProfile;



