import React from "react";
const google = window.google;
import GoogleMap from "Components/map/googlemap.js";
import Rating from "Components/common/starRating.js";
import RatingList from "Components/rating/reviewList.js";
import { Marker } from "react-google-maps";
const deatils = props => {
  const bookingLink =
    "/user/quotes?wid=" +
    props.model.Detail._id +
    "&type=" +
    props.model.Detail.StorageType;
  let i = 1;
  let images = "";
  let warehouseImages = [];
  let services = "";
  let securities = "";
  let warehouseTypes = "";
  let center = {
    lat: -25.363882,
    lng: 131.044922
  };
  let warehouseMap = "";
  const saveSearch = props.saveSearch
    ? (<a
      class="button button-default button-min-width margin-1x"
      onClick={() => {
        props.saveSearch(props.model.Detail._id);
      }}
    >
      Save
      </a>)
    : "";
  if (props.model.Detail._id) {
    center = {
      lat: props.model.Detail.Location.coordinates[0],
      lng: props.model.Detail.Location.coordinates[1]
    }
    warehouseMap = (<div id="map2">
      <GoogleMap center={center}>
        {<Marker position={center} />}
      </GoogleMap>
    </div>)
    warehouseImages = [...props.model.Detail.Images];
    if (warehouseImages.length == 0) {
      warehouseImages.push("/Content/images/warehouse/w1.jpg");
    }
    images = warehouseImages.map((val, j) => {
      return (
        <li key={j}>
          <img
            src={val}
            class="img-responsive"
            onError={e => {
              e.target.src = "/Content/images/warehouse/w1.jpg";
            }}
          />
        </li>
      );
    });
    var otherServices = props.model.Detail.OtherService.split(',');
    services = props.model.Detail.Status != 2 ? Object.entries(
      props.model.Detail.Services
    ).map(([key, val], i) => {
      if (val == true && key == "others") {
        var otherServices = props.model.Detail.OtherService.split(',');
        return (
          otherServices.map((x, y) => {
            return (<label class="options" key={y}>
              {x}
            </label>);
          })
        );
      } else if (val == true) {
        return (
          <label class="options" key={i}>
            {key}
          </label>
        );
      }
    }) :
      otherServices.map((x, y) => {
        return (<label class="options" key={y}>
          {x}
        </label>);
      });

    securities = Object.entries(
      props.model.Detail.Security
    ).map(([key, val], i) => {
      if (val == true && key == "others") {
        return (
          <label class="options" key={i}>
            {props.model.Detail.OtherSpecify}
          </label>
        );
      } else if (val == true) {
        return (
          <label class="options" key={i}>
            {key}
          </label>
        );
      }
    });
    warehouseTypes = Object.entries(
      props.model.Detail.WarehouseType
    ).map(([key, val], i) => {
      if (val == true) {
        return (
          <label class="ch-radioBox" key={i}>
            <input type="radio" name="warehouse" />
            <span>
              {key}
            </span>
          </label>
        );
      }
    });
  }
  return (
    <div
      class={
        props.model.isShowDetail
          ? "modal fade modalWarpper wDetailModal in"
          : "modal fade modalWarpper wDetailModal"
      }
      id="warehouseDetail"
      style={{ display: props.model.isShowDetail ? "block" : "none" }}
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h4>
               {props.model.Detail.Status == 2 ? props.model.Detail.City + " " + "( " + props.model.Detail.Name +" )" : props.model.Detail.City  }
            </h4>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              onClick={props.closeDetail}
            >
              <i class="icon-close ico-t2" />
            </button>
          </div>
          <div class="modal-body">
            <div class="ratingBlk margin-bottom-6x">
              <label class="lbl">Rating</label>
              <Rating input={props.model.Detail.AvgReviews} />
            </div>
            <div class="row">
              <div class="col-xs-12 col-sm-6 col-md-5">
                <div
                  class="sliderBlock  margin-bottom-4x"
                  style={{ height: 228.047 }}
                >
                  <ul id="slickSingle" class="slickSingle1">
                    {images}
                  </ul>
                  {/* <ul class="slickSyncThumbs">
                    {images}
                  </ul> */}

                </div>
                <div class="mapBlk margin-bottom-4x" style={{ height: 228.047 }}>
                  {warehouseMap}
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 col-md-7">
                <div class="blks margin-bottom-6x">
                  <h3>About</h3>
                  <p>
                    {props.model.Detail.About}
                  </p>
                </div>

                <div class="blks margin-bottom-6x">
                  <h3>Address</h3>
                  <p>
                 {props.model.Detail.Status == 2 ? props.model.Detail.City : props.model.Detail.City}
                  </p>
                </div>

                {props.model.Detail.Status == 2 ? (<div class="blks margin-bottom-6x">
                  <h3>Services &#38; Security</h3>
                  {services}
                </div>) : (<div class="blks margin-bottom-6x">
                  <h3>Services</h3>
                  {services}
                </div>)}
                {props.model.Detail.Status == 2 ? "" : (<div class="blks margin-bottom-6x">
                  <h3>Security</h3>
                  {securities = securities ? "N/A" : securities}
                </div>)}
                <div class="blks margin-bottom-6x">
                  <h3></h3>
                  {warehouseTypes}
                </div>
                <div class="blks openingHrs margin-bottom-6x">
                  <h3>Capacity</h3>
                  <p>
                    {props.model.Detail.Capacity} sq {props.model.Detail.Status == 2 ? "Feet" : "Meter"}
                  </p>
                </div>
                <div class="blks openingHrs margin-bottom-6x">
                  <h3>Opening Hours</h3>
                  <p>
                    {props.model.Detail.Status == 2 ? "N/A" : props.model.Detail.Starthrs} - {props.model.Detail.Endhrs}
                  </p>
                </div>
              </div>
            </div>
            <div class="row">
              {props.model.Detail.Status == 2 ? "" : (<div class="col-xs-12">
                <RatingList {...props} />
                <div class="blks text-center margin-bottom-6x">
                  {saveSearch}
                  <a
                    href={bookingLink}
                    class="button button-primary button-min-width margin-1x"
                  >
                    Book
                  </a>
                </div>
              </div>)}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default deatils;



