import React from "react";
import Messagebox from "Components/common/messagebox.js";
import VInput from "Components/common/VInput";
const loginForm = props => {
  const ALertModal = {
    isShow: props.model.ShowAlertModal,
    onClose: props.CloseModal,
    Message: props.model.Message
  };
  const classobj = {
    show: props.model.ShowModel == true ? "in" : "",
    diaplay: props.model.ShowModel == true ? "block" : "none"
  };

  return (
    <div
      class={"modal fade" + " " + classobj.show}
      id="myModal"
      tabIndex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
      style={{ display: classobj.diaplay }}
    >
      <Messagebox {...ALertModal} />

      <div class="modal-dialog payment" role="document">
        <div class="modal-content">
          <div class="closewrap">
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              onClick={props.closePopup}
            >
              &times;
            </button>
          </div>
          <div class="modal-header">
            <div class="panel-heading">
              <h3 class="panel-title">Payment Details</h3>
              <div class="checkbox pull-right">
                {/* <label>
                  <input type="checkbox" />
                  Remember
                </label> */}
              </div>
            </div>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-xs-12">
                <div class="panel panel-default">
                  <div class="panel-body">
                    <form role="form">
                      <div class="form-group">
                        <label for="cardNumber">CARD NUMBER</label>
                        <div class="input-group">
                          <VInput
                            type="text"
                            class="form-control"
                            id="cardNumber"
                            name="number"
                            valueLink={props.stripeLinks.numberLink}
                            size="20"
                            maxLength="20"
                            data-stripe="number"
                            placeholder="Valid Card Number"
                            autoFocus
                          />
                          <span class="input-group-addon">
                            <span class="glyphicon glyphicon-lock" />
                          </span>
                        </div>
                      </div>
                      <div class="row">
                        <div class="col-xs-7 col-md-7 expityMonth">
                          <div class="form-group">
                            <label for="expityMonth">EXPIRY DATE</label>
                            <div class="col-xs-6 col-lg-6 pl-ziro">
                              <VInput
                                type="text"
                                class="form-control"
                                id="expityMonth"
                                placeholder="MM"
                                name="exp_month"
                                maxLength="2"
                                size="2"
                                valueLink={props.stripeLinks.monthLink}
                                data-stripe="exp_month"
                              />
                            </div>
                            <div class="col-xs-6 col-lg-6 pl-ziro">
                              <VInput
                                type="text"
                                class="form-control"
                                id="expityYear"
                                placeholder="YY"
                                size="2"
                                maxLength="2"
                                name="exp_year"
                                valueLink={props.stripeLinks.yearLink}
                                data-stripe="exp_year"
                              />
                            </div>
                          </div>
                        </div>
                        <div class="col-xs-5 col-md-5 pull-right">
                          <div class="form-group">
                            <label for="cvCode">CV CODE</label>
                            <VInput
                              type="password"
                              class="form-control"
                              id="cvCode"
                              placeholder="CVC"
                              name="cvc"
                              valueLink={props.stripeLinks.cvcLink}
                              size="4"
                              data-stripe="cvc"
                            />
                          </div>
                        </div>
                      </div>
                      <div class="row">
                        <div class="col-xs-12 col-md-12 expityMonth">
                          <div class="form-group">
                            <label for="birthDate">DOB</label>
                            <div
                              class="col-xs-12 col-lg-12 pl-ziro"
                              style={{ paddingBottom: "10px" }}
                            >
                              <VInput
                                type="text"
                                class="form-control"
                                id="birthDate"
                                placeholder="yyyy-mm-dd"
                                name="BirthDate"
                                valueLink={props.stripeLinks.birthDateLink}
                              />
                            </div>
                          </div>
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
                <div class="final-pay nav-stacked">
                  <a href="#">
                    <span class="badge pull-right">
                      <span>&pound; </span> {props.model.Price}
                    </span>
                    Final Payment
                  </a>
                </div>
                <br />
                <a
                  href="#"
                  class="btn btn-success btn-lg btn-block"
                  role="button"
                  onClick={() => props.Submit(props.model.Price)}
                >
                  Pay
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = loginForm;



