import React, { Component } from "react";
import Link from "valuelink";
import UserService from "../../services/user.service.js";
import Validation from "../validators/validation.js";
import ManageUser from "./manageuser.js";
import Loader from "Components/common/loader.js";
let links = {};
const NameRegex = /^[a-zA-Z]*$/;
const EmailRegex = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
const userModel = {
  FirstName: "",
  LastName: "",
  Email: "",
  Password: "12345678",
  Role: "Billing",
  ParentId: "",
  Type: "",
  Status: 0
};
let existEmail = "";
export default class manageUserContainer extends Component {
  constructor() {
    super();
    this.state = {
      user: { ...userModel },
      users: [],
      isLoading: true,
      isNew: false,
      ShowAlertModal: false,
      currentId: ""
    };
    this.addUser = this.addUser.bind(this);
    this.getUsers = this.getUsers.bind(this);
    this.showForm = this.showForm.bind(this);
    this.onChange = this.onChange.bind(this);
    this.ondelete = this.ondelete.bind(this);
    this.onpopup = this.onpopup.bind(this);
    this.CloseModal = this.CloseModal.bind(this);
    this.onedit = this.onedit.bind(this);
  }
  onChange(e) {
    e.preventDefault();
    const { name, value } = e.target;

    this.setState({
      user: { ...this.state.user, [name]: value }
    });
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const hasParent = !!errorObjects[0].parent;
      if (hasParent) {
        const parent = errorObjects[0].parent.key,
          key = errorObjects[0].key;
        const obj = this.state[parent];
        obj[key] = undefined;
        this.setState({ [parent]: obj });
      } else {
        const key = errorObjects[0].key;
        //.map(x => x.key)[0];
        this.setState({ [key]: undefined });
      }

      return false;
    } else return true;
  }

  addUser() {
    if (this.valid()) {
      UserService.addUser(this.state.user)
        .then(response => {
          if (response.status == 400) {
            existEmail = this.state.user.Email;            
              this.setState({
                ShowValidationModal: true,
                ModalBody: response.data
              }); 
            return false;
          }
          const users = [];

          users.push(...this.state.users);
          if (response.data.Status) {
            const index = this.state.users.findIndex(
              x => x._id == this.state.user._id
            );
            users[index] = this.state.user;
          } else {
            users.push(response.data);
          }
          existEmail = "";
          this.setState({
            user: { ...userModel },
            users,
            isNew: false,
            ShowAlertModal: false,
            currentId: ""
          });
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
  showForm() {
    this.setState({ isNew: true });
  }
  getUsers() {
    this.setState({ isLoading: true });
    UserService.getAll()
      .then(response => {
        const users = response.data;
        this.setState({
          users,
          ShowAlertModal: false,
          currentId: "",
          isLoading: false
        });
      })
      .catch(err => {
        console.log(err);
      });
  }
  onpopup(id) {
    this.setState({ ShowAlertModal: true, currentId: id });
  }
  ondelete() {
    UserService.delete(this.state.currentId)
      .then(response => {
        this.getUsers();
      })
      .catch(err => {
        console.log(err);
      });
  }
  onedit(item) {
    this.setState({ user: item, isNew: true });
  }
  updateuser(id) {
    UserService.updateuser(this.state.user).then(response => {
      if (response.data.status) {
      }
    });
  }
  CloseModal() {
    this.setState({ ShowAlertModal: false,ShowValidationModal: false });
  }
  componentWillMount() {
    this.getUsers();
  }

  render() {
    links = {
      firstnameLink: Link.state(this, "user")
        .at("FirstName")
        .check(x => x, "FirstName Required")
        .check(x => NameRegex.test(x), "Chatacters only"),
      lastNameLink: Link.state(this, "user")
        .at("LastName")
        .check(x => x, "LastName Required")
        .check(x => NameRegex.test(x), "Chatacters only"),
      emailLink: Link.state(this, "user")
        .at("Email")
        .check(x => x, "Email Required")
        .check(x => EmailRegex.test(x), "Email only")
    };

    const model = {
      model: this.state,
      addUser: this.addUser,
      showForm: this.showForm,
      onChange: this.onChange,
      delete: this.ondelete,
      onpopup: this.onpopup,
      CloseModal: this.CloseModal,
      onEdit: this.onedit,
      links
    };
    if (this.state.isLoading) return <Loader />;
    else
      return (
        <div>
          <ManageUser {...model} />
        </div>
      );
  }
}



