import React from "react";

const Modal = props => {
  const forgotModal = {
    ModalCss: props.State.ShowforgotModal ? "in" : "",
    ModalStyle: { display: props.State.ShowforgotModal ? "block" : "none" },
    ModalBody: props.State.Message,
    onClick: props.onClick,
    onClose: props.onClose,
    recoverPasword: props.recoverPasword
  };
  const verificationModal = {
    ModalCss: props.State.ShowverificationModal ? "in" : "",
    ModalStyle: {
      display: props.State.ShowverificationModal ? "block" : "none"
    },
    ModalBody: props.State.Message,
    onClose: props.onClose,
    onResend: props.onResend
  };
  const ALertModal = {
    ModalCss: props.State.ShowAlertModal ? "in" : "",
    ModalStyle: { display: props.State.ShowAlertModal ? "block" : "none" },
    ModalBody: props.State.Message,
    onClose: props.onClose
  };
  const SuccessModal = {
    ModalCss: props.State.SuccessModal ? "in" : "",
    ModalStyle: { display: props.State.SuccessModal ? "block" : "none" },
    ModalBody: props.State.Message,
    onClose: props.onClose
  };

  return (
    <div>
      <div
        className={"modal fade " + verificationModal.ModalCss}
        id="myModal"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-2"
        style={verificationModal.ModalStyle}
      >
        <div className="modal-dialog" role="document">
          <div className="modal-content forgot">
            <div
              className="closewrap confirm-close"
              onClick={verificationModal.onClose}
            >
              <button type="button" className="close" data-dismiss="modal">
                <i
                  className="fa fa-times"
                  aria-hidden="true"
                  onClick={verificationModal.onClose}
                />
              </button>
            </div>

            <div className="confirm-mail-head">
              <h2>E-mail not Confirmed</h2>
              <p>{verificationModal.ModalBody}</p>
            </div>
            <div className="modal-body confirm-mail">
              <div class="form-group">
                <input
                  type="button"
                  class="button button-border button-primary button-uppercase margin-top-3x"
                  value="Ok"
                  onClick={verificationModal.onClose}
                />
              </div>
              <div className="confirm">
                <p>
                  Didn’t receive confirmation link?{" "}
                  <a
                    href="javascript:void(0)"
                    onClick={verificationModal.onResend}
                  >
                    Resend confirmation mail
                  </a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + forgotModal.ModalCss}
        id="myModal-forgot"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-forgot"
        style={forgotModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={props.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>
                  Forgot Your <strong>Password</strong>
                </h2>
                <p>
                  Don't worry! Enter your email below and we'll email you with
                  instructions on how to reset your password.
                </p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="text"
                    class="form-control"
                    name="forgotEmail"
                    placeholder="E-mail"
                    onChange={props.onChange}
                    value={props.State.forgotEmail}
                  />
                </div>
                <div class="form-group">
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Request new Password"
                    onClick={forgotModal.recoverPasword}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + ALertModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={ALertModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={ALertModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Alert !</h2>
                <p>{ALertModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Ok"
                    onClick={ALertModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + SuccessModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={SuccessModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={SuccessModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Success</h2>
                <p>{SuccessModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <a
                    href="/"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                  >
                    Ok
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = Modal;



