import React from "react";
const security = props => {
  const style = { textTransform: "capitalize" };

  const chkBoxes = Object.entries(props.model.Security).map(([key, val], i) => {
    const other =
      key == "others" && val == true
        ? <div>
            <br />
            <input
              type="text"
              name="OtherSpecify"
              id="OtherSpecify"
              value={props.model.OtherSpecify}
              class="form-control"
              placeholder="Please Specify"
              onChange={props.change}
            />
          </div>
        : "";

    return (
      <label key={i} class="ch-checkMark" style={style}>
        <input
          type="checkbox"
          id={key}
          name="security"
          checked={val}
          onChange={props.oncheckBox}
        />
        <span>
          {key}
        </span>
        {other}
      </label>
    );
  });
  return (
    <div class="form-group clearfix">
      <label class="lbl clearfix">Security</label>
      <div>
        {chkBoxes}
      </div>

      {/*<label class="ch-checkMark">
                <input type="checkbox" />
                <span>Others</span>
                <br />
                <input type="text" name="security" id ="other" value={props.model.Security.other} class="form-control" placeholder="Please Specify" />
            </label>*/}
    </div>
  );
};
module.exports = security;



