import React from "react";
const confirm = (props) => {
  return (
    <div
      class={"modal fade " + props.ModalCss}
      id="myModal-ALert"
      tabIndex="-1"
      role="dialog"
      aria-labelledby="myModalLabel-Alert"
      style={props.ModalStyle}
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content forgot">
          <div class="closewrap">
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              onClick={props.onClose}
            >
              &times;
            </button>
          </div>
          <div class="modal-body forgot-password">
            <div class="headpassword">
              <h2 />
              <p>{props.Message}</p>
            </div>

            <div class="formWarp password">
              <div class="form-group">
                <input
                  type="button"
                  class="button button-border button-primary button-uppercase margin-top-3x"
                  value="Yes"
                  onClick={props.onConfirm}
                />
                <input
                  type="button"
                  class="button button-border button-primary button-uppercase margin-top-3x"
                  value="No"
                  onClick={props.onClose}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default confirm;



