import React, { Component } from "react";
import Link from "valuelink";
import WarehouseService from "Services/warehouse.service.js";
import FindDetail from "./findDetails.js";
import Helper from "../../../helpers/utility.helper.js";

const searchModel = {
  Type: "Pallet",
  Location: "",
  TotalSpace: "",
  MaxHeight: "",
  MaxHeightType: "Meter",
  MaxWeight: "",
  MaxWeightType: "Kgs",
  AvailabilityTo: "",
  AvailabilityFrom: "",
  Distance: "",
  Pallet: {
    Type: { Name: "STD1", Value: "1.20" },
    Total: ""
  },
  Bulk: {
    Item: "",
    ItemType: "Meter",
    TotalItems: "",
    MaxLength: "",
    MaxLengthType: "Meter",
    MaxWidth: "",
    MaxWidthType: "Meter"
  }
};
const NumberRegex = /^[+-]?\d+(\.\d+)?$/;
const NumberDotRegex = /^[0-9.,]+$/;
export default class findDetailContainer extends Component {
  constructor() {
    super();
    this.state = {
      search: { ...searchModel },
      result: []
    };

    this.onChange = this.onChange.bind(this);
    this.onflatpicker = this.onflatpicker.bind(this);
    this.onNext = this.onNext.bind(this);
    this.onkeyPress = this.onkeyPress.bind(this);
    this.onPaste = this.onPaste.bind(this);
    this.onkeyPressWithoutDot = this.onkeyPressWithoutDot.bind(this);
  }
  onChange(e) {
    if (e.preventDefault()) e.preventDefault();
    let { name, value, id, text } = e.target;
    const palletName = e.target.selectedOptions
      ? e.target.selectedOptions[0].id
      : undefined;
    let objectToUpdate = {};
    if (name == "Pallet") {
      if (palletName) {
        objectToUpdate = {
          search: {
            ...this.state.search,
            Pallet: {
              ...this.state.search.Pallet,
              [id]: { Name: palletName, Value: value }
            }
          }
        };
      } else {
        objectToUpdate = {
          search: {
            ...this.state.search,
            Pallet: { ...this.state.search.Pallet, [id]: value }
          }
        };
      }
    } else if (name == "Bulk") {
      if (id == "Item") {
        const maxval = this.onTotalCalculation(value);
        objectToUpdate = {
          search: {
            ...this.state.search,
            Bulk: {
              ...this.state.Bulk,
              [id]: value,
              MaxWidth: maxval,
              MaxLength: maxval
            }
          }
        };
      } else if (id == "MaxLength" || id == "MaxWidth") {
        const total = this.onCalculation(value, id);
        objectToUpdate = {
          search: {
            ...this.state.search,
            Bulk: {
              ...this.state.search.Bulk,
              [id]: value,
              Item: total
            }
          }
        };
      } else {
        objectToUpdate = {
          search: {
            ...this.state.search,
            Bulk: { ...this.state.search.Bulk, [id]: value }
          }
        };
      }
    } else {
      if (!value) value = text;
      this.setState({
        search: {
          ...this.state.search,
          [name]: value
        }
      });
    }
    this.setState(objectToUpdate);
  }
  onkeyPress(e) {
    let matchValue = e.target.value + e.key;
    if (!NumberDotRegex.test(e.key) || 1 < matchValue.match(/\./g)) {
      e.preventDefault();
    }
  }
  onkeyPressWithoutDot(e) {
    if (!NumberRegex.test(e.key)) {
      e.preventDefault();
    }
  }
  onPaste(e) {
    e.preventDefault();
  }
  onTotalCalculation(value) {
    const length = parseFloat(Math.sqrt(value)).toFixed(2);
    return length;
  }
  onCalculation(value, type) {
    let length = this.state.search.Bulk.MaxLength;
    let width = this.state.search.Bulk.MaxWidth;
    length = type == "MaxLength" ? value : length == "" ? 0 : length;
    width = type == "MaxWidth" ? value : width == "" ? 0 : width;
    const total = length * width;
    return total;
  }
  onflatpicker(name, value) {
    this.setState({
      search: {
        ...this.state.search,
        [name]: value
      }
    });
  }
  onNext() {
    const queryString =
      "?ty=" +
      this.state.search.Type +
      "&loc=" +
      this.state.search.Location +
      "&sd=" +
      this.state.search.AvailabilityFrom +
      "&ed=" +
      this.state.search.AvailabilityTo +
      "&ml=" +
      this.state.search.Distance +
      "&h=" +
      this.state.search.MaxHeight +
      "&ht=" +
      this.state.search.MaxHeightType +
      "&w=" +
      this.state.search.MaxWeight +
      "&wt=" +
      this.state.search.MaxWeightType +
      "&pt=" +
      this.state.search.Pallet.Type.Name +
      "&tp=" +
      this.state.search.Pallet.Total +
      "&bi=" +
      this.state.search.Bulk.Item +
      "&bit=" +
      this.state.search.Bulk.ItemType +
      "&tbi=" +
      this.state.search.Bulk.TotalItems +
      "&bl=" +
      this.state.search.Bulk.MaxLength +
      "&blt=" +
      this.state.search.Bulk.MaxLengthType +
      "&bw=" +
      this.state.search.Bulk.MaxWidth +
      "&bwt=" +
      this.state.search.Bulk.MaxWidthType;
    window.history.pushState(
      {},
      "",
      window.location.origin + window.location.pathname + queryString
    );
    this.props.history.push("/warehouse/search" + queryString);
  }

  componentWillMount() {
    const search = this.state.search;
    search["Type"] = Helper.GetParamByName("ty");
    search["Location"] = Helper.GetParamByName("loc");
    search["Distance"] = Helper.GetParamByName("ml");
    search["AvailabilityFrom"] = Helper.GetParamByName("sd");
    search["AvailabilityTo"] = Helper.GetParamByName("ed");
    search["MaxHeight"] = Helper.GetParamByName("h");
    search["MaxWeight"] = Helper.GetParamByName("w");
    if (search.Type == "Pallet") {
      search.Pallet.Type.Name = Helper.GetParamByName("pt");
      if (search.Pallet.Type.Name == "EURO") search.Pallet.Type.Value = "0.96";
      else if (search.Pallet.Type.Name == "STD1")
        search.Pallet.Type.Value = "1.20";
      else if (search.Pallet.Type.Name == "STD2") {
        search.Pallet.Type.Value = "1.44";
      } else {
        search.Pallet.Type.Name = "STD1";
        search.Pallet.Type.Value = "1.20";
      }
      search.Pallet.Total = Helper.GetParamByName("tp");
    } else {
      search.Bulk.Item = Helper.GetParamByName("bi");
      search.Bulk.ItemType = Helper.GetParamByName("bit");
      search.Bulk.TotalItems = Helper.GetParamByName("tbi");
      search.Bulk.MaxLength = Helper.GetParamByName("bl");
      search.Bulk.MaxLengthType = Helper.GetParamByName("blt");
      search.Bulk.MaxWidth = Helper.GetParamByName("bw");
      search.Bulk.MaxWidthType = Helper.GetParamByName("bwt");
    }
    this.setState({
      search
    });
  }
  render() {
    const model = {
      model: this.state,
      next: this.onNext,
      change: this.onChange,
      onkeyPress: this.onkeyPress,
      onflatpicker: this.onflatpicker,
      onPaste: this.onPaste,
      onkeyPressWithoutDot: this.onkeyPressWithoutDot
    };

    return <FindDetail {...model} />;
  }
}



