import React from "react";
import Helpers from "../../helpers/utility.helper.js";
import { ShareButtons, ShareCounts, generateShareIcon } from "react-share";
import MetaTags from 'react-meta-tags';
const BlogDetail = props => {
  const {
    FacebookShareButton,
    LinkedinShareButton,
    TwitterShareButton
  } = ShareButtons;

  const FacebookIcon = generateShareIcon("facebook");
  const TwitterIcon = generateShareIcon("twitter");
  const LinkedinIcon = generateShareIcon("linkedin");
  let date = Helpers.ConvertDateToString(props.model.CreatedDate);
  let shareUrl = "http://logistcompare.com/blog/" + props.model.UrlDescription
  let title = props.model.Name;
  let metaDesc = "";
  return (
    <div id="MainWarpper" class="bg-light aboutus">
      <MetaTags>
        <title>{props.model.Name} | LogistCompare</title>
        <meta name="description" content={props.metaDesc} />
      </MetaTags>
      <section class="services_Section">
        <div class="container">
          <div class="col-xs-12">
            <div class="service_header">
              <h2>Blog</h2>
            </div>
          </div>
        </div>
      </section>

      <section class="hWarpper1 blogWrapper">
        <div class="container">
          <div class="row blogRow">
            <div class="col-xs-12 col-md-12 wow fadeInLeft">
              <h2 class="Titleblog">{props.model.Name}</h2>
              <div class="blogPosted">
                <p>
                  {date}{" "}
                  {/*/ By<strong>
                    {" "}
                    {props.model.By}/ {props.model.Name}
                  </strong> */}
                </p>
              </div>
              <div class="blogText">
                <div class="blog-list-img blog-detail-Image">
                  <img
                    src={props.model.BlogImage}
                    onError={e => {
                      e.target.src = "/Content/images/no-image.jpg";
                    }}
                    class="img-responsive"
                  />
                </div>
                <p>
                  <div id="descriptionId"
                    dangerouslySetInnerHTML={{
                      __html: props.model.Description
                    }}
                  />
                </p>
              </div>{" "}
              <div
                class="Blogback margin-top-4x"
                style={{ marginBottom: "10px", float: "left" }}
              >
                <button
                  style={{ marginTop: "0px" }}
                  class="readmore"
                  onClick={props.goBack}
                >
                  BACK
                </button>
              </div>
              <div
                class="BlogSocialIcon margin-top-4x"
                style={{ marginBottom: "10px", marginTop: "23px" }}
              >
                <FacebookShareButton url={shareUrl} quote={title}>
                  <FacebookIcon size={32} round />
                </FacebookShareButton>
                <TwitterShareButton url={shareUrl} title={title}>
                  <TwitterIcon size={32} round />
                </TwitterShareButton>
                <LinkedinShareButton
                  url={shareUrl}
                  title={title}
                  windowWidth={750}
                  windowHeight={600}
                >
                  <LinkedinIcon size={32} round />
                </LinkedinShareButton>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};
module.exports = BlogDetail;



