import React from "react";
import VInput from "Components/common/VInput";
import Link from "valuelink";
import { Select } from "valuelink/tags";
const userform = props =>
  <div class="col-xs-12 col-sm-9">
    <div class="AddUserForm">
      <form>
        <div class="addform">
          <div class="form-group">
            <div class="row">
              <div class="col-xs-12 col-sm-6 col-md-3 margin-bottom-2x">
                <VInput
                  type="text"
                  class="form-control"
                  placeholder="First Name"
                  name="FirstName"
                  valueLink={props.links.firstnameLink}
                />
              </div>
              <div class="col-xs-12 col-sm-6 col-md-3 margin-bottom-2x">
                <VInput
                  type="text"
                  class="form-control"
                  placeholder="Last Name"
                  name="LastName"
                  valueLink={props.links.lastNameLink}
                />
              </div>
              <div class="col-xs-12 col-sm-6 col-md-3 margin-bottom-2x">
                <VInput
                  type="text"
                  class="form-control"
                  placeholder="Email"
                  name="Email"
                  valueLink={props.links.emailLink}
                />
              </div>
              <div class="col-xs-12 col-sm-6 col-md-3 margin-bottom-2x">
                <div class="multiSelectBlk">
                  <select
                    class="form-control"
                    title="Role"
                    name="Role"
                    value={props.model.user.Role}
                    onChange={props.onChange}
                  >
                    <option value="Billing">Billing</option>
                    <option value="Manager">Manager</option>
                  </select>
                  
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  </div>;
module.exports = userform;



