import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Error from "./errorHandler.js";
export default class ErrorContainer extends Component {
  constructor() {
    super();
    this.state = {
      ErrorCode: 404,
      ErrorMessage: "sorry,page not found."
    };
  }
  componentWillMount() {
    switch (this.props.location.pathname) {
      case "/pagenotfound":
        this.setState({
          ErrorCode: 404,
          ErrorMessage: "sorry,page not found."
        });
        return;
      case "/servererror":
        this.setState({
          ErrorCode: 500,
          ErrorMessage: "Internal Server Error."
        });
        return;
    }
  }
  render() {
    const errorObj = {};
    return <Error {...this.state} />;
  }
}



