import React from "react";
import VInput from "Components/common/VInput";
import Helper from "../../../helpers/utility.helper.js";
const Pellet = props => {
  const daysRemaining = Helper.getDaysDiffrence(props.State.QuoteValidTill);
  const editLink =
    "/user/quotes/" +
    props.State.QuoteId +
    "?wid=" +
    props.State.WarehouseId +
    "&type=Pallet&action=edit";
  return (
    <form>
      <div id="PalletStorage" class="open-form" style={{ display: "block" }}>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Space Available</label>
            <input
              type="text"
              class="form-control"
              placeholder="220 Sqft"
              value={
                parseFloat(props.State.TotalSpace).toFixed(
                  2
                ) +
                " sq" +
                props.State.WarehouseDetail.CapacityType
              }
              readOnly="readOnly"
            />
          </div>
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Time Available</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              value={
                Helper.ConvertDateToString(
                  props.State.WarehouseDetail.Pallet.AvailabilityFrom
                ) +
                " - " +
                Helper.SetEndDate(
                  props.State.WarehouseDetail.Pallet.AvailabilityTo
                )
              }
              readOnly="readOnly"
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Type of Pallets</label>
            <input
              type="text"
              class="form-control"
              placeholder="Euro"
              value={props.State.WarehouseDetail.Pallet.Type.Name}
              readOnly="readOnly"
            />
          </div>
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Number of Pallets</label>
            <input
              type="text"
              class="form-control"
              placeholder="30 Pallets"
              value={props.State.Pallet.TotalPallets}
              readOnly="readOnly"
            />
          </div>
        </div>

        <div class="row Row-10">
          <div class="col-xs-12 form-group">
            <label>Load Description</label>
            <textarea
              class="form-control"
              placeholder="This is the description that is already there from bookings form"
              readOnly="readOnly"
              value={props.State.Pallet.Description}
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Height (Meters/feet)</label>
            <input
              type="text"
              class="form-control"
              placeholder="10 feet"
              value={props.State.Pallet.HeigthPerPellet}
              readOnly="readOnly"
            />
          </div>
          <div class="col-xs-12 form-group">
            <label>Industrial Category</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              value={props.State.Pallet.Category}
              readOnly="readOnly"
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>From (Start date)</label>
            <div class="has-feedback">
              <input
                type="text"
                class="form-control"
                placeholder="30/02/2017"
                value={props.State.Pallet.FromDate}
                readOnly="readOnly"
              />
              <span class="form-control-feedback">
                <i class="fa fa-calendar" />
              </span>
            </div>
          </div>
          <div class="col-xs-12 col-sm-6 form-group">
            <label>To (End Date)</label>
            <div class="has-feedback">
              <input
                type="text"
                class="form-control"
                placeholder="30/05/2017"
                value={props.State.Pallet.ToDate}
                readOnly="readOnly"
              />
              <span class="form-control-feedback">
                <i class="fa fa-calendar" />
              </span>
            </div>
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group costBreak">
            <label>Final Cost</label>

            <div class="btn-group show-on-hover">
              <span class="form-control">
                &pound;{props.State.Pallet.CostBreakDown.FinalCost}
              </span>
            </div>
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group costBreak">
            <div class="form-group return">
              <a href={editLink}>
                <i class="fa fa-arrow-left" aria-hidden="true" />Return to
                bookings form
              </a>
            </div>
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-12">
            <p class="confirm" style={{ display: "block", marginyop: "-20px" }}>
              *Validity - {daysRemaining} days from booking date
            </p>
          </div>
        </div>
      </div>
    </form>
  );
};
module.exports = Pellet;



