import React from "react";
import { Link } from "react-router-dom";
import VInput from "Components/common/VInput";
const Subscription = props => {
  const ALertModal = {
    ModalCss: props.ShowAlertModal ? "in" : "",
    ModalStyle: { display: props.ShowAlertModal ? "block" : "none" },
    ModalBody: props.Message,
    onClose: props.onClose
  };
  const SuccessModel = {
    ModalCss: props.ShowSuccess ? "in" : "",
    ModalStyle: { display: props.ShowSuccess ? "block" : "none" },
    ModalBody: props.Message,
    onClose: props.onClose
  };
  const ShowPaymentModal = {
    ModalCss: props.ShowPaymentModal ? "in" : "",
    ModalStyle: { display: props.ShowPaymentModal ? "block" : "none" },
    onClose: props.onClose
  };

  let submitButton =
    props.model.IsFressSubscription == "none" ? (
      <a href="javascript:void(0)"
        className="cb-button" aria-hidden="true"
        style={{ cursor: "pointer" }}
        data-keyboard="false"
        onClick={props.showBankPayment}
      >
        Submit
     </a>
    ) : (
        <a
          href="javascript:void(0)"
          className="cb-button"
          style={{ cursor: "pointer" }}
          aria-hidden="true"
        >
          Submit
      </a>
      );
  return (
    <section className="sbsWarpper">
      <div className="wHeight innerWarp">
        <div className="cell bg-theme">
          <div className="infoBlk text-center">
            <span className="imgBlk center-block margin-bottom-8x">
              <img
                src="../Content/images/subscribe.png"
                alt="subscription"
                className="img-responsive"
              />
            </span>
            <h2 className="margin-bottom-6x">Select Subscription</h2>
            <p>
              Please select the subscription plan and continue to use our platform for an optimum utilization of your warehouse space.
            </p>
          </div>
        </div>

        <div className="cell">
          <div className="subcribeWarp">
            <div
              className="sBlks center-block margin-bottom-8x margin-top-8x"
              style={{ display: props.model.IsFressSubscription }}
            >
              <h3>Select Plan</h3>
              <div className="cb-pricing">
                <div className="sCell">
                  <h5>Free Subscription</h5>
                </div>
              </div>
              <div className="cb-duration">
                <div className="sCell">
                  Initial 2 months only{" "}
                  <span className="br">(First time user)</span>
                </div>
              </div>
              <a
                href="javascript:void(0)"
                className="cb-button"
                aria-hidden="true"
                onClick={props.onSelect}
              >
                Submit
              </a>
            </div>

            <div className="plan-sab sBlks sblk-info center-block margin-bottom-8x margin-top-8x">
              <h3>Select Plan</h3>
              <div className="cb-pricing">
                <div className="sCell">
                  <h2>
                    £60</h2>
                  <span className="_count" style={{ fontSize: "13px" }}>(£50 + VAT)</span>
                  <p>Jun 1,2018 onwards, we are VAT registered </p>
                </div>
              </div>
              <div className="cb-duration">
                <div className="sCell">Monthly Plan</div>
              </div>

              {submitButton}
              {/*<input type="button" class="cb-button" onClick={props.openPopup} value="Submit Payment" />*/}

            </div>
            <a
              href="javascript:void(0)"
              onClick={props.logOut}
              class="btn btn-danger"

              title="Logout"
            >
              Log Out
            </a>
          </div>

        </div>

      </div>
      {/* <StripeForm {...props} /> */}

      <div
        class={"modal fade " + ALertModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={ALertModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={ALertModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Alert !</h2>
                <p>{ALertModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Ok"
                    onClick={ALertModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + SuccessModel.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={SuccessModel.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap" />
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Success !</h2>
                <p>{SuccessModel.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <Link
                    to="/warehouse/list"
                    className="button button-border button-primary button-uppercase margin-top-3x"
                  >
                    Ok
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      { /*<!-- payment Modal */}
      <div class={"modal fade " + ShowPaymentModal.ModalCss + " plan-payment"} style={ShowPaymentModal.ModalStyle} id="myModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-md" role="document">
          <div class="modal-content">

            <div class="modal-body">
              <div class="closewrap"><button type="button" class="close" onClick={ShowPaymentModal.onClose}>×</button></div>
              <div class="plan-tabs">
                <ul class="nav nav-tabs">
                  <li class="active"><a data-toggle="tab" href="#home">Pay By Card</a></li>
                  <li onClick={props.Reset}><a data-toggle="tab" href="#menu1">Pay by BACS</a></li>
                  <li onClick={props.Reset}><a data-toggle="tab" href="#menu2">Pay by Standing Order</a></li>
                </ul>
              </div>
              <div class="tab-content">
                <div id="home" class="tab-pane fade in active">
                  <p>Please proceed to renew your subscription. Our platform is secure for all transactions. </p>
                  <button href="javascript:void(0)" data-dismiss="modal" class="button button-border button-action button-uppercase button-min-width margin-top-3x margin-right-2x" type="button" onClick={props.openPopup}>Pay</button>

                </div>

                <div id="menu1" class="tab-pane fade">
                  <h1>Bank details </h1>
                  <div class="subscription-plan-form">
                    <div class="formWarp">
                      <div class="form-group">
                        <div>
                          <label>Company Name</label>
                          <input type="text" class="form-control bacs-input" placeholder="" value={props.model.CompanyName} disabled />
                        </div>
                      </div>
                      <div class="form-group">
                        <div>
                          <label>Account Number</label>
                          <input type="text" class="form-control bacs-input" placeholder="" value={props.BankDetails.AccountNumber} disabled />
                        </div>
                      </div>
                      <div class="form-group">
                        <div>
                          <label>Sort Code</label>
                          <input type="text" class="form-control bacs-input" placeholder="" value={props.BankDetails.SortCode} disabled />
                        </div>
                      </div>

                      <div class="form-group">
                        <div>
                          <label>SWIFTBIC</label>
                          <input type="text" class="form-control bacs-input" placeholder="" value={props.BankDetails.SWIFTBIC} disabled />
                        </div>
                      </div>
                      <div class="form-group">
                        <div>
                          <label>IBAN</label>
                          <input type="text" class="form-control bacs-input" placeholder="" value={props.BankDetails.IBAN} disabled />
                        </div>
                      </div>
                      <div class="form-group">
                        <div>
                          <label>Amount</label>
                          <input type="text" class="form-control" placeholder="" value="£ 60 (£ 50 + VAT)" disabled />
                        </div>
                      </div>
                      <div class="form-group">
                        <div>
                          <label>Reference No.</label>
                          <VInput
                            type="text"
                            class="form-control"
                            placeholder=""
                            name="TransactionId"
                            onChange={props.handleTransactionChange}
                            valueLink={props.bankLinks.transactionIdLink}
                          />
                        </div>
                      </div>

                      <div class="form-group">
                        <input type="button" onClick={() => props.CreateBankPayment("BACS")} class="button button-border button-action button-uppercase button-min-width margin-top-3x margin-right-2x" value="Submit" />
                      </div>
                    </div>
                  </div>
                </div>
                <div id="menu2" class="tab-pane fade">
                  <h1>Bank details </h1>
                  <div class="subscription-plan-form">
                    <div class="formWarp">
                      <div class="form-group">
                        <div>
                          <label>Company Name</label>
                          <input type="text" class="form-control bacs-input" placeholder="" value={props.model.CompanyName} disabled />
                        </div>
                      </div>
                      <div class="form-group">
                        <div>
                          <label>Account Number</label>
                          <input type="text" class="form-control" placeholder="" value={props.BankDetails.AccountNumber} />
                        </div>
                      </div>
                      <div class="form-group">
                        <div>
                          <label>Sort Code</label>
                          <input type="text" class="form-control" placeholder="" value={props.BankDetails.SortCode} />
                        </div>
                      </div>

                      <div class="form-group">
                        <div>
                          <label>SWIFTBIC</label>
                          <input type="text" class="form-control" placeholder="" value={props.BankDetails.SWIFTBIC} readOnly />
                        </div>
                      </div>
                      <div class="form-group">
                        <div>
                          <label>IBAN</label>
                          <input type="text" class="form-control" placeholder="" value={props.BankDetails.IBAN} readOnly />
                        </div>
                      </div>
                      <div class="form-group">
                        <div>
                          <label>Amount</label>
                          <input type="text" class="form-control" placeholder="" value="£ 60 (£ 50 + VAT)" disabled />
                        </div>
                      </div>
                      <div class="form-group">
                        <div>
                          <label>Reference No.</label>
                          <VInput
                            type="text"
                            class="form-control"
                            placeholder=""
                            name="TransactionId"
                            onChange={props.handleTransactionChange}
                            valueLink={props.bankLinks.transactionIdLink}
                          />
                        </div>
                      </div>

                      <div class="form-group">
                        <input type="button" onClick={() => props.CreateBankPayment("Standing Order")} class="button button-border button-action button-uppercase button-min-width margin-top-3x margin-right-2x" value="Submit" />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

          </div>
        </div>
      </div>

    </section >
  );
};
module.exports = Subscription;



