import React, { Component } from "react";
import { BrowserRouter as Router, Route, withRouter } from "react-router-dom";
import ResetPassword from "./resetpassword.js";
import UserService from "../../services/user.service.js";
import Link from "valuelink";

let links = {};
export default class ResetPasswordContainer extends Component {
  constructor(props) {
    super();
    this.state = {
      NewPassword: "",
      ConfirmPassword: "",
      Id: props.match.params.id,
      ShowAlertModal: false,
      ShowSuccessModal: false,
      Message: ""
    };
  }
  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  updatePassword(e) {
    e.preventDefault();
    if (this.valid()) {
      UserService.resetPassword(this.state)
        .then(response => {
          if (response.data.status) {
            this.setState({
              ShowSuccessModal: true,
              Message: response.data.Message
            });
          } else {
            this.setState({
              ShowAlertModal: true,
              Message: response.data.Message
            });
          }
        })
        .catch(error => {
          alert(error);
        });
    }
  }
  redirectToLogin() {
    this.props.history.push("/");
  }

  CloseModal() {
    this.setState({
      ShowAlertModal: false,
      Message: "",
      ShowSuccessModal: false
    });
  }
  render() {
    links = {
      passwordLink: Link.state(this, "NewPassword")
        .check(x => x, "New Password is required")
        .check(
          x => x.length >= 6,
          "Passwords must contain at least six characters"
        ),
      confirmPasswordLink: Link.state(this, "ConfirmPassword").check(
        x => x == this.state.NewPassword,
        "Password and confirmation password do not match."
      )
    };
    const resetData = {
      submitPassword: this.updatePassword.bind(this),
      handleChange: this.handleChange.bind(this),
      redirectLogin: this.redirectToLogin.bind(this),
      ShowSuccessModal: this.state.ShowSuccessModal,
      ShowAlertModal: this.state.ShowAlertModal,
      Message: this.state.Message,
      onClose: this.CloseModal.bind(this),
      links
    };
    return <ResetPassword {...resetData} />;
  }
}



