import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import MyBooking from "./mybooking.js";
import QuoteService from "Services/quote.service.js";
import WarehouseService from "Services/warehouse.service.js";
import ConversionService from "Services/formula.service.js";
import Helper from "Helpers/utility.helper.js";
import Loader from "Components/common/loader.js";
const searchModel = {
  Name: "",
  fromDate: "",
  toDate: ""
};
const viewModel = {
  Name: "",
  Capacity: "",
  CapacityType: "",
  Address: "",
  City: "",
  PostCode: "",
  Website: "",
  Starthrs: "",
  Endhrs: "",
  About: "",
  StorageType: "Pallet",
  Services: {},
  WarehouseType: {},
  Security: {},
  Images: [],
  PendingSpace: "",
  UserId: "",
  lat: "",
  lng: "",
  Bulk: {
    MaxLength: "",
    MaxLengthType: "",
    MaxWidth: "",
    MaxWidthType: "",
    MaxWeight: "",
    MaxWeightType: "",
    MaxHeight: "",
    MaxHeightType: "",
    AvailabilityFrom: "",
    AvailabilityTo: "",
    ApxPrice: "",
    ApxPriceType: "",
    TotalSpace: "",
    Description: ""
  },
  Pallet: {
    Type: { Name: "", Value: "" },
    Total: "",
    MaxHeight: "",
    MaxHeightType: "",
    ApxPrice: "",
    AvailabilityTo: "",
    AvailabilityFrom: "",
    Images: ""
  }
};
const reviewModel = {
  Id: "",
  Rating: "",
  Description: ""
};
export default class MyBookingContainer extends Component {
  constructor() {
    super();
    this.state = {
      Bookings: [],
      Search: { ...searchModel },
      isLoading: true,
      Reviews: [],
      RatingModel: { ...reviewModel },
      Detail: { ...viewModel },
      isShowDetail: false
    };
    this.getWarehouseDetail = this.getWarehouseDetail.bind(this);
    this.CloseRating = this.CloseRating.bind(this);
    this.getDetail = this.getDetail.bind(this);
    this.closeDetail = this.closeDetail.bind(this);
    this.getMyBookings = this.getMyBookings.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
  }
  componentWillMount() {
    this.getMyBookings();
  }
  handleKeyPress(e) {
    if (e.key === "Enter") {
      this.getMyBookings();
    }
  }
  getMyBookings() {
    this.setState({ isLoading: true });
    QuoteService.GetAllBookings(this.state.Search).then(response => {
      this.setState({ Bookings: response.data, isLoading: false });
    });
  }
  handleChange(e) {
    this.setState({
      Search: { ...this.state.Search, [e.target.name]: e.target.value }
    });
  }
  onfaltPicker(name, value) {
    this.setState({
      Search: { ...this.state.Search, [name]: value }
    });
  }
  searchQuote() {
    this.setState({ isLoading: true });
    QuoteService.search(this.state.Search).then(response => {
      this.setState({ QuoteList: response.data, isLoading: false });
    });
  }
  componentDidUpdate() {
    $(".slickSingle").not(".slick-initialized").slick({
      slidesToShow: 1,
      slidesToScroll: 1,
      dots: false,
      infinite: false,
      focusOnSelect: true,
      nextArrow:
        '<a href="javascript:void(0);" class="slick-next"><i class="fa fa-angle-right"></i></a>',
      prevArrow:
        '<a href="javascript:void(0);" class="slick-prev"><i class="fa fa-angle-left"></i></a>'
    });
  }

  CloseRating() {
    this.setState({ isShowRating: false });
  }
  closeDetail() {
    this.setState({ isShowDetail: false });
    $("body").removeClass("modal-open");
  }
  onChange(e) {
    e.preventDefault();
    const { name, value } = e.target;
    this.setState({
      RatingModel: { ...this.state.RatingModel, [name]: value }
    });
  }
  getWarehouseDetail(id) {
    WarehouseService.Get(id)
      .then(respose => {
        WarehouseService.GetReviews(id)
          .then(res => {
            this.setState({
              Detail: respose.data,
              Reviews: res.data,
              RatingModel: { ...this.state.RatingModel, Id: id }
            });
          })
          .catch(error => {
            console.log(error);
          });
      })
      .catch(err => {
        console.log(err);
      });
  }
  getDetail(id) {
    const valobj = this.state.Bookings.filter(x => x.WarehouseId == id)[0];
    this.getWarehouseDetail(id);
    this.setState({
      Detail: { ...valobj.WarehouseDetail },
      isShowDetail: true,
      RatingModel: {
        Id: id,
        Rating: "",
        Description: ""
      },
      center: {
        lat: -25.363882,
        lng: 131.044922
      }
    });
    $("body").addClass("modal-open");
  }
  addRating() {
    WarehouseService.SaveReview(this.state.RatingModel)
      .then(response => {
        const list = this.state.Reviews;
        list.push(response.data);
        this.setState({
          RatingModel: {
            ...this.state.RatingModel,
            Description: "",
            Rating: ""
          },
          Reviews: list
        });
      })
      .catch(error => {
        console.log(error);
      });
  }
  onRatingClick(val) {
    this.setState({ RatingModel: { ...this.state.RatingModel, Rating: val } });
  }
  render() {
    const QuotesObject = {
      model: this.state,
      bookingList: this.state.Bookings,
      Search: this.state.Search,
      onflatPicker: this.onfaltPicker.bind(this),
      handleChange: this.handleChange.bind(this),
      searchQuotes: this.searchQuote.bind(this),
      getWarehouseDetail: this.getWarehouseDetail,
      CloseRating: this.CloseRating,
      addRating: this.addRating.bind(this),
      onRatingClick: this.onRatingClick.bind(this),
      onChange: this.onChange.bind(this),
      getDetail: this.getDetail,
      closeDetail: this.closeDetail,
      getMyBookings: this.getMyBookings,
      handleKeyPress: this.handleKeyPress
    };
    if (this.state.isLoading) return <Loader />;
    else return <MyBooking {...QuotesObject} />;
  }
}



