import React from "react";
import VInput from "Components/common/VInput";
import Helper from "../../../helpers/utility.helper.js";
const Bulk = props => {
  return (
    <form>
      <div id="PalletStorage" class="open-form" style={{ display: "block" }}>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Space Available</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              value={
                parseFloat(props.State.TotalSpace).toFixed(
                  2
                ) +
                " sq" +
                props.State.WarehouseDetail.CapacityType
              }
              readOnly="readOnly"
            />
          </div>
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Time Available</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              value={
                Helper.ConvertDateToString(
                  props.State.WarehouseDetail.Bulk.AvailabilityFrom
                ) +
                " - " +
                Helper.SetEndDate(
                  props.State.WarehouseDetail.Bulk.AvailabilityTo
                )
              }
              readOnly="readOnly"
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-12 form-group">
            <label>Goods category</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              value={props.State.Bulk.Category}
              readOnly="readOnly"
            />
          </div>
        </div>

        <div class="row Row-10">
          <div class="col-xs-12 form-group">
            <label>Load Description / Additional Information</label>
            <textarea
              class="form-control"
              placeholder=""
              readOnly="readOnly"
              value={props.State.Bulk.Description}
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group ">
            <label>
              Total Space Required <sub>*</sub>
            </label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              readOnly="readOnly"
              value={
                props.State.Bulk.TotalSpace +
                " sq " +
                props.State.Bulk.TotalSpaceType
              }
            />
          </div>
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Total Items</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              readOnly="readOnly"
              value={props.State.Bulk.TotalItem}
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Total Weight (Kgs/Lbs)</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              readOnly="readOnly"
              value={props.State.Bulk.TotalWeight}
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>From (Start date)</label>
            <div class="has-feedback">
              <input
                type="text"
                class="form-control"
                placeholder=""
                value={Helper.ConvertDateToString(props.State.Bulk.FromDate)}
                readOnly="readOnly"
              />
              <span class="form-control-feedback">
                <i class="fa fa-calendar" />
              </span>
            </div>
          </div>
          <div class="col-xs-12 col-sm-6 form-group">
            <label>To (End Date)</label>
            <div class="has-feedback">
              <input
                type="text"
                class="form-control"
                placeholder=""
                value={Helper.ConvertDateToString(props.State.Bulk.ToDate)}
                readOnly="readOnly"
              />
              <span class="form-control-feedback">
                <i class="fa fa-calendar" />
              </span>
            </div>
          </div>
        </div>

        <div class="row Row-10">
          <div class="costBreak">
            <label>Cost Breakdown</label>

            <div class="form-group row">
              <label for="example-text-input" class="col-2 col-form-label">
                Handling in
              </label>
              <div class="col-10 input-group">
                <span class="input-group-addon" id="basic-addon1">
                  &pound;
                </span>
                <VInput
                  type="text"
                  class="form-control"
                  placeholder=""
                  name="HandlingIn"
                  onChange={props.onBulkPriceChange}
                  valueLink={props.palletlinks.handlingInLink}
                  aria-describedby="basic-addon1"
                />
              </div>
            </div>
            <div class="form-group row">
              <label for="example-text-input" class="col-2 col-form-label">
                Handling Out
              </label>
              <div class="col-10 input-group">
                <span class="input-group-addon" id="basic-addon1">
                  &pound;
                </span>
                <VInput
                  type="text"
                  class="form-control"
                  placeholder=""
                  onChange={props.onBulkPriceChange}
                  name="HandlingOut"
                  valueLink={props.bulklinks.handlingOutLink}
                  aria-describedby="basic-addon1"
                />
              </div>
            </div>
            <div class="form-group row">
              <label for="example-text-input" class="col-2 col-form-label">
                Storage
              </label>
              <div class="col-10 input-group">
                <span class="input-group-addon" id="basic-addon1">
                  &pound;
                </span>
                <VInput
                  type="text"
                  class="form-control"
                  placeholder=""
                  name="Storage"
                  onChange={props.onBulkPriceChange}
                  valueLink={props.bulklinks.storageLink}
                  aria-describedby="basic-addon1"
                />
              </div>
            </div>
          </div>
        </div>

        <div class="row Row-10">
          <div class="costBreak">
            <div class="form-group row">
              <label for="example-text-input" class="col-2 col-form-label">
                Final Cost
              </label>
              <div class="col-10 input-group">
                <span class="input-group-addon" id="basic-addon1">
                  &pound;
                </span>
                <VInput
                  type="text"
                  class="form-control"
                  placeholder=""
                  valueLink={props.bulklinks.FinalCostLink}
                  readOnly="true"
                  aria-describedby="basic-addon1"
                />
              </div>
            </div>
          </div>
        </div>

        <div class="row Row-10">
          <div
            class="col-xs-12 col-sm-6 form-group"
            style={{ marginTop: "10px" }}
          >
            <div class="form-group return">
              <button
                type="button"
                class="button button-primary"

                onClick={props.submitQuote}
              >Submit to client</button>
            </div>
          </div>
        </div>
      </div>
    </form>
  );
};
module.exports = Bulk;



