import React from "react";
import Pallet from "./findPalletDetail.js";
import Bulk from "./findBulkDetail.js";
const findDetails = props => {
  const section = props.model.search.Type == "Pallet" ? <Pallet {...props} /> : <Bulk {...props} />;
  return (
    <div>
      <div class="header">
        <h2>Select Storage</h2>
      </div>
      <div class="mainContent boxScroll">
        <div class="findStorageInner innerPadding">
          <div class="row">
            <div class="col-xs-12 col-sm-6 col-md-4">
              <div class="curvedTabs margin-top-2x margin-bottom-8x">
                <ul>
                  <li class={props.model.search.Type == "Pallet"?"active":""}>
                    <a href="#" name="Type">
                      Pallet
                    </a>
                  </li>
                  <li class={props.model.search.Type == "Bulk"?"active":""}>
                    <a href="#"  name="Type">
                      Bulk
                    </a>
                  </li>
                </ul>
              </div>
            </div>
            <div class="col-xs-12">
              <div class="tab-content">
                {section}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

module.exports = findDetails;



