import { http } from "Helpers/axios.helper.js";
import Helper from "Helpers/utility.helper.js";
export default class StripeService {
  static savePayment(data) {
    const url = "/api/stripe/charge";
    return http.post(url, data);
  }

  static stripePayment(obj, price, type, callback) {
    Stripe.setPublishableKey("pk_test_oitDMx8pNX1WzkeE0L8cioUI");
    const userId = Helper.DecodeToken(localStorage.getItem("userinfo")).Id;
    Stripe.card.createToken(obj, (status, response) => {
      if (response.error) {
        callback(response);
      } else {
        var data = {
          stripeToken: response.id,
          price: price,
          userId: userId,
          type: type
        };

        this.savePayment(data).then((res, err) => {
          callback(res);
        });
      }
    });
  }
}



