import "babel-polyfill";
import React from "react";
import ReactDOM from "react-dom";
import {
  BrowserRouter as Router,
  Route,
  Link,
  withRouter,
  Switch,
  Redirect
} from "react-router-dom";
import { BrowserRouter } from "react-router-dom";

import routes from "./route.config.js";
import SideHeader from "Components/side.header.js";
import ErrorHandler from "Components/errorhandler/errorHandler.container.js";
import Helper from "./helpers/utility.helper.js";
import NotificationCount from "Services/notification.service.js";
import Loader from "Components/common/loader.js";
let notifications = 0;
let currentRoute = null;
const App = ({ location }) => {
  const showSideHeaderLinks = false; //!!localStorage.getItem("token");
  currentRoute = routes.find(x =>
    location.pathname.includes(x.path.split(":")[0])
  );
  const showHeader = currentRoute ? currentRoute.sideHeader : false;
  let links = null;
  if (!!localStorage.getItem("userinfo")) {
    // NotificationCount.GetCount().then(response => {
    //   notifications = response.data;
    //   console.log("notifications" + notifications);
    // });
    let IsOwner =
      Helper.DecodeToken(localStorage.getItem("userinfo")).Type == "Owner"
        ? true
        : false;
    links = sidebarItems.filter(x => x.listSpace == IsOwner).map((x, i) => {
      return (
        <CustomLink key={i} to={x.path} label={x.title} cssClass={x.class} />
      );
    });
  } else {
    links = sidebarItems.map((x, i) => {
      return (
        <CustomLink key={i} to={x.path} label={x.title} cssClass={x.class} />
      );
    });
  }

  const routeObject = routes.map((x, i) => {
    return (
      <Route
        key={i}
        exact
        path={x.path}
        onEnter={window.scrollTo(0, 0)}
        render={props => <x.component {...props} />}
      />
    );
  });
  if (showHeader && !!localStorage.getItem("userinfo"))
    return (
      <SideHeader links={links}>
        {" "}
        <Switch>
          {routeObject}
          <Route component={ErrorHandler} />
        </Switch>
      </SideHeader>
    );
  else {
    return (
      <div>
        <Switch>
          {routeObject}
          <Route component={ErrorHandler} />
        </Switch>
      </div>
    );
  }
};

