const CustomLink = ({ label, to, cssClass }) => (
  <Route
    path={to}
    exact={true}
    children={({ match }) => (
      <li
        class={match ? "active" : ""}
        class={label == "Notifications" ? "notify__Setting" : ""}
      >
        {label == "Notifications" ? (
          <div class="notify_icon">{notifications}</div>
        ) : (
            ""
          )}
        <Link
          to={to}
          data-toggle="tooltip"
          data-placement="right"
          title={label}
        >
          <i class={cssClass} /> <span>{label}</span>
        </Link>
      </li>
    )}
  />
);

ReactDOM.render(
  <Router>
    <Route render={({ location }) => <App location={location} />} />
  </Router>,
  document.getElementById("root")
);



