import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Subscription from "./subscription.js";
import UserService from "../../services/user.service.js";
import Helper from "Helpers/utility.helper.js";
import Link from "valuelink";
import { Set } from "Helpers/axios.helper.js";
//import StripeService from "../../services/stripe.service.js";
import PaymentService from "Services/payment.service.js";
import Loader from "Components/common/loader.js";
const NameRegex = /^[a-zA-Z\s]*$/;
const NumberRegex = /^\d+$/;
const DOBRegex = /([12]\d{3}-(0[1-9]|1[0-2])-(0[1-9]|[12]\d|3[01]))/;
let stripeLinks = {};
let bankLinks = {};
export default class SubscriptionContainer extends Component {
  constructor() {
    super();
    const tokenObj = {
      number: "",
      exp_month: "",
      exp_year: "",
      cvc: ""
    };
    this.state = {
      userStatus: undefined,
      ShowModel: false,
      ShowAlertModal: false,
      ShowSuccess: false,
      Message: "",
      Price: 60,
      IsFressSubscription: "block",
      showSpinner: "none",
      tokenObj: tokenObj,
      BirthDate: "",
      BankDetails: {},
      TransactionId: "",
      isLoading: false,
      CompanyName: "",
      ShowPaymentModal: false
    };
    this.onChange = this.onChange.bind(this);
    this.Submit = this.Submit.bind(this);
    this.SumitSubscription = this.SumitSubscription.bind(this);
    this.openPopup = this.openPopup.bind(this);
    this.closePopup = this.closePopup.bind(this);
  }

  onChange(e) {
    const { name, value } = e.target;

    this.setState({
      tokenObj: Object.assign({}, this.state.tokenObj, { [name]: value })
    });
  }

  handleTransactionChange(e) {
    const { name, value } = e.target;
    this.setState({ TransactionId: value })
  }
  valid() {
    const errorObjects = Object.values(stripeLinks).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  validTransaction() {
    const errorObjects = Object.values(bankLinks).filter(x => !!x.error);
    if (errorObjects.length) {
      const hasParent = !!errorObjects[0].parent;
      if (hasParent) {
        const parent = errorObjects[0].parent.key,
          key = errorObjects[0].key;
        const obj = this.state[parent];
        obj[key] = undefined;
        this.setState({ [parent]: obj });
      } else {
        const key = errorObjects[0].key;
        //.map(x => x.key)[0];
        this.setState({ [key]: undefined });
      }

      return false;
    } else return true;
  }
  validCard() {
    const errorObjects = Object.values(stripeLinks).filter(x => !!x.error);
    if (errorObjects.length) {
      const hasParent = !!errorObjects[0].parent;
      if (hasParent) {
        const parent = errorObjects[0].parent.key,
          key = errorObjects[0].key;
        const obj = this.state[parent];
        obj[key] = undefined;
        this.setState({ [parent]: obj });
      } else {
        const key = errorObjects[0].key;
        //.map(x => x.key)[0];
        this.setState({ [key]: undefined });
      }

      return false;
    } else return true;
  }
  openPopup() {
    this.setState({ isLoading: true });
    PaymentService.BuySubscription().then(response => {
      if (response.data.Status) {
        window.location.href = response.data.redirectUrl;
      } else {
        this.setState({
          ShowAlertModal: true,
          Message: response.data.Messsage
        });
      }
    });
  }
  closePopup() {
    this.setState({ ShowModel: false });
  }

  Submit(price) {
    const type = "Subscription";

  }
  logOut() {
    localStorage.removeItem("userinfo");
    Set(null);
    this.props.history.push("/warehouse/login");

  }
  componentWillMount() {
    const userObj = Helper.DecodeToken(localStorage.getItem("userinfo"));
    var date = new Date();
    var subscriptionDate = new Date(userObj.SubscriptionEndDate);

    if (date <= subscriptionDate) {
      this.props.history.push("/warehouse/list");
      return;
    }
    if (this.props.location.search) {
      let transactionId = Helper.GetParamByName("transactionId");
      PaymentService.SaveSubscription(transactionId)
        .then(response => {
          if (response.data.Status) {
            localStorage.setItem("userinfo", response.data.accessToken);
            Set(response.data.accessToken);
            this.setState({
              isLoading: false,
              ShowSuccess: true,
              Message: "Payment Completed Successfully"
            });
          } else {
            this.setState({
              isLoading: false,
              ShowAlertModal: true,
              Message: response.data.Message
            });
            PaymentService.GetBankInformation().then(response => {
              if (response.data.status) {
                this.setState({ BankDetails: response.data.BankDetail, CompanyName: userObj.FirstName + " " + userObj.LastName })
              }
            })
          }
        })
        .catch(error => { });
    } else {
      PaymentService.GetBankInformation().then(response => {
        if (response.data.status) {
          this.setState({ BankDetails: response.data.BankDetail, CompanyName: userObj.FirstName + " " + userObj.LastName })
        }
      })
    }

    var date = new Date();
    var subscriptionDate = new Date(userObj.SubscriptionEndDate);
    if (date > subscriptionDate) this.setState({ IsFressSubscription: "none" });
  }
  showBankPAymentPopup() {
    let body = document.getElementsByTagName("BODY")[0];
    body.classList.add("modal-open");
    this.setState({ ShowPaymentModal: true })
  }
  SumitSubscription() {
    UserService.AddSubscription().then(response => {
      if (response.data.Status) {
        this.props.history.push("/bankdetail/add");
      } else if (response.data.Message == "Session Expired") {
        localStorage.clear();
        this.props.history.push("/warehouse/login");
      }
    });
  }
  CreateBankPayment(paymentMode) {

    if (this.validTransaction()) {
      PaymentService.CreateBankPayment(this.state.TransactionId, paymentMode).then(response => {
        let body = document.getElementsByTagName("BODY")[0];
        body.classList.remove("modal-open");
        if (response.data.Status) {
          localStorage.setItem("userinfo", response.data.accesstoken);
          Set(response.data.accesstoken);

          this.setState({ ShowSuccess: true, Message: response.data.Message, ShowPaymentModal: false })
        } else {
          this.setState({ ShowAlertModal: true, Message: response.data.Message, ShowPaymentModal: false })
        }
      })
    }
  }
  CloseModal() {
    let body = document.getElementsByTagName("BODY")[0];
    body.classList.remove("modal-open");
    this.setState({
      ShowAlertModal: false,
      ShowPaymentModal: false,
      Message: ""
    });

  }
  render() {
    stripeLinks = {
      numberLink: Link.state(this, "tokenObj")
        .at("number")
        .check(x => x, "card number is required")
        .check(x => NumberRegex.test(x), "Enter numbers only")
        .check(x => x.trim(), "Space not allowed"),
      monthLink: Link.state(this, "tokenObj")
        .at("exp_month")
        .check(x => x, "Month is required")
        .check(x => NumberRegex.test(x), "Enter number only"),
      yearLink: Link.state(this, "tokenObj")
        .at("exp_year")
        .check(x => x, "Year is required")
        .check(x => NumberRegex.test(x), "Enter number only"),
      cvcLink: Link.state(this, "tokenObj")
        .at("cvc")
        .check(x => x, "CVC is required")
        .check(x => NumberRegex.test(x), "Enter number only"),
      birthDateLink: Link.state(this, "BirthDate")
        .check(x => x, "DOB is required")
        .check(x => DOBRegex.test(x), "Please enter a valid DOB (yyyy-mm-dd)")
    };
    bankLinks = {
      transactionIdLink: Link.state(this, "TransactionId")
        .check(x => x, "Reference number is required")
    }
    const params = {
      model: this.state,
      onChange: this.onChange,
      onClose: this.CloseModal.bind(this),
      Submit: this.Submit,
      onSelect: this.SumitSubscription,
      ShowSuccess: this.state.ShowSuccess,
      ShowAlertModal: this.state.ShowAlertModal,
      Message: this.state.Message,
      openPopup: this.openPopup,
      closePopup: this.closePopup,
      stripeLinks,
      bankLinks,
      BankDetails: this.state.BankDetails,
      handleTransactionChange: this.handleTransactionChange.bind(this),
      CreateBankPayment: this.CreateBankPayment.bind(this),
      logOut: this.logOut.bind(this),
      ShowPaymentModal: this.state.ShowPaymentModal,
      showBankPayment: this.showBankPAymentPopup.bind(this)
    };
    if (this.state.isLoading) return <Loader />;
    return <Subscription {...params} />;
  }
}



