import { http } from "Helpers/axios.helper.js";

export default class QuoteService {
  static AddQuote(data) {
    let url = "/api/quote/addquote";
    if (data.QuoteId) url = "/api/quote/updatequote";
    return http.post(url, data);
  }
  static GetItems(id) {
    const url = "/api/quote/getitems?id=" + id;
    return http.get(url);
  }
  static updateQuote(data) {
    const url = "/api/quote/updatecategory";
    return http.post(url, data);
  }
  static removeQuote(data) {
    const url = "/api/quote/removecategory";
    return http.post(url, data);
  }
  static GetQuotes(data) {
    const url = "/api/quote/getquotes";
    return http.post(url, data);
  }
  static All() {
    const url = "/api/quote/all";
    return http.get(url);
  }
  static search(data) {
    return http.get(
      "/api/quote/search?name=" +
      data.Name +
      "&fd=" +
      data.fromDate +
      "&td=" +
      data.toDate
    );
  }
  static sendpricebycategory(data) {
    const url = "/api/quote/sendcategoryprice";
    return http.post(url, data);
  }
  static sendQuotes(data) {
    const url = "/api/quote/sendquotes";
    return http.post(url, data);
  }

  static getBookingfromStatus(status) {
    var url = "/api/quote/getbooking/" + status;
    return http.get(url);
  }

  static completePayment(quoteId) {
    const url = "/api/quote/completepayment?id=" + quoteId;
    return http.get(url);
  }
  static GetAllBookings(data) {
    const url =
      "/api/quote/getuserbookings?name=" +
      data.Name +
      "&fd=" +
      data.fromDate +
      "&td=" +
      data.toDate;
    return http.get(url);
  }
  static BookSpace(warehouseId) {
    const url = "/api/quote/bookspace?id=" + warehouseId;
    return http.get(url);
  }
}



