import React from "react";
import WarehouseService from "Services/warehouse.service.js";
import WareList from "./list.js";
import Loader from "Components/common/loader.js";
class WareHouseList extends React.Component {
  constructor() {
    super();
    this.state = {
      wareObj: [],
      ShowAlertModal: false,
      isLoading: true,
      currentId: "",
      coordinates: [],
      isShowMap: false,
      center: {
        lat: -25.363882,
        lng: 131.044922
      }
    };
    this.getlist = this.getlist.bind(this);
    this.ondelete = this.ondelete.bind(this);
    this.onpopup = this.onpopup.bind(this);
    this.CloseModal = this.CloseModal.bind(this);
    this.onMapView = this.onMapView.bind(this);
    this.closeMap = this.closeMap.bind(this);
  }
  closeMap() {
    this.setState({ isShowMap: false });
  }
  componentWillMount() {
    this.getlist();
  }
  componentDidUpdate() {
    if ($(".slickSingle").not(".slick-initialized").length)
      $(".slickSingle").slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        dots: false,
        infinite: false,
        focusOnSelect: true,
        nextArrow:
          '<a href="javascript:void(0);" class="slick-next"><i class="fa fa-angle-right"></i></a>',
        prevArrow:
          '<a href="javascript:void(0);" class="slick-prev"><i class="fa fa-angle-left"></i></a>'
      });
  }
  getlist() {
    WarehouseService.GetWarehouse().then(response => {
      this.setState({
        wareObj: response.data,
        ShowAlertModal: false,
        isLoading: false
      });
    });
  }
  onpopup(id) {
    this.setState({ ShowAlertModal: true, currentId: id });
  }
  redirect(id) {
    this.props.history.push('/bankdetail/add');
  }
  onMapView(id) {
    const detail = this.state.wareObj.find(x => x._id == id);
    if (detail.Location.coordinates.length > 0) {
      this.setState({
        center: {
          lat: detail.Location.coordinates[0],
          lng: detail.Location.coordinates[1]
        },
        isShowMap: true
      });
    }
  }
  ondelete() {
    WarehouseService.delete(this.state.currentId)
      .then(response => {
        this.getlist();
        $(".slickSingle").slick("unslick");
      })
      .catch(err => {
        console.log(err);
      });
  }
  CloseModal() {
    this.setState({ ShowAlertModal: false });
  }

  render() {
    const model = {
      model: this.state,
      ondelete: this.ondelete,
      onpopup: this.onpopup,
      CloseModal: this.CloseModal,
      onMapView: this.onMapView,
      closeMap: this.closeMap,
      redirect:this.redirect.bind(this)
    };
    if (this.state.isLoading) return <Loader />;
    else return <WareList {...model} />;
  }
}
module.exports = WareHouseList;



