import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Address from "./address.js";
import WarehouseService from "../../services/warehouse.service.js";
import Helper from "Helpers/utility.helper.js";
import Link from "valuelink";

const NameRegex = /^[a-zA-Z\s]*$/;
const NumberRegex = /^\d+$/;
const initialState = {
  Name: "",
  Capacity: "",
  CapacityType: "Meter",
  Address: "",
  City: "",
  PostCode: "",
  Location: {
    type: "Point",
    coordinates: []
  },
  Website: "",
  Starthrs: "09:00",
  Endhrs: "18:00",
  About: "",
  StorageType: "Pallet",
  Services: {},
  WarehouseType: {},
  Security: {},
  Images: [],
  PendingSpace: "",
  AvailableSpace: "",
  UserId: "",
  OtherSpecify: "",
  OtherService: "",
  Bulk: {
    MaxLength: "",
    MaxLengthType: "Meter",
    MaxWidth: "",
    MaxWidthType: "Meter",
    MaxWeight: "",
    MaxWeightType: "Kgs",
    MaxHeight: "",
    MaxHeightType: "Meter",
    AvailabilityFrom: "",
    AvailabilityTo: "",
    ApxPrice: "",
    ApxPriceType: "Meter",
    TotalSpace: "",
    Description: ""
  },
  Pallet: {
    Type: { Name: "STD1", Value: "1.2" },
    Total: "",
    MaxHeight: "",
    MaxHeightType: "Meter",
    MaxWeight: "",
    MaxWeightType: "Kgs",
    ApxPrice: "",
    AvailabilityTo: "",
    AvailabilityFrom: "",
    Images: ""
  },
  Terms: false
};
let validationLink = {};

export default class AddressContainer extends Component {
  constructor() {
    super();
    this.state = initialState;

    this.addLatLng = this.addLatLng.bind(this);
  }

  handleChange(e) {
    const { name, value } = e.target;
    if (name == "Terms") {
      this.setState({ [name]: e.target.checked });
    } else this.setState({ [name]: value });
  }
  valid() {
    const errorObjects = Object.values(validationLink).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  handleSubmit() {
    if (this.valid()) {
      if (!this.state.Terms) {
        alert("Please accept terms and conditons");
        return false;
      }
      this.setState({
        UserId: Helper.DecodeToken(localStorage.getItem("userinfo")).Id
      });
      WarehouseService.AddWarehouse(this.state).then(response => {
        if (response.data.status) {
          localStorage.setItem("userinfo", response.data.token);
          this.props.history.push(
            "/warehouse/edit/" + response.data.warehouseId
          );
        }
      });
    }
  }
  componentWillMount() {
    const data = {
      Services: {},
      WarehouseType: {},
      Security: {}
    };
    WarehouseService.Services()
      .then(response => {
        this.setState({ Services: response.data });
      })
      .catch(error => {
        console.log(error);
      });
    WarehouseService.Types()
      .then(response => {
        this.setState({ WarehouseType: response.data });
      })
      .catch(error => {
        console.log(error);
      });
    WarehouseService.Security()
      .then(response => {
        this.setState({ Security: response.data });
      })
      .catch(error => {
        console.log(error);
      });
  }
  componentDidMount() {
    this.addressPostcode = new google.maps.Geocoder();
  }
  addLatLng() {
    const addres = this.state.PostCode;
    const addressPostcode = this.addressPostcode;
    addressPostcode.geocode(
      {
        address: addres
      },
      (result1, status1) => {
        if (status1 == google.maps.GeocoderStatus.OK) {
          const location = [
            result1[0].geometry.location.lat(),
            result1[0].geometry.location.lng()
          ];

          addressPostcode.geocode(
            {
              location: {
                lat: parseFloat(location[0]),
                lng: parseFloat(location[1])
              }
            },
            (result, status) => {
              if (status == google.maps.GeocoderStatus.OK) {
                // const PostCode =
                //   (result[0].address_components.find(
                //     x => x.types.indexOf("postal_code") == 0
                //   ) || "").short_name || "";
                const City =
                  (result[0].address_components.find(
                    x => x.types.indexOf("locality") == 0
                  ) || "").long_name || "";
                const Address = result[0].formatted_address;
                this.setState({
                  Location: { ...this.state.Location, coordinates: location },
                  City,
                  Address
                });
              }
            }
          );
        }
      }
    );
    // const Location = this.state.Location;
    // Location.coordinates = location;

    // this.setState({ Location, City });
  }
  render() {
    validationLink = {
      nameLink: Link.state(this, "Name")
        .check(x => x, "Provider/Warehouse name is required")
        //.check(x => NameRegex.test(x), "Characters allowed only")
        .check(x => x.trim(), "Provider/Warehouse name is required"),

      postCodeLink: Link.state(this, "PostCode")
        .check(x => x, "Post Code is required")
        .check(x => x.trim(), "PostCode name is required"),
      cityLink: Link.state(this, "City").check(x => x, "City is required"),
      addressLink: Link.state(this, "Address").check(
        x => x,
        "Address is required"
      ),
      websiteLink: Link.state(this, "Website")
    };
    const addressProps = {
      handleChange: this.handleChange.bind(this),
      handleSubmit: this.handleSubmit.bind(this),
      addLatLng: this.addLatLng,
      model: this.state,
      validationLink,
      setRef: el => (this.AddressAutocomplete = el)
    };
    return <Address {...addressProps} />;
  }
}



