import React, { Component } from "react";
import WarehouseService from "Services/warehouse.service.js";
import SearchService from "Services/search.service.js";
import Formula from "Services/formula.service.js";
import Search from "./search.js";
import Helper from "../../../helpers/utility.helper.js";
import Loader from "Components/common/loader.js";
import Link from "valuelink";

const NameRegex = /^[a-zA-Z\s]*$/;
const EmailRegex = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

let links = {};
const initialState = {
  Name: "",
  Email: "",
  Subject: "",
  Message: "",
  Phone: "",
  WarehouseId: "",
  Type: "Request a Call",
  FromDate: "",
  ToDate: "",
  SpaceRequired: ""
};
const searchModel = {
  Type: "",
  Location: "",
  TotalSpace: "",
  MaxHeight: "",
  MaxHeightType: "",
  MaxWeight: "",
  MaxWeightType: "",
  AvailabilityTo: "",
  AvailabilityFrom: "",
  Distance: "",
  showMap: false,
  Pallet: {
    Type: { Name: "", Value: "" },
    Total: ""
  },
  Bulk: {
    Item: "",
    ItemType: "Meter",
    TotalItems: "",
    MaxLength: "",
    MaxLengthType: "Meter",
    MaxWidth: "",
    MaxWidthType: "Meter"
  },
  showDetailPopup: false

};
const viewModel = {
  Name: "",
  Capacity: "",
  CapacityType: "",
  Address: "",
  City: "",
  PostCode: "",
  Website: "",
  Starthrs: "",
  Endhrs: "",
  About: "",
  StorageType: "Pallet",
  Services: {},
  WarehouseType: {},
  Security: {},
  Images: [],
  PendingSpace: "",
  UserId: "",
  lat: "",
  lng: "",
  Bulk: {
    MaxLength: "",
    MaxLengthType: "",
    MaxWidth: "",
    MaxWidthType: "",
    MaxWeight: "",
    MaxWeightType: "",
    MaxHeight: "",
    MaxHeightType: "",
    AvailabilityFrom: "",
    AvailabilityTo: "",
    ApxPrice: "",
    ApxPriceType: "",
    TotalSpace: "",
    Description: ""
  },
  Pallet: {
    Type: { Name: "", Value: "" },
    Total: "",
    MaxHeight: "",
    MaxHeightType: "",
    ApxPrice: "",
    AvailabilityTo: "",
    AvailabilityFrom: "",
    Images: ""
  }
};
const reviewModel = {
  Id: "",
  Rating: "",
  Description: ""
};
export default class searchContainer extends Component {
  constructor() {
    super();
    this.state = {
      search: { ...searchModel },
      ShowAlertModal: false,
      Message: "",
      result: [],
      Detail: { ...viewModel },
      Reviews: [],
      isLoading: true,
      RatingModel: { ...reviewModel },
      center: {
        lat: -25.363882,
        lng: 131.044922
      },
      slickSyncMain: "",
      slickSyncThumbs: "",
      isShowMap: false,
      WarehouseRequest: { ...initialState },
      ShowRequestModal: false,
      RequestAlertModal: false,
      RequestSuccessModal: false,
      ModalBody: "",
      PopUpHeader: "Request a Call"
    };
    this.addSearch = this.addSearch.bind(this);
    this.CloseModal = this.CloseModal.bind(this);
    this.getDetail = this.getDetail.bind(this);
    this.addRating = this.addRating.bind(this);
    this.onChange = this.onChange.bind(this);
    this.getRating = this.getRating.bind(this);
    this.onRatingClick = this.onRatingClick.bind(this);
    this.onMapView = this.onMapView.bind(this);
    this.closeMap = this.closeMap.bind(this);
    this.getReview = this.getReview.bind(this);
  }
  closeMap() {
    this.setState({ isShowMap: false });
  }
  getParamObject() {
    const search = { ...searchModel };
    search.Type = Helper.GetParamByName("ty");
    search.Location = Helper.GetParamByName("loc");
    search.AvailabilityFrom = Helper.GetParamByName("sd");
    search.AvailabilityTo = Helper.GetParamByName("ed");
    search.Distance = Helper.GetParamByName("ml");
    search.MaxHeightType = Helper.GetParamByName("ht");
    search.MaxHeight = Helper.GetParamByName("h");
    if (search.MaxHeightType != "Meter")
      search.MaxHeight = Formula.FeetToMeter(search.MaxHeight);

    search.MaxWeight = Helper.GetParamByName("w");
    search.MaxWeightType = Helper.GetParamByName("wt");
    if (search.MaxWeightType != "Kgs")
      search.MaxWeight = Formula.LbtoKg(search.MaxWeight);

    if (search.Type == "Pallet") {
      search.Pallet.Type.Name = Helper.GetParamByName("pt");

      search.Pallet.Total = Helper.GetParamByName("tp");
    } else {
      search.Bulk.Item = Helper.GetParamByName("bi");
      search.Bulk.ItemType = Helper.GetParamByName("bit");
      if (search.Bulk.ItemType != "Meter")
        search.Bulk.Item = Formula.FeetToMeter(search.Bulk.Item);

      search.Bulk.TotalItems = Helper.GetParamByName("tbi");
      search.Bulk.MaxLength = Helper.GetParamByName("bl");
      search.Bulk.MaxLengthType = Helper.GetParamByName("blt");
      if (search.Bulk.MaxLengthType != "Meter")
        search.Bulk.MaxLength = Formula.FeetToMeter(search.Bulk.MaxLength);

      search.Bulk.MaxWidth = Helper.GetParamByName("bw");
      search.Bulk.MaxWidthType = Helper.GetParamByName("bwt");
      if (search.Bulk.MaxWidthType != "Meter")
        search.Bulk.MaxWidth = Formula.FeetToMeter(search.Bulk.MaxWidth);

      search.TotalSpace = search.Bulk.Item * search.Bulk.TotalItems;
    }

    return search;
  }
  //Request a Call functions start
  onflatpicker(name, value) {
    this.setState({
      WarehouseRequest: { ...this.state.WarehouseRequest, [name]: value }
    });
  }
  ShowRequestPopup(id, header) {
    let body = document.getElementsByTagName("BODY")[0];
    body.classList.add("modal-open");
    let user = Helper.DecodeToken(localStorage.getItem("userinfo"));
    let warehouseRequestState = { ...initialState };
    warehouseRequestState.WarehouseId = id
    warehouseRequestState.Name = user.FirstName + " " + user.LastName;
    warehouseRequestState.Email = user.Email;
    this.setState({ ShowRequestModal: true, PopUpHeader: header, WarehouseRequest: { ...warehouseRequestState } })
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  sendMessage() {
    if (this.valid()) {
      this.state.WarehouseRequest.Type = this.state.PopUpHeader;
      WarehouseService.sendMessage(this.state.WarehouseRequest).then(response => {
        if (response.data.Status) {
          this.setState({
            RequestSuccessModal: true,
            ShowRequestModal: false,
            ModalBody: response.data.Message,
            WarehouseRequest: { ...initialState }
          });
        } else {
          this.setState({
            RequestAlertModal: true,
            ShowRequestModal: false,
            ModalBody: response.data.Message,
            WarehouseRequest: { ...initialState }
          });
        }
      });
    }
  }
  //end
  onChange(e) {
    e.preventDefault();
    const { name, value } = e.target;
    this.setState({
      RatingModel: { ...this.state.RatingModel, [name]: value }
    });
  }
  onRatingClick(val) {
    this.setState({ RatingModel: { ...this.state.RatingModel, Rating: val } });
  }
  componentWillMount() {
    const search = this.getParamObject();

    WarehouseService.Search(search)
      .then(response => {
        this.setState({
          result: response.data,
          isLoading: false
        });
      })
  }
  componentDidUpdate() {
    $(".slickSingle").not(".slick-initialized").slick({
      slidesToShow: 1,
      slidesToScroll: 1,
      dots: false,
      infinite: false,
      focusOnSelect: true,
      nextArrow:
        '<a href="javascript:void(0);" class="slick-next"><i class="fa fa-angle-right"></i></a>',
      prevArrow:
        '<a href="javascript:void(0);" class="slick-prev"><i class="fa fa-angle-left"></i></a>'
    });
  }
  getDetail(id) {
    //  const valobj = this.state.result.filter(x => x._id == id);
    this.getReview(id, true);
    /* this.setState({
       Detail: { ...valobj[0] },
       RatingModel: {
         Id: id,
         Rating: "",
         Description: ""
       },
       center: {
         lat: -25.363882,
         lng: 131.044922
       }
     });*/
  }
  onMapView(id) {
    const detail = this.state.result.find(x => x._id == id);
    if (detail.Location.coordinates.length > 0) {
      this.setState({
        center: {
          lat: detail.Location.coordinates[0],
          lng: detail.Location.coordinates[1]
        },
        isShowMap: true
      });
    }
  }

  getReview(id, showDetail) {
    const valobj = this.state.result.filter(x => x._id == id);
    let reviews = [];
    WarehouseService.GetReviews(id)
      .then(respose => {
        this.setState({
          Reviews: respose.data,
          Detail: { ...valobj[0] },
          showDetail: showDetail,
          RatingModel: {
            Id: id,
            Rating: "",
            Description: ""
          },
          center: {
            lat: -25.363882,
            lng: 131.044922
          },
        });
      })
      .catch(err => {
        this.setState({
          Reviews: [],
          Detail: { ...valobj[0] },
          showDetail: showDetail,
          RatingModel: {
            Id: id,
            Rating: "",
            Description: ""
          },
        });
      })
  }
  getRating(id) {
    this.getReview(id, false);
  }

  addSearch(id) {
    SearchService.Save({ WarehouseId: id, UserId: "" })
      .then(response => {
        if (response.status != 200) {
          this.setState({
            ShowAlertModal: true,
            Message: "Search already exist"
          });
        } else {
          this.setState({
            ShowAlertModal: true,
            Message: "Search saved successfully"
          });
        }
      })
      .catch(err => {
        console.log(err);
      });
  }

  CloseModal() {
    let body = document.getElementsByTagName("BODY")[0];
    body.classList.remove("modal-open");
    this.setState({ ShowAlertModal: false, ShowRequestModal: false, RequestAlertModal: false, RequestSuccessModal: false });
  }
  addRating() {
    WarehouseService.SaveReview(this.state.RatingModel)
      .then(response => {
        const list = this.state.Reviews;
        list.push(response.data);
        this.setState({
          RatingModel: {
            ...this.state.RatingModel,
            Description: "",
            Rating: ""
          },
          Reviews: list
        });
      })
      .catch(error => {
        console.log(error);
      });
  }
  render() {
    links = {
      nameLink: Link.state(this, "WarehouseRequest").at("Name")
        .check(x => x, "Name is Required")
        .check(x => NameRegex.test(x), "Enter Characters only"),
      emailLink: Link.state(this, "WarehouseRequest").at("Email")
        .check(x => x, "Email is Required")
        .check(x => EmailRegex.test(x), "Email not valid"),
      phoneLink: Link.state(this, "WarehouseRequest").at("Phone")
        .check(x => x, "Phone Number is Required"),
      subjectLink: Link.state(this, "WarehouseRequest").at("Subject").check(
        x => x,
        "Subject is required"
      ),
      messageLink: Link.state(this, "WarehouseRequest").at("Message").check(
        x => x,
        "Message is required"
      ),
      spaceLink: Link.state(this, "WarehouseRequest").at("SpaceRequired")
    };
    const model = {
      model: this.state,
      saveSearch: this.addSearch,
      CloseModal: this.CloseModal,
      getDetail: this.getDetail,
      closeDetails: this.closeDetails,
      addRating: this.addRating,
      onChange: this.onChange,
      CloseRating: this.CloseRating,
      getRating: this.getRating,
      onRatingClick: this.onRatingClick,
      closeMap: this.closeMap,
      onMapView: this.onMapView,
      setRef: el => (this.state.slickSyncMain = el),
      setRef1: el => (this.state.slickSyncThumbs = el),
      showDetail: this.state.showDetail,
      links,
      ShowRequestPopup: this.ShowRequestPopup.bind(this),
      sendMessage: this.sendMessage.bind(this),
      onflatpicker: this.onflatpicker.bind(this)
    };
    if (this.state.isLoading) return <Loader />;
    else return <Search {...model} />;
  }
}



