import React from "react";
const service = props => {
  const style = { textTransform: "capitalize" };

  const chkBoxes = Object.entries(props.model.Services).map(([key, val], i) => {
    const other =
      key == "others" && val == true
        ? <div>
            <br />
            <input
              type="text"
              name="OtherService"
              id="OtherService"
              value={props.model.OtherService}
              class="form-control"
              placeholder="Please Specify"
              onChange={props.change}
            />
          </div>
        : "";
    return (
      <label key={i} class="ch-checkMark">
        <input
          type="checkbox"
          id={key}
          name="service"
          checked={val}
          onChange={props.oncheckBox}
        />
        <span style={style}>
          {key}
        </span>
        {other}
      </label>
    );
  });

  return (
    <div class="col-xs-12 col-sm-6">
      <div class="form-group clearfix">
        <label class="lbl clearfix">Services</label>
        <div>
          {chkBoxes}
        </div>
        {/*<label class="ch-checkMark">
                <input type="checkbox" />
                <span>Others</span>
                <br />
                <input type="text" class="form-control" name="service" placeholder="Please Specify" />
            </label>*/}
      </div>
    </div>
  );
};

module.exports = service;



