import React from "react";
import Item from "./blogItem.js";
import MetaTags from 'react-meta-tags';
const BlogList = props => {
  let listObj = "";
  if (Object.keys(props.model.Blogs).length) {
    listObj = props.model.Blogs.map((item, key) => {
      return <Item key={key} {...item} ViewBlog={props.viewBlog} />;
    });
  }
  const hideLoadMore =
    props.model.totalCount == props.model.Blogs.length ? true : false;
  let LoadMore = "";
  if (!hideLoadMore) {
    LoadMore = (
      <div class="row loadWrapper">
        <div class="container">
          <div class="col-xs-12">
            <div class="loadMore">
              <a
                href="#"
                onClick={() =>
                  props.handlePageChange(parseInt(props.model.activePage + 1))}
              >
                Load More
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
  return (
    <div id="MainWarpper" class="bg-light aboutus">
      <MetaTags>
        <title>Warehouse Storage Solution Blog | LogistCompare</title>
        <meta name="description" content="A logistics blog providing insights and tips from top industry experts." />
        <meta name="keywords" content="warehouse, storage solutions, blog, logistics, industry experts" />
      </MetaTags>
      <section class="services_Section">
        <div class="container">
          <div class="col-xs-12">
            <div class="service_header">
              <h2>Blog</h2>
            </div>
          </div>
        </div>
      </section>
      <section class="hWarpper1 blogWrapper">
        <div class="container">
          {listObj}
          {LoadMore}
        </div>
      </section>
    </div>
  );
};
module.exports = BlogList;



