import { http } from "Helpers/axios.helper.js";
import Helper from "Helpers/utility.helper.js";
export default class QuoteService {
  static GetSpaceReport(data) {
    const user = Helper.DecodeToken(localStorage.getItem("userinfo"));
    let url = "/api/reports/space?type=" + data;
    if (user.Type == "Owner") {
      url = "/api/reports/list/space?type=" + data;
    }

    return http.get(url);
  }

  static GetRevenueReport(data) {
    const user = Helper.DecodeToken(localStorage.getItem("userinfo"));
    let url = "/api/reports/revenue?type=" + data;
    if (user.Type == "Owner") {
      url = "/api/reports/list/revenue?type=" + data;
    }

    return http.get(url);
  }

  static GetRatingReport(data) {
    const user = Helper.DecodeToken(localStorage.getItem("userinfo"));

    let url = "/api/reports/rating?type=" + data;
    if (user.Type == "Owner") {
      url = "/api/reports/list/rating?type=" + data;
    }
    return http.get(url);
  }

  static SaveReport(data) {
    const url = "/api/reports/save";

    return http.post(url, data);
  }

  static GetReport(data) {
    const url = "/api/reports/get?type=" + data;

    return http.get(url);
  }
}



