import React from "react";
import Booked from "./booked.js";
// import PendingBooking from "./pendingbooking.js";
import MetaTags from 'react-meta-tags';
const Booking = props => {
  let title = "";
  let display = "";

  if (props.model.showTitle) { title = "Bookings List", display = "block" }
  else { title = "Payment Reports", display = "none" }

  const classObj = {
    active: props.model.status == 3 ? 'active' : '',
    Inactive: props.model.status == 2 ? 'active' : '',
  };

  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="header">
        <h2>{title}</h2>
      </div>
      <div class="mainContent boxScroll">
        <div class="sStorageWarp paymentWrap">
          <div class="row">
            <div class="col-xs-12 col-sm-6 col-md-4">
              <div class="curvedTabs margin-top-2x margin-bottom-8x">
                <ul style={{ display: display }}>
                  <li class={classObj.active}>
                    <a href="" onClick={() => props.bookedList(3)} data-toggle="tab">
                      Bookings
                    </a>
                  </li>
                  <li class={classObj.Inactive}>
                    <a href="#PalletsSection" onClick={() => props.pendingList(2)} data-toggle="tab">
                      Pending Bookings
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>

          <div class="tab-content bookingList">

            <div class="tab-pane fade in active" id="BulkSection">
              <Booked {...props} />
            </div>


          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = Booking;


