import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Register from "./register.js";
import UserService from "../../services/user.service.js";
import Validation from "../validators/validation.js";
import Helper from "Helpers/utility.helper.js";
import Modal from "./register.modal.js";
import Link from "valuelink";
import Loader from "Components/common/loader.js";
const NameRegex = /^[a-zA-Z]*$/;
const EmailRegex = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

const initialState = {
  FirstName: "",
  LastName: "",
  Email: "",
  Password: "",
  ConfirmPassword: "",
  ProfilePicture: "",
  Terms: false,
  formValid: false,
  Type: "",
  ShowverificationModal: false,
  ShowAlertModal: false,
  isLoading: true,
  Videos: ""
};
let links = {};

export default class RegisterContainer extends Component {
  constructor() {
    super();
    this.state = initialState;
    this.handleChange = this.handleChange.bind(this);
    this.register = this.register.bind(this);
  }
  componentWillMount() {
    if (localStorage.getItem("userinfo") != null) {
      let UserDetail = Helper.DecodeToken(localStorage.getItem("userinfo"));
      if (UserDetail.Type == "Buyer") {
        this.props.history.push("/warehouse/find");
      } else {
        switch (UserDetail.Status) {
          case 1:
            this.props.history.push("/subscription");
            break;
          case 3:
            this.props.history.push("/warehouse/list");
            break;
        }
      }
    }

    UserService.GetVideos().then(response => {
      if (this.props.location.pathname == "/warehouse/register")
        this.setState({ Videos: response.data[1].Url });
      else this.setState({ Videos: response.data[0].Url });
    });
  }

  uploadImage(e) {
    const formData = new FormData();
    const { files, name } = e.target;
    if (
      files[0].type.includes("image") &&
      !files[0].type.includes("ico") &&
      !files[0].type.includes("gif") &&
      !files[0].type.includes("svg")
    ) {
      formData.append("image", files[0]);
      UserService.UploadImage(formData)
        .then(response => {
          this.setState({
            ProfilePicture: response.data
          });
        })
        .catch(err => {
          console.log(err);
        });
    } else {
      document.getElementById("profileImage").value = "";
    }
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  resendVerification() {
    this.setState({ ShowverificationModal: false, Message: "" });
    console.log(this.state);
    UserService.resendlink(this.state.Email).then(response => {
      if (response.data.Status) {
        this.setState({
          ShowverificationModal: true,
          Message:
            "A verification link has been sent to your email. Please verify your account."
        });
      }
    });
  }
  handleChange(e) {
    const { name, value } = e.target;
    if (name == "Terms") {
      this.setState({ [name]: e.target.checked });
    } else this.setState({ [name]: value });
  }
  componentDidMount() {
    this.setState({ isLoading: false });
  }
  closeModal() {
    this.setState({
      ShowverificationModal: false,
      ShowAlertModal: false,
      Message: ""
    });
  }
  reset() {
    this.setState(initialState);
  }

  register() {
    if (this.valid()) {
      if (!this.state.Terms) {
        this.setState({
          ShowAlertModal: true,
          Message: "Please accept terms and conditions"
        });
        return;
      }

      if (this.props.location.pathname == "/warehouse/register")
        this.state.Type = "Owner";
      else this.state.Type = "Buyer";

      UserService.register(this.state)
        .then(response => {
          if (response.data.Status) {
            this.setState({ ShowverificationModal: true });
          } else {
            this.setState({
              ShowAlertModal: true,
              Message: response.data.Message
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
  render() {
    links = {
      firstNameLink: Link.state(this, "FirstName")
        .check(x => x, "First Name is required")
        .check(x => NameRegex.test(x), "Please enter characters only"),
      lastNameLink: Link.state(this, "LastName")
        .check(x => x, "Last Name is required")
        .check(x => NameRegex.test(x), "Please enter characters only"),
      emailLink: Link.state(this, "Email")
        .check(x => x, "Email Required")
        .check(x => EmailRegex.test(x), "Email not valid"),
      passwordLink: Link.state(this, "Password")
        .check(x => x, "Password Required")
        .check(
          x => x.length >= 6,
          "Password must be contain at least six characters"
        ),
      confirmPasswordLink: Link.state(this, "ConfirmPassword").check(
        x => x == this.state.Password,
        "Password and Confirm Password should be same."
      )
    };

    const modalObject = {
      ShowverificationModal: this.state.ShowverificationModal,
      onClose: this.closeModal.bind(this),
      onResend: this.resendVerification.bind(this),
      ShowAlertModal: this.state.ShowAlertModal,
      ModalBody: this.state.Message,
      IsBuyer: this.state.Type == "Buyer" ? true : false
    };

    const registerObject = {
      State: this.state,
      onChange: this.handleChange,
      onClick: this.register,
      UploadImage: this.uploadImage.bind(this),
      ProfileImage: this.state.ProfilePicture,
      links,
      path: this.props.location.pathname
    };
    if (this.state.isLoading) return <Loader />;
    else
      return (
        <div>
          <Register {...registerObject} />
          <Modal {...modalObject} />
        </div>
      );
  }
}



