import React from "react";
import Helper from "../../helpers/utility.helper.js";
import Rating from "Components/common/starRating.js";
const CustomerQuotes = props => {
  let sendQuoteButton = null;
  if (props.QuotesRequested.Status == 1) {
    sendQuoteButton = (
      <a
        href={"/warehouse/quotes/" + props.QuotesRequested._id}
        class="button button-action button-border-thin button-min-width margin-top-2x margin-bottom-2x"
      >
        SendQuote
      </a>
    );
  } else if (props.QuotesRequested.Status == 5) {
    sendQuoteButton = (
      <a
        href={"/warehouse/quotes/" + props.QuotesRequested._id}
        class="button button-action button-border-thin button-min-width margin-top-2x margin-bottom-2x"
      >
        SendQuote
      </a>
    );
  }

  return (
    <div class="row payment">
      <div class="cell">
        <div class="col-xs-12 col-sm-12 col-lg-12">
          <div class="payment_img">
            <div class="noitfy_img">
              <img
                src={props.Userinfo.ProfilePicture}
                onError={e => {
                  e.target.src = "/Content/images/default-user.jpg";
                }}
                alt="Logist Compare"
                class="img-responsive"
              />
            </div>
          </div>
          <div class="star-payment">
            <h2>
              {props.Userinfo.FirstName + " " + props.Userinfo.LastName}
            </h2>
            <span
              onClick={() => {
                props.getWarehouseDetail(props._id);
              }}
            >
              <label style={{ cursor: "pointer" }} />
              <div class="ratingBlk">
                {/* <Rating input={props.Userinfo.AvgReviews} /> */}
              </div>
            </span>
          </div>
        </div>
      </div>
      <div class="cell">
        <div class="__content">
          <h3>
            {props.Name}
          </h3>
          <div class="row">
            <div class="col-xs-12 col-sm-6 col-lg-3 form-group">
              <label>Request On</label>
              <p>
                {Helper.ConvertDateToString(props.QuotesRequested.BookedOn)}
              </p>
            </div>
            <div class="col-xs-12 col-sm-6 col-lg-3 form-group">
              <label>Quote Sent On</label>
              <p>
                {Helper.ConvertDateToString(props.QuotesRequested.CreatedDate)}
              </p>
            </div>
            <div class="col-xs-12  col-sm-6 col-lg-3 form-group">
              <label>Quotation Form</label>
              <p>
                {props.QuotesRequested.Status == 1 ? "Pending" : "Sent"}
              </p>
            </div>
            <div class="col-xs-12 col-sm-6 col-lg-3 form-group">
              {sendQuoteButton}
              <a
                href={"/user/message/" + props.Userinfo._id}
                class="button button-action button-border-thin button-min-width margin-top-2x margin-bottom-2x"
              >
                <i class="fa fa-comments" /> Messages
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = CustomerQuotes;



