import React from "react";
const google = window.google;
import GoogleMap from "Components/map/googlemap.js";
import { Marker } from "react-google-maps";
import RatingList from "Components/rating/reviewList.js";
import Helper from "Helpers/utility.helper.js";
import Rating from "Components/common/starRating.js";
import MetaTags from 'react-meta-tags';
const detail = props => {
  const style = { textTransform: "capitalize" };
  const services = Object.entries(
    props.model.Detail.Services
  ).map(([key, val], i) => {
    if (val == true && key == "others") {
      return (
        <label class="options" key={i} style={style}>
          {props.model.Detail.OtherService}
        </label>
      );
    } else if (val == true) {
      return (
        <label class="options" key={i} style={style}>
          {key}
        </label>
      );
    }
  });

  const securities = Object.entries(
    props.model.Detail.Security
  ).map(([key, val], i) => {
    if (val == true && key == "others") {
      return (
        <label class="options" key={i} style={style}>
          {props.model.Detail.OtherSpecify}
        </label>
      );
    } else if (val == true) {
      return (
        <label class="options" key={i} style={style}>
          {key}
        </label>
      );
    }
  });
  const warehouseTypes = Object.entries(
    props.model.Detail.WarehouseType
  ).map(([key, val], i) => {
    if (val == true) {
      return (
        <label class="options" key={i} style={style}>
          {key}
        </label>
      );
    }
  });

  let i = 1;
  let images = props.model.Detail.Images.map(val => {
    i += 1;
    return (
      <li key={i}>
        <img src={val} alt="1" class="img-responsive" />
      </li>
    );
  });
  if (images.length == 0) {
    images.push(<li key="0">
      <img src="/Content/images/warehouse/w1.jpg" alt="1" class="img-responsive" />
    </li>);
  }

  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="header">
        <h2>Warehouses</h2>
      </div>
      <div class="mainContent boxScroll">
        <div class="innerPadding padding-top-4x padding-bottom-4x">
          <div class="wh_Warp warehouseDetailWarp">
            <div class="row">
              <div class="col-sm-7 col-xss-7">
                <div class="ratingBlk margin-bottom-3x">
                  <label class="lbl">Rating</label>
                  <Rating input={props.model.Detail.AvgReviews} />
                </div>
              </div>
              <div class="col-sm-5 col-xss-5 dark">
                <a href="/warehouse/list" class="__backButton margin-bottom-3x">
                  <i class="fa fa-arrow-left" aria-hidden="true" /> Back to
                  warehouse list
                </a>
              </div>
            </div>

            <div class="row">
              <div class="col-xs-12 col-sm-6 col-md-5">
                <div class="sliderBlock margin-bottom-4x">
                  <ul class="slickSyncMain">{images}</ul>
                  <ul class="slickSyncThumbs">{images}</ul>
                </div>
                <div class="mapBlk margin-bottom-4x">
                  <div id="map">
                    <GoogleMap center={props.model.center}>
                      {<Marker position={props.model.center} />}
                    </GoogleMap>
                  </div>
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 col-md-7">
                <div class="blks margin-bottom-6x">
                  <h3>About</h3>
                  <p>{props.model.Detail.About}</p>
                </div>
                <div class="blks margin-bottom-6x">
                  <h3>Address</h3>
                  <p>
                    {props.model.Detail.Address},{props.model.Detail.City},{props.model.Detail.PostCode}
                  </p>
                </div>
                <div class="blks margin-bottom-6x">
                  <h3>Services</h3>
                  {services}
                </div>
                <div class="blks margin-bottom-6x">
                  <h3>Security</h3>
                  {securities}
                </div>

                <div class="blks margin-bottom-6x">
                  {" "}
                  <h3>Warehouse Type</h3>
                  {warehouseTypes}
                </div>
                <div class="blks openingHrs margin-bottom-6x">
                  <h3>Capacity</h3>
                  <p>
                    {Helper.RoundOffCapacity(props.model.Detail.Capacity)}{" "}
                    {props.model.Detail.CapacityType == ""
                      ? "Sq Meter"
                      : "Sq " + props.model.Detail.CapacityType}
                  </p>
                </div>
                <div class="blks openingHrs margin-bottom-6x">
                  <h3>Opening Hours</h3>
                  <p>
                    {props.model.Detail.Starthrs} - {props.model.Detail.Endhrs}
                  </p>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-xs-12">
                <RatingList {...props} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

module.exports = detail;



