import React from "react";
const images = props => {
  let i = 1;
  var images = props.model.Images.map(val => {
    i += 1;
    return (
      <span class="__blk" key={i}>
        <span
          class="button button-danger button-box button-small"
          onClick={() => {
            props.removeImage(val);
          }}
        >
          {" "}<i class="fa fa-trash" />
        </span>
        <img src={val} alt="" class="img-responsive" />
      </span>
    );
  });
  if (images.length < 4) {
    images.push(
      <span class="__blk" key={images.length + 2}>
        <input
          type="file"
          name="pallet"
          onChange={props.onUploadimage}
          accept="image/*"
        />
        <i class="icon-plus ico-t4" />
      </span>
    );
  }
  return (
    <div class="row">
      <div class="col-xs-12">
        <div class="form-group">
          <label>Upload Images</label>
          <div class="__uploadedBlk">
            {images}
          </div>
        </div>
      </div>
    </div>
  );
};

module.exports = images;



