import React, { Component } from "react";
import Detail from "./detailModal.js";
export default class searchContainer extends Component {
  constructor() {
    super();
    this.state = {
    };
    this.initSlider = this.initSlider.bind(this);
  }

  initSlider() {
    if (this.props.model.Detail._id) {
      try {
        const syncMain = $("#slickSingle"),
          syncThumbs = document.getElementsByClassName("slickSyncThumbs");
        $("#slickSingle").not(".slick-initialized").slick({
          slidesToShow: 1,
          slidesToScroll: 1,
          dots: false,
          infinite: false,
          focusOnSelect: true,
          nextArrow:
            '<a href="javascript:void(0);" class="slick-next"><i class="fa fa-angle-right"></i></a>',
          prevArrow:
            '<a href="javascript:void(0);" class="slick-prev"><i class="fa fa-angle-left"></i></a>'
        });
      }
      catch (err) {
        console.log(err);
      }
    }
  }
  componentWillUpdate() {
    if (!$("#slickSingle").not(".slick-initialized").length) {
      $("#slickSingle").slick("unslick");
    }
  }
  componentDidUpdate() {
    //this.initSlider();
    if (this.props.model.Detail.Images.length > 1) {
      if ($("#slickSingle").not(".slick-initialized").length)
        $("#slickSingle").slick({
          slidesToShow: 1,
          slidesToScroll: 1,
          dots: false,
          infinite: true,
          focusOnSelect: true,
          nextArrow:
            '<a href="javascript:void(0);" class="slick-next"><i class="fa fa-angle-right"></i></a>',
          prevArrow:
            '<a href="javascript:void(0);" class="slick-prev"><i class="fa fa-angle-left"></i></a>'
        });
    }
  }
  render() {
    //let data = { ...this.props };
    return (<Detail {...this.props} />);
  }
}



