import React from "react";
import QuoteItem from "./quoteitem.js";
import Flatpickr from "react-flatpickr";
import ReviewModal from "Components/rating/reviewModal.js";
import Detail from "Components/detailModal/detailModal.container.js";
import MapModal from "Components/common/mapModal.js";
import MetaTags from 'react-meta-tags';
const MyQuotes = props => {
  let quoteList = {};
  const map = props.model.isShowMap ? <MapModal {...props} /> : "";
  quoteList = props.bookingList.map((x, i) => {
    return (
      <QuoteItem
        getWarehouseDetail={props.getWarehouseDetail}
        key={i}
        WarehouseDetail={x.warehouse_detail}
        getDetail={props.getDetail}
        index={i}
        onMapView={() => props.onMapView(x._id)}
        {...x}
      />
    );
  });
  if (quoteList.length == 0) {
    quoteList = (
      <div class="no-result-found" key="1">
        <p>No Result Found</p>
      </div>
    );
  }

  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="header">
        <h2>My Quotes</h2>
      </div>
      <div class="mainContent boxScroll">
        <div class="sStorageWarp quoteWarp">
          <div class="tab-content">
            <div class="tab-pane fade in active" id="resultViewTab">
              <div class="row">
                <div class="col-xs-12 col-sm-10">
                  <div class="__findBy">
                    <div class="__searchByName form-group">
                      <label class="ib_v">Find</label>
                      <input
                        type="text"
                        class="form-control ib_v"
                        name="Name"
                        value={props.Search.Name}
                        onChange={props.handleChange}
                        onKeyPress={props.handleKeyPress}
                      />
                    </div>
                    <div class="__searchByDate form-group">
                      <label>Date</label>
                      <div class="__dateRang">
                        <div class="input-group">
                          <Flatpickr
                            type="text"
                            class="form-control"
                            placeholder="Start Date"
                            value={props.Search.fromDate}
                            onChange={(_, str) =>
                              props.onflatPicker("fromDate", str)}
                          />
                          <span class="input-group-addon">to</span>
                          <Flatpickr
                            type="text"
                            class="form-control"
                            placeholder="End Date"
                            value={props.Search.toDate}
                            onChange={(_, str) =>
                              props.onflatPicker("toDate", str)}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="col-xs-12 col-sm-2">
                  <a
                    href="javascript:void(0);"
                    class="search-btn button-action "
                    onClick={props.searchQuotes}
                  >
                    <i class="fa fa-search" />
                  </a>
                </div>
              </div>
              {quoteList}
            </div>
          </div>
        </div>
      </div>

      <ReviewModal {...props} />
      {props.model.isShowDetail && <Detail {...props} />}
      {map}
    </div>
  );
};
module.exports = MyQuotes;



