import React from "react";
import BulkItem from "./confirmedbulkItem.js";
import PelletItem from "./confirmedpalletItem.js";
import VInput from "Components/common/VInput";
const QuoteList = props => {
  let quoteButton = null;
  let quoteList = null;
  if (props.State.IsBulk) {
    if (props.State.Bulks.length > 0) {
      quoteList = props.State.Bulks.map((x, i) => {
        return (
          <BulkItem
            EditBulk={props.EditBulk}
            key={i}
            index={i}
            {...x}
            bid={props.State.Bulk._id}
          />
        );
      });
    }
  } else {
    if (props.State.Pallets.length > 0) {
      quoteList = props.State.Pallets.map((x, i) => {
        return (
          <PelletItem
            EditPallet={props.EditPallet}
            key={i}
            index={i}
            {...x}
            pid={props.State.Pallet._id}
          />
        );
      });
    }
  }

  return (
    <div class="cell">
      <div class="bookingFormWarp">
        <h2 class="h2Title">Booking Form</h2>
        {quoteList}

        <div class="row Row-10 margin-top-2x">
          <div class="col-xs-12 col-sm-4 form-group grandTotal">
            <strong>
              {" "}Total Cost: &pound;{props.State.TotalCostBreakDown.FinalCost}
            </strong>
          </div>

          <div class="col-xs-12 col-sm-8 form-group">
            <div class="form-group">
              <input
                type="button"
                class="button button-primary pull-right"
                value="Confirm & Pay"
                onClick={() => {
                  props.openPopup();
                }}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = QuoteList;



