import React from "react";
import helper from "Helpers/utility.helper.js";
import { Set } from "Helpers/axios.helper.js";
import NotificationCount from "Services/notification.service.js";
import { withRouter } from "react-router-dom";
class SideHeader extends React.Component {
  constructor() {        
    super();
    this.state = {
      count:0
    };
    this.logOut = this.logOut.bind(this);
    this.getNotificationCount.bind(this);    
    this.redirect.bind(this);
  }
  
  componentWillReceiveProps(){ 
    this.getNotificationCount();
  }
  componentWillMount(){ 
    this.getNotificationCount();
  }
  getNotificationCount(i){
    if (!!localStorage.getItem("userinfo")) {            
      NotificationCount.GetCount().then(response => {
        this.setState({count:response.data})       
      });
    }
  }
  redirect(){
    this.props.history.push("/user/notification");
  }
  logOut() {
    let userType = helper.DecodeToken(localStorage.getItem("userinfo")).Type;
    localStorage.removeItem("userinfo");
    Set(null);
    if (userType == "Owner") this.props.history.push("/warehouse/login");
    else this.props.history.push("/findspace/login");
  }
  render() {    
    const { history } = this.props;
    let userDetail = helper.DecodeToken(localStorage.getItem("userinfo"));
    return (
      <div id="MainWarpper" class="bg-light">
        <div class="container">
          <div class="row">
            <div class="col-xs-12">
              <section class="mainSection warehouseWarpper findStorageWarpper bookingFormWarpper quotesWarpper">
                <aside id="aside">
                  <a href="javascript:void(0);" class="collapesToggle">
                    <i class="fa fa-list-ul" />
                  </a>
                  <nav class="boxScroll2">
                    <ul>
                      <li class="proInfo">
                        <span class="imgBlk margin-bottom-2x">
                          <img
                            src={userDetail.ProfilePicture}
                            onError={e => {
                              e.target.src = "/Content/images/default-user.jpg";
                            }}
                            alt=""
                            class="img-responsive"
                          />
                        </span>
                        <h3>
                          {userDetail.FirstName + " " + userDetail.LastName}
                        </h3>
                      </li>
                      {this.props.links}
                      <li className="notify__Setting">
                      <div class="notify_icon">{this.state.count}</div>
                        <a
                          href="javascript:void(0)"
                          onClick={() => this.redirect()}
                          data-toggle="tooltip"
                          data-placement="right"
                          title="Notifications"                          
                        >
                          <i class="icon-bell" /> <span>Notifications</span>
                        </a>
                      </li>               
                      <li>
                        <a
                          href="javascript:void(0)"
                          onClick={() => this.logOut()}
                          data-toggle="tooltip"
                          data-placement="right"
                          title="Logout"
                        >
                          <i class="fa fa-sign-out" /> <span>Log Out</span>
                        </a>
                      </li>
                    </ul>
                  </nav>
                </aside>
                {this.props.children}
              </section>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
module.exports = withRouter(SideHeader);



