import React from "react";
import BlogList from "./blogs.js";
import BlogService from "Services/blog.service.js";
let skip = 1;
export default class BlogListContainer extends React.Component {
  constructor() {
    super();
    this.state = { activePage: 1, totalCount: 0, Blogs: [] };
  }
  getAllBlogs(skipCount, pageNumber) {
    BlogService.getSkipBlockList(skipCount).then(response => {
      this.setState({
        Blogs: this.state.Blogs.concat(response.data.blogs),
        activePage: pageNumber
      });
    });
  }

  handlePageChange(pageNumber) {
    let skipCount = (pageNumber - skip) * 3;
    this.getAllBlogs(skipCount, pageNumber);
  }
  viewBlog(Id) {
    this.props.history.push("/blog/" + Id);
  }
  componentWillMount() {
    this.getAllBlogs(0, 1);
    BlogService.all().then(response => {
      this.setState({ totalCount: response.data.length });
    });
  }
  render() {
    const listObj = {
      model: this.state,
      handlePageChange: this.handlePageChange.bind(this),
      viewBlog: this.viewBlog.bind(this)
    };
    return <BlogList {...listObj} />;
  }
}



