import React from "react";
import QuoteList from "./providerQuoteList.js";
import QuoteForm from "./providerQuoteForm.js";
import MetaTags from 'react-meta-tags';
const Quotes = props => {
  const SuccessModal = {
    ModalCss: props.State.ShowSuccessModal ? "in" : "",
    ModalStyle: { display: props.State.ShowSuccessModal ? "block" : "none" },
    ModalBody: props.State.ModalBody,
    onClose: props.onClose
  };
  const ALertModal = {
    ModalCss: props.State.ShowAlertModal ? "in" : "",
    ModalStyle: { display: props.State.ShowAlertModal ? "block" : "none" },
    ModalBody: props.State.ModalBody,
    onClose: props.onClose
  };
  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="header">
        <h2>Select Storage</h2>
      </div>
      <div class="mainContent boxScroll">
        <div class="findStorageInner">
          <QuoteList {...props} />
          {props.State.WarehouseDetail == null ? "" : <QuoteForm {...props} />}
        </div>
      </div>
      <div
        class={"modal fade " + SuccessModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={SuccessModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={SuccessModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Success</h2>
                <p>
                  {SuccessModal.ModalBody}
                </p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <a
                    href="javascript:void(0)"
                    onClick={props.successRedirect}
                    class="button button-border button-primary button-uppercase margin-top-3x"
                  >
                    Ok
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div
        class={"modal fade " + ALertModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={ALertModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={ALertModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Alert !</h2>
                <p>
                  {ALertModal.ModalBody}
                </p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Ok"
                    onClick={ALertModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = Quotes;



