import React from "react";
import Rating from "Components/common/starRating.js";
import Messagebox from "Components/common/messagebox.js";
import MapModal from "Components/common/mapModal.js";
import Helper from "../../../helpers/utility.helper.js";
import Detail from "Components/detailModal/detailModal.container.js";
import ReviewModal from "Components/rating/reviewModal.js";
import GoogleMap from "Components/map/googlemap.js";
import { Marker } from "react-google-maps";
import Formula from "Services/formula.service.js";
import MetaTags from 'react-meta-tags';
import CallModal from "./requestModal";
import VInput from "Components/common/VInput";
import { TextArea } from "valuelink/tags";
import Flatpickr from "react-flatpickr";
const search = props => {
  const watrehouses = props.model.result.map((item, index) => {
    const images = item.Images.map((val, k) => (
      <li key={k}>
        <img
          src={val}
          alt="1"
          class="img-responsive"
          onError={e => {
            e.target.src = "/Content/images/warehouse/w1.jpg";
          }}
        />
      </li>
    ));
    if (images.length < 1) {
      images.push(
        <li key="1">
          <img
            src="/Content/images/warehouse/w1.jpg"
            alt="1"
            class="img-responsive"
          />
        </li>
      );
    }
    let Totalcost = 0;
    let avlSpace = 0;
    let userSpace = 0;
    let userPrice = 0;
    let AvailabilityTime = "";
    if (Helper.GetParamByName("ty") == "Pallet" && item.Status != 2) {
      AvailabilityTime = (
        <p>
          {Helper.ConvertDateToString(item.Pallet.AvailabilityFrom)} -{" "}
          {Helper.SetEndDate(item.Pallet.AvailabilityTo)}
        </p>
      );
      const palltes =
        parseFloat(Helper.GetParamByName("tp")) ||
        parseFloat(item.Pallet.Total);
      Totalcost = isNaN(
        parseFloat(parseFloat(item.Pallet.ApxPrice) * palltes).toFixed(2)
      )
        ? 0
        : parseFloat(parseFloat(item.Pallet.ApxPrice) * palltes).toFixed(2);
      avlSpace = parseFloat(
        parseFloat(item.Pallet.Type.Value) * parseFloat(item.Pallet.Total)
      ).toFixed(2);
      userSpace = parseFloat(
        parseFloat(item.Pallet.Type.Value) * palltes
      ).toFixed(2);

      userPrice = item.Pallet.ApxPrice + " / pallet";
    } else if (Helper.GetParamByName("ty") == "Bulk" && item.Status != 2) {
      AvailabilityTime = (
        <p>
          {Helper.ConvertDateToString(item.Bulk.AvailabilityFrom)}-{" "}
          {Helper.SetEndDate(item.Bulk.AvailabilityTo)}
        </p>
      );
      let bulkPrice = 0;

      const bulkSpace =
        parseFloat(Helper.GetParamByName("bi")) || item.Bulk.TotalSpace;
      if (item.Bulk.ApxPriceType == "Meter" && item.CapacityType != "Meter") {
        bulkPrice = parseFloat(item.Bulk.ApxPrice * 3.28084).toFixed(2);
      } else {
        bulkPrice = parseFloat(item.Bulk.ApxPrice);
      }

      avlSpace = item.Bulk.TotalSpace == null ? 0 : item.Bulk.TotalSpace;
      Totalcost = isNaN(parseFloat(
        parseFloat(bulkPrice) * parseFloat(bulkSpace)
      ).toFixed(2)) ? 0 : parseFloat(
        parseFloat(bulkPrice) * parseFloat(bulkSpace)
      ).toFixed(2);

      userSpace = bulkSpace;
      userPrice = item.Bulk.ApxPrice + " / Sq " + item.Bulk.ApxPriceType;
    }
    const messageLink = "/user/message/" + item.UserId;
    const bookingLink =
      "/user/quotes?wid=" + item._id + "&type=" + Helper.GetParamByName("ty");

    return (
      <div key={index} class="resultWarp margin-bottom-6x">
        <div class="cell">
          <ul class="slickSingle">{images}</ul>
        </div>
        <div class="cell">
          <div class="__content">
            <span
              style={{ cursor: "pointer" }}
              data-toggle="modal"
              data-target="#warehouseDetail"
              onClick={() => {
                props.getDetail(item._id);
              }}
            >
              <h3>{item.City}</h3>
            </span>
            <div class="row">
              {item.Status == 2 ? "" : (<div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
                <label>Space Availabilty</label>
                <p>{avlSpace} sq {item.Status == 2 ? "Feet" : "Meter"}</p>
              </div>)}
              {item.Status == 2 ? "" : (<div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
                <label>Time Availblity</label>
                {AvailabilityTime}
              </div>)}
              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
                <label>Location</label>
                <p>
                  <span
                    href="#MapDetailModal"
                    data-toggle="modal"
                    onClick={() => {
                      props.onMapView(item._id);
                    }}
                    style={{ cursor: "pointer" }}
                  >
                    View on map <i class="fa fa-angle-right" />
                  </span>
                </p>
              </div>
              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
                <label>Capacity</label>
                <p>{item.Capacity} sq {item.Status == 2 ? "Feet" : "Meter"}</p>
              </div>
              {item.Status == 2 ? "" : (<div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
                <span
                  data-toggle="modal"
                  data-target="#viewRatingModal"
                  onClick={() => {
                    props.getRating(item._id);
                  }}
                >
                  <label style={{ cursor: "pointer" }}>Rating</label>
                  <div class="ratingBlk">
                    <Rating input={item.AvgReviews} />
                  </div>
                </span>
              </div>)}
              {item.Status == 2 ? "" : (<div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
                <label>Approx Cost</label>
                <div class="dropdown">
                  <div class="costDropdown">
                    <a
                      href="javascript:void(0);"
                      class="dropdown-toggle"
                      data-toggle="dropdown"
                    >
                      &pound;{Totalcost}
                    </a>
                    <ul class="dropdown-menu">
                      <li>
                        <a href="javascript:void(0);">{userSpace} / Sq Meter</a>
                      </li>
                      <li>
                        <a href="javascript:void(0);">{userPrice}</a>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>)}

              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-12 savebtn">
                {item.Status != 2 ? <a
                  class="button button-action button-min-width margin-top-2x margin-bottom-2x"
                  onClick={() => {
                    props.saveSearch(item._id);
                  }}
                >
                  Save
                </a> : ""}
              </div>
            </div>
          </div>
        </div>

        {
          item.Status == 2 ? (<div class="cell button-cell"><a
            href="javascript:void(0);"
            onClick={() => props.ShowRequestPopup(item._id, "Request a Call")}
            class="button button-action button-min-width margin-top-2x margin-bottom-2x"
          >
            Request a call back
          </a>
            <a
              href="javascript:void(0);"
              onClick={() => props.ShowRequestPopup(item._id, "Enquire")}
              class="button button-default button-min-width margin-top-2x margin-bottom-2x"
            >
              Enquire
          </a></div>) :
            (<div class="cell button-cell"><a
              href={bookingLink}
              class="button button-action button-min-width margin-top-2x margin-bottom-2x"
            >
              Book
          </a>
              <a
                href={messageLink}
                class="button button-default button-min-width margin-top-2x margin-bottom-2x"
              >
                Message
          </a></div>)
        }
      </div >
    );
  });

  const map = props.model.isShowMap ? <MapModal {...props} /> : "";
  if (watrehouses.length == 0)
    watrehouses.push(
      <div class="no-result-found" key="0">
        <p>No Result Found</p>
      </div>
    );
  const ALertModal = {
    isShow: props.model.ShowAlertModal,
    onClose: props.CloseModal,
    Message: props.model.Message
  };
  const RequestAlertModal = {
    ModalCss: props.model.RequestAlertModal ? "in" : "",
    ModalStyle: { display: props.model.RequestAlertModal ? "block" : "none" },
    onClose: props.CloseModal,
    ModalBody: props.model.ModalBody
  };
  const RequestSuccessModal = {
    ModalCss: props.model.RequestSuccessModal ? "in" : "",
    ModalStyle: { display: props.model.RequestSuccessModal ? "block" : "none" },
    onClose: props.CloseModal,
    ModalBody: props.model.ModalBody
  };
  const RequestModal = {
    ModalCss: props.model.ShowRequestModal ? "in" : "",
    ModalStyle: { display: props.model.ShowRequestModal ? "block" : "none" },
    onClose: props.CloseModal
  };

  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="header">
        <h2>Select Storage</h2>
      </div>
      <div class="mainContent boxScroll">
        <Messagebox {...ALertModal} />
        <div class="sStorageWarp">
          <div class="tab-content">
            <div class="tab-pane fade in active" id="resultViewTab">
              <div class="row head margin-bottom-6x">
                <div class="col-xs-12 col-xss-6 col-sm-6">
                  <h2 class="h2Title">Search Results</h2>
                </div>
                <div class="col-xs-12 col-xss-6 col-sm-6" />
              </div>
              {watrehouses}
            </div>
          </div>
        </div>
      </div>

      <Detail {...props} />

      <ReviewModal {...props} />
      {map}
      <div
        class={"modal fade " + RequestModal.ModalCss}
        id="myModal-Request"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={RequestModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={RequestModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>{props.model.PopUpHeader}</h2>
              </div>
              <div class="formWarp password">
                <div class="form-group">
                  <div class="request-message">
                    <div class="contactForm">
                      <div class="formWarp">
                        <div class="form-group">
                          <input
                            class="form-control"
                            placeholder="Name"
                            type="text"
                            maxLength="100"
                            readOnly
                            value={props.model.WarehouseRequest.Name}
                          />
                        </div>
                        <div class="form-group">
                          <input
                            class="form-control"
                            placeholder="Email"
                            type="text"
                            maxLength="100"
                            readOnly
                            value={props.model.WarehouseRequest.Email}
                          />
                        </div>
                        <div class="form-group">
                          <VInput
                            class="form-control"
                            placeholder="Phone Number"
                            type="text"
                            maxLength="100"
                            valueLink={props.links.phoneLink}
                          />
                        </div>
                        <div class="form-group">
                          <VInput
                            class="form-control"
                            placeholder="Subject"
                            type="text"
                            maxLength="100"
                            valueLink={props.links.subjectLink}
                          />
                        </div>
                        {props.model.PopUpHeader == "Enquire" ? (<div class="form-group">
                          <VInput
                            class="form-control"
                            placeholder="Space Required"
                            type="text"
                            maxLength="100"
                            valueLink={props.links.spaceLink}
                          />
                        </div>) : ""}
                        {props.model.PopUpHeader == "Enquire" ? (<div class="form-group">
                          <Flatpickr
                            type="text"
                            class="form-control"
                            data-min-date="today"
                            placeholder="From Date"
                            value={props.model.WarehouseRequest.FromDate}
                            onChange={(_, str) =>
                              props.onflatpicker("FromDate", str)}
                          />
                        </div>) : ""}
                        {props.model.PopUpHeader == "Enquire" ? (<div class="form-group">

                          <Flatpickr
                            type="text"
                            class="form-control"
                            data-min-date="today"
                            placeholder="To Date"
                            value={props.model.WarehouseRequest.ToDate}
                            onChange={(_, str) =>
                              props.onflatpicker("ToDate", str)}
                          />
                        </div>) : ""}
                        <div class="form-group">
                          <TextArea
                            class="form-control"
                            rows="5"
                            id="comment"
                            placeholder="Additional Notes"
                            valueLink={props.links.messageLink}
                          />
                          {/* {props.links.messageLink.validationError && (
                            <p class="text-danger">
                              {" "}
                              {props.links.messageLink.validationError}
                            </p>
                          )} */}
                        </div>
                        <div class="form-group request-message-submit-button">
                          <input
                            class="button button-border button-action button-uppercase button-min-width margin-top-3x margin-right-2x"
                            value="Send"
                            onClick={props.sendMessage}
                            type="button"
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/*Request Popup Start*/}
      <div
        class={"modal fade " + RequestAlertModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={RequestAlertModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={RequestAlertModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Alert !</h2>
                <p>{RequestAlertModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Ok"
                    onClick={RequestAlertModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + RequestSuccessModal.ModalCss}
        id="myModal-dfgdg"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-sdfsdf"
        style={RequestSuccessModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={RequestSuccessModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Success</h2>
                <p>{RequestSuccessModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <a
                    href="javascript:void(0);"
                    onClick={RequestSuccessModal.onClose}
                    class="button button-border button-primary button-uppercase margin-top-3x"
                  >
                    Ok
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/*Request Popup end*/}
    </div >
  );
};

module.exports = search;



