import { http } from "Helpers/axios.helper.js";
const addUrl = `/api/warehouse/add`;
const updateUrl = `/api/warehouse/update`;
const getUrl = "/api/warehouse/get/";
const saveUrl = "/api/warehouse/create";
const imageuploadUrl = `/api/warehouse/uploadimage`;
const allUrl = `/api/warehouse/all/`;
const findUrl = `/api/warehouse/find/`;
const searchUrl = `/api/warehouse/search/`;
const deleteUrl = `/api/warehouse/delete/`;
const serviceUrl = `/api/warehouse/services/`;
const securityUrl = `/api/warehouse/security/`;
const typeUrl = `/api/warehouse/types/`;
const addReviewUrl = `/api/warehouse/review/save`;
const getReviewUrl = `/api/warehouse/review/`;

export default class WarehouseService {
  static Add(data) {
    if (!data._id) return http.post(addUrl, data);
    else return http.post(updateUrl, data);
  }
  static GetWarehouse() {
    return http.get(allUrl);
  }

  static Find(data) {
    return http.get(
      `${findUrl}?type=${data.Type}&startdate=${data.Startdate}&enddate=${data.EndDate}&lnglat=${data.Lat},${data.Lng}&distance=${data.Distance}`
    );
  }
  static Search(data) {
    return http.get(
      searchUrl +
      "?ty=" +
      data.Type +
      "&loc=" +
      data.Location +
      "&sd=" +
      data.AvailabilityFrom +
      "&ed=" +
      data.AvailabilityTo +
      "&ml=" +
      data.Distance +
      "&h=" +
      data.MaxHeight +
      "&w=" +
      data.MaxWeight +
      "&pt=" +
      data.Pallet.Type.Name +
      "&tp=" +
      data.Pallet.Total +
      "&bi=" +
      data.Bulk.Item +
      "&tbs=" +
      data.TotalSpace +
      "&bl=" +
      data.Bulk.MaxLength +
      "&bw=" +
      data.Bulk.MaxWidth
    );
  }

  static Get(id) {
    return http.get(getUrl + id);
  }

  static delete(id) {
    return http.get(deleteUrl + id);
  }

  static UploadImage(data) {
    return http.post(imageuploadUrl, data);
  }

  static AddWarehouse(data) {
    return http.post(saveUrl, data);
  }

  static Services(data) {
    return http.get(serviceUrl, data);
  }

  static Types(data) {
    return http.get(typeUrl, data);
  }

  static Security(data) {
    return http.get(securityUrl, data);
  }
  static SaveReview(data) {
    return http.post(addReviewUrl, data);
  }
  static GetReviews(id) {
    return http.get(getReviewUrl + id);
  }
  static sendMessage(data) {
    const url = "/api/warehouserequest/save";
    return http.post(url, data);
  }
}



