import React from "react";
import Rating from "Components/common/starRating.js";
const reviewList = props => {
  const reviews = props.model.Reviews.map((val, key) => {
    return (
      <div class="media" key={key}>
        <div class="media-left">
          <a href="javascript:void(0);" class="imgBlk">
            <img
              src={
                val.Image == "" ? "/Content/images/default-user.jpg" : val.Image
              }
              onError={e => {
                e.target.src = "/Content/images/default-user.jpg";
              }}
              alt=""
              class="img-responsive"
            />
          </a>
        </div>
        <div class="media-body">
          <h5>
            {val.Name}
          </h5>
          <div class="ratingBlk margin-bottom-1x">
            <Rating input={val.Rating} />
          </div>
          <p>
            {val.Description}
          </p>
        </div>
      </div>
    );
  });
  if (reviews.length == 0) {
    reviews.push(
      <div class="no-result-found" key="0">
        <p>No Review's Found</p>
      </div>
    );
  }

  return (
    <div class="blks reviewBlk margin-bottom-6x">
      <h3>Latest Reviews</h3>
      {reviews}
      {props.model.RatingModel &&
        <div>
          <div class="media-body ratingUs margin-top-8x">
            <div class="ratingWrap">
              <label class="lbl">Rating Us</label>
              <div class="ratingBlk margin-bottom-1x">
                <Rating
                  input={props.model.RatingModel.Rating}
                  onclick={props.onRatingClick}
                />
              </div>
            </div>
          </div>

          <div class="__reviewField margin-top-4x">
            <div class="form-group">
              <textarea
                class="form-control"
                placeholder="Enter Review Here"
                onChange={props.onChange}
                value={props.model.RatingModel.Description}
                name="Description"
              />
            </div>
            <div class="form-group">
              <button class="postReviewBtn" onClick={props.addRating}>
                <i class="fa fa-paper-plane" />
              </button>
            </div>
          </div>
        </div>}
    </div>
  );
};
export default reviewList;



