  /*--------------Bulk------------------- */

  validBulks() {
    const errorObjects = Object.values(bulkLinks).filter(x => !!x.error);
    if (errorObjects.length) {
      const hasParent = !!errorObjects[0].parent;
      if (hasParent) {
        const parent = errorObjects[0].parent.key,
          key = errorObjects[0].key;
        const obj = this.state[parent];
        obj[key] = undefined;
        this.setState({ [parent]: obj });
      } else {
        const key = errorObjects[0].key;
        //.map(x => x.key)[0];
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  onBulkChange(e) {
    let BulkDetail = { ...this.state.Bulk };
    switch (e.target.name) {
      case "Length":
        if (parseFloat(BulkDetail.Width) > 0 && e.target.value != "") {
          BulkDetail.TotalSpace = parseFloat(
            parseFloat(BulkDetail.Width) * parseFloat(e.target.value)
          ).toFixed(2);
          BulkDetail.Length = e.target.value;
          BulkDetail.TotalSpaceType = BulkDetail.WidthType;
        } else {
          BulkDetail.Length = e.target.value;
        }
        break;
      case "LengthType":
        BulkDetail.LengthType = e.target.value;
        break;
      case "Width":
        if (parseFloat(BulkDetail.Length) > 0 && e.target.value != "") {
          BulkDetail.TotalSpace = parseFloat(
            parseFloat(BulkDetail.Length) * parseFloat(e.target.value)
          ).toFixed(2);
          BulkDetail.Width = e.target.value;
          BulkDetail.TotalSpaceType = BulkDetail.LengthType;
        } else {
          BulkDetail.Width = e.target.value;
        }
        break;
      case "WidthType":
        BulkDetail.WidthType = e.target.value;

        break;
      case "WeightPerItem":
        if (parseFloat(BulkDetail.TotalItem) > 0 && e.target.value != "") {
          BulkDetail.TotalWeight = parseFloat(
            parseFloat(BulkDetail.TotalItem) * parseFloat(e.target.value)
          ).toFixed(2);

          BulkDetail.WeightPerItem = e.target.value;
        } else {
          BulkDetail.WeightPerItem = e.target.value;
          BulkDetail.TotalWeight = "";
        }
        break;
      case "WeightType":
        BulkDetail.WeightType = e.target.value;
        break;
      case "TotalItems":
        if (parseFloat(BulkDetail.WeightPerItem) > 0 && e.target.value != "") {
          BulkDetail.TotalWeight = parseFloat(
            parseFloat(BulkDetail.WeightPerItem) * parseFloat(e.target.value)
          ).toFixed(2);
          BulkDetail.TotalItem = e.target.value;
        } else {
          BulkDetail.TotalItem = e.target.value;
          BulkDetail.TotalWeight = "";
        }
        break;
      case "TotalSpace":
        let widthBulk =
          BulkDetail.Width == "" ? 0 : parseFloat(BulkDetail.Width);
        let lengthBulk =
          BulkDetail.Length == "" ? 0 : parseFloat(BulkDetail.Length);
        BulkDetail.TotalSpace = e.target.value;
        if (widthBulk == 0 && lengthBulk == 0) {
          let num = Math.sqrt(parseFloat(e.target.value));
          BulkDetail.Width = parseFloat(num).toFixed(2);
          BulkDetail.Length = parseFloat(num).toFixed(2);
        } else if (widthBulk == 0 && lengthBulk > 0) {
          BulkDetail.Width = parseFloat(
            parseFloat(e.target.value) / parseFloat(BulkDetail.Length)
          ).toFixed(2);
        } else if (widthBulk > 0 && lengthBulk == 0) {
          BulkDetail.Length = parseFloat(
            parseFloat(e.target.value) / parseFloat(BulkDetail.Width)
          ).toFixed(2);
        } else {
          BulkDetail.Width = parseFloat(
            parseFloat(e.target.value) / parseFloat(BulkDetail.Length)
          ).toFixed(2);
        }
        break;
      case "TotalWeight":
        BulkDetail.TotalWeight = e.target.value;
        if (parseFloat(BulkDetail.TotalItem) > 0 && e.target.value != "") {
          BulkDetail.WeightPerItem =
            parseFloat(e.target.value) / parseFloat(BulkDetail.TotalItem);
        } else if (
          parseFloat(BulkDetail.WeightPerItem) > 0 &&
          e.target.value != ""
        ) {
          BulkDetail.TotalItem = parseFloat(
            parseFloat(e.target.value) / parseFloat(BulkDetail.WeightPerItem)
          );
        }
        break;
    }
    this.setState({ Bulk: { ...BulkDetail } });
  }
