  AddBulks() {
    if (this.state.Bulk.FromDate == "") {
      this.setState({
        ShowAlertModal: true,
        ModalBody: "Please select availability start date",

        Pallet: { ...this.state.Bulk, FromDate: "" }
      });
      return false;
    }
    if (this.state.Bulk.ToDate == "") {
      this.setState({
        ShowAlertModal: true,
        ModalBody: "Please select availability end  date",

        Pallet: { ...this.state.Bulk, ToDate: "" }
      });
      return false;
    }
    let fromDate = new Date(this.state.Bulk.FromDate);
    let toDate = new Date(this.state.Bulk.ToDate);
    if (toDate < fromDate) {
      this.setState({
        ShowAlertModal: true,
        ModalBody: "start date need to be earlier than end date",

        Bulk: { ...this.state.Bulk, FromDate: "", ToDate: "" }
      });
      return false;
    }
    if (this.validBulks()) {
      let BulkQuote = { ...this.state };
      BulkQuote.Bulks.push(BulkQuote.Bulk);
      BulkQuote.Bulks.map((x, i) => {
        x = this.ConvertBulkUnitsToStandardUnits(x);
      });

      QuoteService.AddQuote(BulkQuote).then(response => {
        if (response.data.Message == "Success")
          this.props.history.push(
            "/user/quotes/" + response.data.Id + this.props.location.search
          );
        if (response.data.Message == "Updated") {
          const WarehouseDetail = this.updateTotalBulks(
            response.data.bulks,
            this.state
          );

          this.setState({
            ...WarehouseDetail,
            Bulk: { ...bulkState }
          });
        }
      });
    }
  }
  UpdateBulk(bulkId) {
    let fromDate = new Date(this.state.Bulk.FromDate);
    let toDate = new Date(this.state.Bulk.ToDate);
    if (toDate < fromDate) {
      this.setState({
        ShowAlertModal: true,
        ModalBody: "start date need to be earlier than end date",

        Bulk: { ...this.state.Bulk, FromDate: "", ToDate: "" }
      });
      return false;
    }
    if (this.validBulks()) {
      let BulkQuote = { ...this.state };
      var prevBulkState = BulkQuote.Bulks.find(x => x._id == bulkId);
      // BulkQuote.TotalSpace -= prevBulkState.TotalSpace;
      //BulkQuote.Bulk = this.ConvertBulkUnitsToStandardUnits(BulkQuote.Bulk);
      let index = BulkQuote.Bulks.indexOf(prevBulkState);
      BulkQuote.Bulks[index] = BulkQuote.Bulk;
      BulkQuote.Bulks.map((x, i) => {
        x = this.ConvertBulkUnitsToStandardUnits(x);
      });
      QuoteService.updateQuote(BulkQuote).then(response => {
        if (response.data.status) {
          const WarehouseDetail = this.updateTotalBulks(
            response.data.bulks,
            this.state
          );
          this.setState({
            ...WarehouseDetail,
            Bulk: {
              ...bulkState,
              LengthType: this.state.WarehouseDetail.Bulk.MaxLengthType.toLowerCase(),
              WidthType: this.state.WarehouseDetail.Bulk.MaxWidthType.toLowerCase(),
              HeightType: this.state.WarehouseDetail.Bulk.MaxHeightType.toLowerCase(),
              WeightType: this.state.WarehouseDetail.Bulk.MaxWeightType.toLowerCase()
            }
          });
        }
      });
    }
  }
  DeleteBulk(index) {
    let BulkQuote = { ...this.state };
    BulkQuote.Bulks.splice(index, 1);
    BulkQuote.Bulks.map((x, i) => {
      x = this.ConvertBulkUnitsToStandardUnits(x);
    });
    QuoteService.removeQuote(BulkQuote).then(response => {
      if (response.data.status) {
        const WarehouseDetail = this.updateTotalBulks(
          response.data.bulks,
          this.state
        );
        this.setState({ ...WarehouseDetail });
      }
    });
  }
