  RequestQuotes() {
    let Quote = { ...this.state };
    if (this.state.Action == "edit") Quote.Status = 5;
    else Quote.Status = 1;

    let capacityRequested = 0;
    if (this.state.IsBulk) {
      Quote.Bulks.map((x, i) => {
        capacityRequested = capacityRequested + parseFloat(x.TotalSpace);
      });
    } else {
      Quote.Pallets.map((x, i) => {
        capacityRequested = capacityRequested + parseFloat(x.TotalSpace);
      });
    }
    Quote.Bulks.map((x, i) => {
      x = this.ConvertBulkUnitsToStandardUnits(x);
    });
    Quote.Pallets.map((x, i) => {
      x = this.ConvertPalletUnitsToStandardUnits(x);
    });
    Quote.TotalCapacity = capacityRequested;
    QuoteService.GetQuotes(Quote).then(response => {
      if (response.data.status) {
        this.setState({
          ShowSuccessModal: true,
          ModalBody: response.data.Message
        });
      } else {
        this.setState({
          ShowAlertModal: true,
          ModalBody: response.data.Message
        });
      }
    });
  }
