import { http } from "Helpers/axios.helper.js";
import axios from "axios";
import WarehouseService from "./warehouse.service";
import UserService from "./user.service";
import Helper from "Helpers/utility.helper.js";
import mangoPay from "mangopay-cardregistration-js-kit";

const env = "P";
export default class MangoService {
  constructor() { }
  static CheckValidityandSpace(data) {
    return http.post("/api/warehouse/spaceavailability", data);
  }
  static SaveTransactionDetail(userId, paymentDetail) {
    let transactionDetail = {
      PayerId: userId,
      Price: parseFloat(paymentDetail.Amount),
      PayeeId: paymentDetail.PayeeId,
      Type: "Quote",
      Status: 0,
      TypeId: paymentDetail.QuoteId
    };
    return http.post("/api/mango/savetransaction", transactionDetail);
  }
  static RegisterCard(mangoUserId) {
    let data = {
      MangoUserId: mangoUserId
    };
    return http.post("/api/mango/cardpreregistration", data);
  }

  static initMangoCardRegistration(cardDetail) {
    try {
      mangoPay.cardRegistration.baseURL = env == "D" ? "https://api.sandbox.mangopay.com/" : "https://api.mangopay.com";
      mangoPay.cardRegistration.clientId = env == "D" ? "igniva44" : "logistcompare1";
      mangoPay.cardRegistration.init({
        cardRegistrationURL: cardDetail.CardRegistrationURL,
        preregistrationData: cardDetail.PreregistrationData,
        accessKey: cardDetail.AccessKey,
        Id: cardDetail.Id
      });
    } catch (ex) {

    }
  }

  static updateCardDetails(user, paymentDetail, cardRegistrationData) {

    //This data needs to be securely passed by the user
    this.initMangoCardRegistration(cardRegistrationData);

    const cardDetail = paymentDetail.cardDetail;
    var cardData = {
      cardNumber: cardDetail.number,
      cardExpirationDate: cardDetail.exp_month + cardDetail.exp_year,
      cardCvx: cardDetail.cvc,
      cardType: cardRegistrationData.CardType
    };
    var headers = {
      "Content-Type": "application/x-www-form-urlencoded"
    };
    // Configure the request
    var options = {
      url: cardRegistrationData.CardRegistrationURL,
      headers: headers,
      form: {
        data: cardRegistrationData.PreregistrationData,
        accessKeyRef: cardRegistrationData.AccessKey,
        cardNumber: cardData.cardNumber,
        cardExpirationDate: cardData.cardExpirationDate,
        cardCvx: cardData.cardCvx
      }
    };

    return new Promise(function (success, error) {
      mangoPay.cardRegistration.registerCard(
        cardData,
        function (res) {

          success(res); // console.log(res); // Success, you can use res.CardId now that points to registered card
        },
        function (err) {

          error(err); //console.log(err);// Handle error, see res.ResultCode and res.ResultMessage
        }
      );
    });
  }
  static CreateNaturalUser(userDetail, paymentDetail) {
    let data = {
      Email: userDetail.Email,
      FirstName: userDetail.FirstName,
      LastName: userDetail.LastName,
      BirthDate: paymentDetail.BirthDate
    };
    return http.post("/api/mango/createnaturaluser", data);
  }
  static CreatePreAuth(userId, cardId, paymentDetail) {
    let transactionDetail = {
      PayerId: userId,
      Price: parseFloat(paymentDetail.Amount),
      PayeeId: paymentDetail.PayeeId,
      Type: "Quote",
      Status: 0,
      TypeId: paymentDetail.QuoteId,
      CardId: cardId
    };
    return http.post("/api/mango/createprepayment", transactionDetail);
  }
}



