import React from "react";
import VInput from "Components/common/VInput";
import { TextArea } from "valuelink/tags";
const ContactUs = props => {
  const ALertModal = {
    ModalCss: props.ShowAlertModal ? "in" : "",
    ModalStyle: { display: props.ShowAlertModal ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };
  const SuccessModal = {
    ModalCss: props.SuccessModal ? "in" : "",
    ModalStyle: { display: props.SuccessModal ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };
  return (
    <div>
      <div id="MainWarpper" class="bg-light aboutus">
        <div class="container">
          <div class="aboutheader">
            <h2>Contact Us</h2>
          </div>

          <div class="aboutus_wrapper">
            <div class="col-md-6">
              <div class="contact_info">
                <ul>
                  <li>
                    <span>
                      <img
                        src="/Content/images/phone.png"
                        alt="logist-compare"
                        class="img-responsive center-block"
                      />
                    </span>
                    <h3>Phone</h3>
                    <a href="tel:(+44) 07956 664569">(+44) 07956 664569</a>
                  </li>
                  <li>
                    <span>
                      <i class="fa fa-envelope-o" aria-hidden="true" />
                    </span>
                    <h3>Email</h3>
                    <a href="mailto:Serafina@LogistCompare.com">
                      feedback@logistcompare.com
                    </a>
                  </li>
                </ul>
              </div>
            </div>
            <div class="col-md-6">
              <div class="contactForm">
                <div class="formWarp">
                  <div class="form-group">
                    <VInput
                      class="form-control"
                      placeholder="Name"
                      type="text"
                      maxLength="100"
                      valueLink={props.links.nameLink}
                    />
                  </div>
                  <div class="form-group">
                    <VInput
                      class="form-control"
                      placeholder="Email"
                      type="text"
                      maxLength="100"
                      valueLink={props.links.emailLink}
                    />
                  </div>
                  <div class="form-group">
                    <VInput
                      class="form-control"
                      placeholder="Subject"
                      type="text"
                      maxLength="100"
                      valueLink={props.links.subjectLink}
                    />
                  </div>
                  <div class="form-group">
                    <TextArea
                      class="form-control"
                      rows="5"
                      id="comment"
                      placeholder="Message"
                      valueLink={props.links.messageLink}
                    />
                    {props.links.messageLink.validationError && (
                      <p class="text-danger">
                        {" "}
                        {props.links.messageLink.validationError}
                      </p>
                    )}
                  </div>
                  <div class="form-group">
                    <input
                      class="button button-border button-action button-uppercase button-min-width margin-top-3x margin-right-2x"
                      value="Send"
                      onClick={props.sendMessage}
                      type="button"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + ALertModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={ALertModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={ALertModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Alert !</h2>
                <p>{ALertModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Ok"
                    onClick={ALertModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + SuccessModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={SuccessModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={SuccessModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Success</h2>
                <p>{SuccessModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <a
                    href="/"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                  >
                    Ok
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = ContactUs;



