import {http} from 'Helpers/axios.helper.js';
const addUrl = `/api/search/save`;
const updateUrl = `/api/search/update`;
const getUrl = '/api/search/get/';
const allUrl = `/api/search/all/`;
const findUrl = `/api/search/find/`;

export default class searchService {
    static Save(data) {
        if (!data._id)
            return http.post(addUrl, data);
        else
            return http.post(updateUrl, data);
    }
    static GetSearch() {

        return http.get(allUrl);
    }

    static Find(data){
        return http.get(findUrl);
    }

    static Get(id) {
        return http.get(getUrl + id)
    }

    static delete(id) {
        return http.get(deleteUrl + id)
    }

}   




