import React from "react";
import Flatpickr from "react-flatpickr";
import VInput from "Components/common/VInput";
import { Select } from "valuelink/tags";
import Link from "valuelink";
import Type from "./type.js";
import Security from "./security.js";
import StorageType from "./storageType.js";
import Pallet from "./pallet.js";
import Bulk from "./bulk.js";
import Services from "./services.js";

const detail = props => {
  let storage = [];
  if (props.model.StorageType == "Pallet")
    storage.push(<Pallet {...props} key="1" />);
  else if (props.model.StorageType == "Bulk")
    storage.push(<Bulk {...props} key="1" />);
  else if (props.model.StorageType == "Bulk_Pallet") {
    storage.push(<Pallet {...props} key="1" />);
    storage.push(<Bulk {...props} key="2" />);
  }
  let AddressHtml = "";

  if (props.model.Addresses.length > 0) {
    let adClass =
      props.model.Addresses.length > 1 ? "form-group adres-box" : "form-group";
    AddressHtml = props.model.Addresses.map((item, key) => {
      return (
        <div class="row" key={key}>
          <div class="col-xs-12 col-sm-6">
            <div class="row">
              <div class="Row-6 col-xs-6 col-sm-6">
                <div class="form-group">
                  <label class="lbl">Postcode</label>
                  <input
                    type="text"
                    className="form-control"
                    name={"PostCode" + key}
                    placeholder="PostCode*"
                    value={item.PostCode}
                    onChange={props.onAddressChange}
                    onBlur={() => props.addLatLng(key)}
                  />
                </div>
              </div>
              <div class="col-xs-6 col-sm-6">
                <div class="form-group">
                  <label class="lbl">City</label>

                  <input
                    type="text"
                    className="form-control"
                    name={"City" + key}
                    placeholder="City*"
                    onChange={props.onAddressChange}
                    value={item.City}
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="col-xs-12 col-sm-6">
            <div class={adClass}>
              <label class="lbl">Address</label>
              <input
                type="text"
                className="form-control"
                name={"Address" + key}
                placeholder="Address*"
                onChange={props.onAddressChange}
                value={item.Address}
              />
              {props.model.Addresses.length > 1 ? (
                <a
                  href="javascript:void(0);"
                  class="trashLink margin-left-1x"
                  onClick={() => props.RemoveAddress(key)}
                >
                  <i class="icon-trash" />
                </a>
              ) : (
                ""
              )}
            </div>
          </div>
        </div>
      );
    });
  }
  const timePockerOptions = {
    enableTime: true,
    noCalendar: true,
    enableSeconds: false,
    time_24hr: false,
    dateFormat: "H:i",
    defaultHour: 12,
    defaultMinute: 0
  };

  return (
    <div class="formWarp">
      <div class="row">
        <div class="col-xs-12 col-sm-6">
          <div class="form-group">
            <label class="lbl">Warehouse name</label>

            <VInput
              type="text"
              className="form-control"
              name="Name"
              placeholder="Warehouse name*"
              valueLink={props.links.nameLink}
            />
          </div>
        </div>
        <div class="col-xs-12 col-sm-6">
          <div class="form-group">
            <label class="lbl">Warehouse Capacity</label>
            <div class="row">
              <div class="col-xs-6 col-sm-8">
                <VInput
                  type="text"
                  className="form-control"
                  name="Capacity"
                  placeholder="Capacity (Sq Feet / Meters)"
                  onChange={props.change}
                  valueLink={props.links.capacityLink}
                />
              </div>
              <div class="col-xs-6 col-sm-4">
                <div class="row Row-2">
                  <select
                    class="form-control"
                    id="CapacityType"
                    name="CapacityType"
                    value={props.model.CapacityType}
                    onChange={props.change}
                  >
                    <option value="Meter">Sq Meters</option>
                    <option value="Feet">Sq Feet</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {AddressHtml}
      <div class="clearfix warehouseHeader add-more">
        <a
          href="javascript:void(0)"
          onClick={props.addAddresses}
          class="margin-top-4x"
        >
          <span class="addWarehouses">
            <i class="icon-plus ico-t2" />
          </span>&nbsp; Add More Address
        </a>
      </div>
      <div class="row">
        <div class="col-xs-12 col-sm-6">
          <div class="form-group">
            <label class="lbl">Website</label>
            <VInput
              type="text"
              className="form-control"
              name="Website"
              placeholder="Enter URL"
              valueLink={props.links.websiteLink}
            />
          </div>
        </div>
        <div class="col-xs-12 col-sm-6">
          <div class="form-group">
            <label class="lbl">Opening hours</label>
            <div class="row">
              <div class="col-xs-6">
                <Flatpickr
                  className="form-control"
                  id="Starthrs"
                  name="Starthrs"
                  value={props.model.Starthrs}
                  placeholder="Start Hrs"
                  onChange={(_, str) => props.onflatpicker("Starthrs", str)}
                  options={timePockerOptions}
                />
              </div>
              <div class="col-xs-6">
                <Flatpickr
                  className="form-control"
                  id="Endhrs"
                  name="Endhrs"
                  placeholder="End Hrs"
                  value={props.model.Endhrs}
                  onChange={(_, str) => props.onflatpicker("Endhrs", str)}
                  options={timePockerOptions}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12 form-group about-me">
          <label>About Warehouse</label>
          <textarea
            class="form-control"
            rows="8"
            id="About"
            name="About"
            placeholder="Message"
            value={props.model.About}
            onChange={props.change}
          />
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12 col-sm-6">
          {/*warehouse tye*/}
          <Type {...props} />
          {/*Security*/}
          <Security {...props} />
        </div>
        {/*services*/}
        <Services {...props} />
      </div>
      {/*load storageType*/}
      <StorageType {...props} />
      {storage}
    </div>
  );
};
module.exports = detail;



