import React from "react";
import GoogleMap from "Components/map/googlemap.js";
import { Marker } from "react-google-maps";
const mapModal = props => {
  return (
    <div
      class={
        props.model.isShowMap
          ? "modal fade modalWarpper wDetailModal in modal-open"
          : "modal fade modalWarpper wDetailModal"
      }
      id="MapDetailModal"
      style={{ display: props.model.isShowMap ? "block" : "none" }}
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              onClick={props.closeMap}
            >
              <i class="icon-close ico-t2" />
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="__mapBlk qoutesMap">
                <div id="map">
                  <GoogleMap center={props.model.center}>
                    {<Marker position={props.model.center} />}
                  </GoogleMap>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default mapModal;



