import React, { Component } from "react";
import Notification from "./notification.js";
import Service from "Services/notification.service.js";
import Loader from "Components/common/loader.js";
import Helper from "Helpers/utility.helper.js";
export default class notificationContainer extends Component {
  constructor() {
    super();
    this.state = {
      Result: [],
      isLoading: true,
      showLoadmore: true,
      IsProvider:
        Helper.DecodeToken(localStorage.getItem("userinfo")).Type == "Owner"
          ? true
          : false
    };
    this.GetNotifications = this.GetNotifications.bind(this);
  }
  componentDidMount() {
    this.GetNotifications(0);
  }

  GetNotifications(data) {
    this.setState({ isLoading: true });
    Service.GetAll(data)
      .then(response => {
        const result = this.state.Result;
        const Result = result.concat(response.data);
        if (response.data.length < 10)
          this.setState({ Result, isLoading: false, showLoadmore: false });
        else this.setState({ Result, isLoading: false });
      })
      .catch(err => {
        console.log(err);
      });
  }
  render() {
    const model = {
      model: this.state,
      Loadmore: this.GetNotifications
    };
    if (this.state.isLoading) return <Loader />;
    else return <Notification {...model} />;
  }
}



