import React from "react";
import Addform from "./adduser.js";
import MetaTags from 'react-meta-tags';
const manageUsers = props => {
  let getUserForm = null;
  if (props.model.isNew) {
    getUserForm = <Addform {...props} />;
  }

  let users = props.model.users.map((val, key) => {
    return (
      <tr key={key}>
        <td>
          {val.FirstName + " "} {val.LastName}
        </td>
        <td>
          {val.Role}
        </td>
        <td>
          <span class="__actions">
            <a
              href="javascript:void(0);"
              class="editLink"
              onClick={() => {
                props.onEdit(val);
              }}
            >
              <i class="icon-edit-pencil" />
            </a>
            <a
              href="javascript:void(0);"
              class="trashLink margin-left-1x"
              onClick={() => {
                props.onpopup(val._id);
              }}
            >
              <i class="icon-trash" />
            </a>
          </span>
        </td>
      </tr>
    );
  });
  const ALertModal = {
    ModalCss: props.model.ShowAlertModal ? "in" : "",
    ModalStyle: { display: props.model.ShowAlertModal ? "block" : "none" },
    delete: props.delete,
    onClose: props.CloseModal
  };
  const ValidationModal = {
    ModalCss: props.model.ShowValidationModal ? "in" : "",
    ModalStyle: { display: props.model.ShowValidationModal ? "block" : "none" },
    ModalBody: props.model.ModalBody,
    onClose: props.CloseModal
  };
  if (users.length == 0)
    users = (
      <div class="no-result-found" style={{}}>
        <p>No users added yet</p>
      </div>
    );
  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="header">
        <h2>User Managment</h2>
      </div>
      <div class="mainContent boxScroll">
        <div class="sStorageWarp user_managementwrap">
          <div class="row addUser">
            {getUserForm}
            <div class="col-xs-12 col-sm-3 addBtn">
              <span class="user">
                <a
                  role="button"
                  id="music"
                  href="#"
                  onClick={
                    props.model.isNew == true ? props.addUser : props.showForm
                  }
                >
                  <i class="fa fa-plus" aria-hidden="true" />{" "}
                  {props.model.user._id ? "Update User" : "Add User"}
                </a>
              </span>
            </div>
          </div>

          <div class="row UserInfo">
            <div class="UserManage">
              <table class="blue table">
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Role</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {users}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + ALertModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={ALertModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={ALertModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2 />
                <p>Are you sure to delete user ?</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Yes"
                    onClick={ALertModal.delete}
                  />
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="No"
                    onClick={ALertModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + ValidationModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={ValidationModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={ValidationModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Alert !</h2>
                <p>
                  {ValidationModal.ModalBody}
                </p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Ok"
                    onClick={ValidationModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

module.exports = manageUsers;



