import React from "react";
import VInput from "Components/common/VInput";
import { Select, TextArea } from "valuelink/tags";
import Flatpickr from "react-flatpickr";
import Helper from "../../helpers/utility.helper.js";
const Pellet = props => {
  // let options = {
  //   minDate: Helper.ConvertDateToString(
  //     props.State.WarehouseDetail.Pallet.AvailabilityFrom
  //   ),
  //   maxDate: Helper.ConvertDateToString(
  //     props.State.WarehouseDetail.Pallet.AvailabilityTo
  //   )
  // };
  let button = (
    <input
      type="button"
      class="button button-primary margin-top-1x margin-bottom-1x"
      value="Add Category"
      onClick={props.addPallet}
    />
  );
  if (props.State.Pallet._id) {
    button = (
      <input
        type="button"
        class="button button-primary margin-top-1x margin-bottom-1x"
        value="Update Category"
        onClick={() => props.updatePallet(props.State.Pallet._id)}
      />
    );
  }
  return (
    <div class="tab-pane fade in active" id="PalletsSection">
      <div class="formWarpInner palletForm">
        <form>
          <div
            id="PalletStorage"
            class="open-form"
            style={{ display: "block" }}
          >
            <div class="row Row-10">
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Space Available</label>
                <input
                  type="text"
                  class="form-control border-green"
                  placeholder=""
                  value={
                    parseInt(props.State.TotalSpace) +
                    " sq" + props.State.WarehouseDetail.CapacityType +
                    "- (" +
                    parseInt(props.State.TotalPallets) +
                    " Pallet)"
                  }
                  readOnly="readonly"
                />
              </div>
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Time Available</label>
                <input
                  type="text"
                  class="form-control border-green"
                  placeholder=""
                  value={
                    Helper.ConvertDateToString(
                      props.State.WarehouseDetail.Pallet.AvailabilityFrom
                    ) +
                    " - " +
                    Helper.SetEndDate(
                      props.State.WarehouseDetail.Pallet.AvailabilityTo
                    )
                  }
                  readOnly="readonly"
                />
              </div>
            </div>

            <div class="row Row-10">
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Type of Pallets</label>
                <input
                  type="text"
                  class="form-control"
                  placeholder=""
                  value={props.State.WarehouseDetail.Pallet.Type.Name}
                  readOnly
                />
              </div>
              <div class="col-xs-12 col-sm-6 form-group">
                <label>
                  Number of Pallets <sub>*</sub>
                </label>
                <VInput
                  type="text"
                  class="form-control"
                  placeholder=""
                  name="TotalPallets"
                  onChange={props.palletChange}
                  onKeyPress={props.onkeyPressWithoutDot}
                  valueLink={props.pelletLinks.totalPalletsLink}
                />
              </div>
            </div>

            <div class="row Row-10">
              <div class="col-xs-12 form-group">
                <label>Load Description / Additional Information</label>
                <TextArea
                  class="form-control"
                  placeholder=""
                  valueLink={props.pelletLinks.palletDescriptionLink}
                />
              </div>
            </div>
            <div class="row Row-10">
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Weight(Per pallet) </label>
                <div class="row Row-10">
                  <div class="col-xs-6 col-sm-6 form-group">
                    <VInput
                      type="text"
                      class="form-control"
                      placeholder=""
                      name="PerPalletWeight"
                      onChange={props.palletChange}
                      valueLink={props.pelletLinks.palletWeightLink}
                      onKeyPress={props.onkeyPress}
                    />
                  </div>
                  <div class="col-xs-6 col-sm-6 form-group">
                    <div class="multiSelectBlk">
                      <select
                        class="multiSelectDrop"
                        title=""
                        value={props.State.Pallet.WeightType}
                        onChange={props.palletChange}
                        name="PalletWeightType"
                      >
                        <option>Kgs</option>
                        <option>Lbs</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Height (Per pallet)</label>
                <div class="row Row-10">
                  <div class="col-xs-6 col-sm-6 form-group">
                    <VInput
                      type="text"
                      class="form-control"
                      placeholder=""
                      valueLink={props.pelletLinks.palletHeightLink}
                      onKeyPress={props.onkeyPress}
                    />
                  </div>
                  <div class="col-xs-6 col-sm-6 form-group">
                    <div class="multiSelectBlk">
                      <select
                        class="multiSelectDrop"
                        title=""
                        value={props.State.Pallet.HeightType.toLowerCase()}
                        disabled
                      >
                        <option value="meter">Meters</option>
                        <option value="feet">feet</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-xs-12 form-group">
                <label>Industrial Category</label>
                <div class="multiSelectBlk">
                  <Select
                    class="multiSelectDrop"
                    title=""
                    valueLink={props.pelletLinks.palletCategoryLink}
                  >
                    <option value="Textile, Apparel and Luxury Goods">
                      Textile, Apparel and Luxury Goods
                    </option>
                    <option value="Household products">
                      Household products
                    </option>
                    <option value="Electronics">Electronics</option>
                    <option value="Leisure products">Leisure products</option>
                    <option value="Paper and forest products">
                      Paper and forest products
                    </option>
                    <option value="Footwear">Footwear</option>
                    <option value="Furniture">Furniture</option>
                    <option value="Plastic goods">Plastic goods</option>
                    <option value="Rubber products">Rubber products</option>
                    <option value="Toys">Toys</option>
                    <option value="Cosmetics">Cosmetics</option>
                    <option value="Metalworking products">
                      Metalworking products
                    </option>
                    <option value="Beverages">Beverages</option>
                    <option value="Marketing material">
                      Marketing material
                    </option>
                    <option value="Data archive material">
                      Data archive material
                    </option>
                  </Select>
                </div>
              </div>
              <div class="col-xs-12 form-group">
                <label>Total Pallets Weight(Lbs/Kgs)</label>
                <input
                  type="text"
                  class="form-control"
                  placeholder=""
                  name="TotalWeight"
                  value={props.State.Pallet.TotalWeight}
                  readOnly
                />
              </div>
            </div>
            <div class="row Row-10">
              <div class="col-xs-12 col-sm-6 form-group">
                <label>
                  From (Start date) <sub>*</sub>
                </label>
                <div class="has-feedback">
                  <Flatpickr
                    type="text"
                    class="form-control"
                    data-min-date="today"
                    placeholder=""
                    value={props.State.Pallet.FromDate}
                    onChange={(_, str) =>
                      props.onpalletflatpicker("FromDate", str)}
                  />
                  {/*options={ {minDate : new Date(),dateFormat : "d-m-Y"}}*/}
                  <span class="form-control-feedback">
                    <i class="fa fa-calendar" />
                  </span>
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 form-group">
                <label>
                  To (End Date) <sub>*</sub>
                </label>
                <div class="has-feedback">
                  <Flatpickr
                    type="text"
                    class="form-control"
                    placeholder=""
                    value={props.State.Pallet.ToDate}
                    data-min-date="today"
                    onChange={(_, str) =>
                      props.onpalletflatpicker("ToDate", str)}
                  />
                  {/*options={ {minDate : props.State.Pallet.FromDate, maxDate: new Date().fp_incr(14),dateFormat : "d-m-Y"}}        */}
                  <span class="form-control-feedback">
                    <i class="fa fa-calendar" />
                  </span>
                </div>
              </div>
            </div>
            <div class="row Row-10">
              <div class="col-xs-12 col-sm-6 form-group add">{button}</div>
              <div
                class="col-xs-12 col-sm-6 form-group add"
                style={{ marginTop: "5px" }}
              />
            </div>
            <div class="form-group">
              <p class="messageShow">
                <sub>*</sub> Please Fill Mandatory Feilds
              </p>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};
module.exports = Pellet;



