import React from "react";
import Flatpickr from "react-flatpickr";

const bulk = props => {
  return (
    <div class="tab-pane fade in active" id="BulkSection">
      <div class="formWarpInner">
        <form>
          <div class="row">
            <div class="col-xs-12 col-sm-6 col-md-3">
              <div class="form-group">
                <label>Sq feet/meters</label>
                <div class="row Row-2">
                  <div class="col-xs-7">
                    <input
                      type="text"
                      class="form-control"
                      placeholder=""
                      name="Bulk"
                      id="Item"
                      value={props.model.search.Bulk.Item}
                      onChange={props.change}
                      onKeyPress={props.onkeyPress}
                      onPaste={props.onPaste}
                    />
                  </div>
                  <div class="col-xs-5">
                    <div class="multiSelectBlk">
                      <select
                        class="multiSelectDrop form-control"
                        title=""
                        name="Bulk"
                        id="ItemType"
                        value={props.model.search.Bulk.ItemType}
                        onChange={props.change}
                      >
                        <option value="Meter">Sq Meters</option>
                        <option value="Feet">Sq feet</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-xs-12 col-sm-6 col-md-3">
              <div class="form-group">
                <label>Total Items</label>
                <input
                  type="text"
                  class="form-control"
                  placeholder=""
                  name="Bulk"
                  id="TotalItems"
                  value={props.model.search.Bulk.TotalItems}
                  onChange={props.change}
                  onKeyPress={props.onkeyPressWithoutDot}
                  onPaste={props.onPaste}
                />
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col-xs-12 col-sm-6 col-md-3 form-group">
              <label>Weight</label>
              <div class="row Row-2">
                <div class="col-xs-7">
                  <input
                    type="text"
                    class="form-control"
                    placeholder=""
                    name="MaxWeight"
                    id="MaxWeight"
                    value={props.model.search.MaxWeight}
                    onChange={props.change}
                    onKeyPress={props.onkeyPress}
                    onPaste={props.onPaste}
                  />
                </div>
                <div class="col-xs-5">
                  <div class="multiSelectBlk">
                    <select
                      class="multiSelectDrop form-control"
                      title=""
                      name="MaxWeightType"
                      id="MaxWeightType"
                      value={props.model.search.MaxWeightType}
                      onChange={props.change}
                    >
                      <option val="Kgs">Kgs</option>
                      <option value="Lbs">Lbs</option>
                    </select>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-xs-12 col-sm-6 col-md-3 form-group">
              <label>Length</label>
              <div class="row Row-2">
                <div class="col-xs-7">
                  <input
                    type="text"
                    class="form-control"
                    placeholder=""
                    name="Bulk"
                    id="MaxLength"
                    value={props.model.search.Bulk.MaxLength}
                    onChange={props.change}
                    onKeyPress={props.onkeyPress}
                    onPaste={props.onPaste}
                  />
                </div>
                <div class="col-xs-5">
                  <div class="multiSelectBlk">
                    <select
                      class="multiSelectDrop form-control"
                      title=""
                      name="Bulk"
                      id="MaxLengthType"
                      value={props.model.search.Bulk.MaxLengthType}
                      onChange={props.change}
                    >
                      <option value="Meter">Meters</option>
                      <option value="Feet">Feet</option>
                    </select>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-xs-12 col-sm-6 col-md-3 form-group">
              <label>Width</label>
              <div class="row Row-2">
                <div class="col-xs-7">
                  <input
                    type="text"
                    class="form-control"
                    placeholder=""
                    name="Bulk"
                    id="MaxWidth"
                    value={props.model.search.Bulk.MaxWidth}
                    onChange={props.change}
                    onKeyPress={props.onkeyPress}
                    onPaste={props.onPaste}
                  />
                </div>
                <div class="col-xs-5">
                  <div class="multiSelectBlk">
                    <select
                      class="multiSelectDrop form-control"
                      title=""
                      name="Bulk"
                      id="MaxWidthType"
                      value={props.model.search.MaxWidthType}
                      onChange={props.change}
                    >
                      <option value="Meter">Meters</option>
                      <option value="Feet">Feet</option>
                    </select>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-xs-12 col-sm-6 col-md-3 form-group">
              <label>Height</label>
              <div class="row Row-2">
                <div class="col-xs-7">
                  <input
                    type="text"
                    class="form-control"
                    placeholder=""
                    name="MaxHeight"
                    id="MaxHeight"
                    value={props.model.search.MaxHeight}
                    onChange={props.change}
                    onKeyPress={props.onkeyPress}
                    onPaste={props.onPaste}
                  />
                </div>
                <div class="col-xs-5">
                  <div class="multiSelectBlk">
                    <select
                      class="multiSelectDrop form-control"
                      title=""
                      name="MaxHeightType"
                      id="MaxHeightType"
                      value={props.model.search.MaxHeightType}
                      onChange={props.change}
                    >
                      <option value="Meter">Meters</option>
                      <option value="Feet">Feet</option>
                    </select>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col-xs-12 col-sm-6 col-md-3 form-group">
              <label>Estimated Storage Period</label>
              <div class="has-feedback">
                <Flatpickr
                  className="form-control"
                  id="AvailabilityFrom"
                  name="AvailabilityFrom"
                  value={props.model.search.AvailabilityFrom}
                  placeholder="From"
                  data-min-date="today"
                  onChange={(_, str) =>
                    props.onflatpicker("AvailabilityFrom", str)}
                />
              </div>
            </div>
            <div class="col-xs-12 col-sm-6 col-md-3 form-group">
              <label>&nbsp;</label>
              <div class="has-feedback">
                <Flatpickr
                  className="form-control"
                  id="AvailabilityTo"
                  name="AvailabilityTo"
                  value={props.model.search.AvailabilityTo}
                  placeholder="To"
                  data-min-date="today"
                  onChange={(_, str) =>
                    props.onflatpicker("AvailabilityTo", str)}
                />
              </div>
            </div>
          </div>
          <div class="form-group">
            <input
              type="button"
              onClick=""
              class="button button-primary"
              value="Search"
              onClick={props.next}
            />
          </div>
        </form>
      </div>
    </div>
  );
};
module.exports = bulk;



