import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Address from "./changepassword.js";
import UserService from "Services/user.service.js";
import Helper from "Helpers/utility.helper.js";
import Link from "valuelink";
const initialState = {
  OldPassword: "",
  NewPassword: "",
  ConfirmPassword: "",
  ShowSuccessModal: false,
  ShowAlertModal: false,
  Message: ""
};
let links = {};
export default class ChangePasswordContainer extends Component {
  constructor() {
    super();
    this.state = initialState;
  }
  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }
  CloseModal() {
    this.setState({
      ShowAlertModal: false,
      Message: "",
      ShowSuccessModal: false
    });
    this.reset();
  }
  reset() {
    this.setState(initialState);
  }
  CloseModal() {
    this.setState({
      ShowAlertModal: false,
      ShowSuccessModal: false,
      Message: ""
    });
    this.resetForm();
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  updatePassword() {
    if (this.valid()) {
      if (this.state.OldPassword == this.state.NewPassword) {
        this.setState({
          ShowAlertModal: true,
          Message: "Old Password and new password cannot be same"
        });
        return;
      }
      UserService.UpdatePassword(this.state).then(response => {
        if (response.data.status) {
          this.setState({
            ...initialState,
            ShowSuccessModal: true,
            Message: response.data.Message
          });
        } else {
          this.setState({
            ShowAlertModal: true,
            Message: response.data.Message
          });
        }
      });
    }
  }
  render() {
    links = {
      oldPasswordLink: Link.state(this, "OldPassword").check(
        x => x,
        "Old Password is required"
      ),
      passwordLink: Link.state(this, "NewPassword")
        .check(x => x, "New Password is required")
        .check(
          x => x.length >= 6,
          "Passwords must contain at least six characters"
        ),
      confirmPasswordLink: Link.state(this, "ConfirmPassword").check(
        x => x == this.state.NewPassword,
        "password and confirmation password do not match."
      )
    };
    const passwordProps = {
      handleChange: this.handleChange.bind(this),
      handleSubmit: this.updatePassword.bind(this),
      ShowSuccessModal: this.state.ShowSuccessModal,
      ShowAlertModal: this.state.ShowAlertModal,
      Message: this.state.Message,
      onClose: this.CloseModal.bind(this),
      links
    };
    return <Address {...passwordProps} />;
  }
}



