import React from "react";
import MetaTags from 'react-meta-tags';
const space = props => {
  const list = props.model.ReportList.map((val, key) => {
    const date = new Date(val.CreatedDate);
    const showdate =
      date.getMonth() + "/" + date.getDate() + "/" + date.getFullYear();
    const showTime = date.getHours() + ":" + date.getMinutes();
    return (
      <tr key={key}>
        <td data-title="Code">
          {key + 1}
        </td>
        <td data-title="Company">
          {val.Description}
        </td>
        <td data-title="Price" class="numeric">
          {showdate}
        </td>
        <td data-title="Change" class="numeric">
          {showTime}
        </td>
      </tr>
    );
  });
  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="header">
        <h2>Reports</h2>
      </div>
      <div class="mainContent">
        <div class="innerPadding padding-top-6x padding-bottom-6x">
          <div class="reportWarpping">
            <div class="tab" role="tabpanel">
              <div class="col-xs-6 col-sm-3 col-md-3">
                <select
                  class="form-control"
                  id="Duration"
                  name="Duration"
                  value={props.model.Duration}
                  onChange={props.onchange}
                >
                  <option disabled>Select Value</option>
                  <option value="Month">Past Month</option>
                  <option value="Week">Past Week</option>
                  <option value="Day">Past Day</option>
                  <option value="Hour">Past Hrs</option>
                </select>
              </div>
              <div class="col-xs-6 col-sm-3 col-md-3">
                <select
                  class="form-control"
                  id="Type"
                  name="Type"
                  value={props.model.Type}
                  onChange={props.onchange}
                >
                  <option disabled>Select Value</option>
                  <option value="Space">Space Occupied</option>
                  <option value="Revenue">Revenue Generated</option>
                  <option value="Rating">Rating</option>
                </select>
              </div>

              <div class="col-md-6">
                <div class="setting-report">
                  {/* <a href="javascript:void(0);">
                    <i class="fa fa-cog" aria-hidden="true" />
                  </a> */}
                </div>
              </div>
              <div class="tab-content tabs">
                <div
                  role="tabpanel"
                  class="tab-pane fade in active"
                  id="Section1"
                >
                  <div class="reportWarp">
                    <div class="form-group">
                      <div class="__lineChart margin-bottom-4x">
                        <canvas id="chart-legend-top" />
                      </div>
                    </div>
                    <div class="form-group">
                      <textarea
                        class="form-control"
                        name="Description"
                        value={props.model.Description}
                        onChange={props.onchange}
                      />
                    </div>
                    <div class="form-group">
                      <input
                        type="button"
                        value="Add Report"
                        class="button button-primary button-min-width margin-top-2x"
                        onClick={props.SaveReport}
                      />
                    </div>
                  </div>
                </div>
              </div>

              <div class="row">
                <div class="col-xs-12">
                  <div id="no-more-tables">
                    <table class="col-md-12 table-bordered table-striped table-condensed cf">
                      <thead class="cf">
                        <tr>
                          <th>Sr No</th>
                          <th>Description</th>
                          <th class="numeric">Date</th>
                          <th class="numeric">Time</th>
                        </tr>
                      </thead>
                      {list}
                      <tbody />
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default space;



