import { http } from "Helpers/axios.helper.js";
//const tt = localStorage.getItem("userinfo");
export default class UserService {
  static login(user) {
    const url = "/api/user/login";
    return http.post(url, user);
  }
  static register(user) {
    const url = `/api/user/register`;
    return http.post(url, user);
  }

  static addUser(user) {
    let url = `/api/user/adduser`;
    if (user._id) {
      url = `/api/user/updateuser`;
    }

    return http.post(url, user);
  }

  static getAll() {
    const url = `/api/user/getall`;
    return http.get(url);
  }

  static delete(id) {
    const url = `/api/user/delete/` + id;
    return http.get(url);
  }

  static resendlink(email) {
    const url = "/api/user/resendverification?email=" + email;
    return http.get(url);
  }
  static SendforgotPassword(email) {
    const url = "/api/user/sendforgotpassword?email=" + email;
    return http.get(url);
  }
  static AddSubscription() {
    const url = "/api/user/addsubscription";
    return http.get(url);
  }
  static AddBankDetails(bankdetail) {
    const url = "/api/mango/createlegaluser"; //"/api/user/addbankdetails";
    return http.post(url, bankdetail);
  }
  static UpdateProfile(user) {
    const url = "/api/user/update";
    return http.post(url, user);
  }
  static UploadImage(data) {
    const url = "/api/user/uploadimage";
    return http.post(url, data);
  }
  static UpdatePassword(data) {
    const url = "/api/user/updatepassword";
    return http.post(url, data);
  }
  static GetSubscription() {
    return http.get("/api/user/getsubscription");
  }
  static resetPassword(data) {
    const url = "/api/user/resetpassword";
    return http.post(url, data);
  }

  static GetVideos() {
    return http.get("/user/getvideo");
  }
  static GetUserMangoDetail(userId) {
    return http.get("/api/user/getuserdetail/" + userId)
  }
  static UpdateMangoDetails(userDetail, mangoUserId, cardId) {
    let data = {
      Email: userDetail.Email,
      MangoUserId: mangoUserId,
      CardId: cardId
    }
    return http.post("/api/user/updatemangodetail", data);
  }
}



