
import React from "react";
import VInput from "Components/common/VInput";
import { TextArea } from "valuelink/tags";

const CallModal = (props) => {
    return (

        <div
            class={
                props.model.isShowDetail
                    ? "modal fade modalWarpper wDetailModal in"
                    : "modal fade modalWarpper wDetailModal"
            }
            id="warehouseDetail"
            style={{ display: props.model.isShowDetail ? "block" : "none" }}
        >
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4>
                        </h4>
                        <button
                            type="button"
                            class="close"
                            data-dismiss="modal"
                            onClick={props.closeDetail}
                        >
                            <i class="icon-close ico-t2" />
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="aboutus_wrapper">
                            <div class="col-md-6">
                                <div class="contact_info">
                                    <ul>
                                        <li>
                                            <span>
                                                <img
                                                    src="/Content/images/phone.png"
                                                    alt="logist-compare"
                                                    class="img-responsive center-block"
                                                />
                                            </span>
                                            <h3>Phone</h3>
                                            <a href="tel:(+44) 07956 664569">(+44) 07956 664569</a>
                                        </li>
                                        <li>
                                            <span>
                                                <i class="fa fa-envelope-o" aria-hidden="true" />
                                            </span>
                                            <h3>Email</h3>
                                            <a href="mailto:Serafina@LogistCompare.com">
                                                feedback@logistcompare.com
                    </a>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            <div class="col-md-6">
                                <div class="contactForm">
                                    <div class="formWarp">
                                        <div class="form-group">
                                            <VInput
                                                class="form-control"
                                                placeholder="Name"
                                                type="text"
                                                maxLength="100"
                                                valueLink={props.links.nameLink}
                                            />
                                        </div>
                                        <div class="form-group">
                                            <VInput
                                                class="form-control"
                                                placeholder="Email"
                                                type="text"
                                                maxLength="100"
                                                valueLink={props.links.emailLink}
                                            />
                                        </div>
                                        <div class="form-group">
                                            <VInput
                                                class="form-control"
                                                placeholder="Subject"
                                                type="text"
                                                maxLength="100"
                                                valueLink={props.links.subjectLink}
                                            />
                                        </div>
                                        <div class="form-group">
                                            <TextArea
                                                class="form-control"
                                                rows="5"
                                                id="comment"
                                                placeholder="Message"
                                                valueLink={props.links.messageLink}
                                            />
                                            {props.links.messageLink.validationError && (
                                                <p class="text-danger">
                                                    {" "}
                                                    {props.links.messageLink.validationError}
                                                </p>
                                            )}
                                        </div>
                                        <div class="form-group">
                                            <input
                                                class="button button-border button-action button-uppercase button-min-width margin-top-3x margin-right-2x"
                                                value="Send"
                                                onClick={props.sendMessage}
                                                type="button"
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

module.exports = CallModal;


