import React from "react";
import Helper from "../../../helpers/utility.helper.js";
const BulkItem = props => {
  return (
    <div class="bCateBlk">
      <div class="blk_heading">
        <h4>{props.Category}</h4>
      </div>
      <ul>
        <li>
          <label>Quantity</label>
          <p>{props.TotalItem}</p>
        </li>
        <li>
          <label>Length (Meters/feet)</label>
          <p>{props.Length + " " + props.LengthType}</p>
        </li>
        <li>
          <label>Width (Meters/feet)</label>
          <p>{props.Width + " " + props.WidthType}</p>
        </li>
        {props.TotalWeight == null || props.TotalWeight == "" ? (
          ""
        ) : (
          <li>
            <label>Weight ( LBs/Kgs)</label>
            <p>{props.TotalWeight + " " + props.WeightType}</p>
          </li>
        )}
        {props.Height == null || props.Height == "" ? (
          ""
        ) : (
          <li>
            <label>Height (Meters/feet)</label>
            <p>{props.Height + " " + props.HeightType}</p>
          </li>
        )}
        <li>
          <label>Start Date</label>
          <p>{Helper.ConvertDateToString(props.FromDate)}</p>
        </li>
        <li>
          <label>End Date</label>
          <p>{Helper.ConvertDateToString(props.ToDate)}</p>
        </li>
        <div class="bottm_view margin-top-4x">
          <a
            href="javascript:void(0)"
            onClick={() => props.EditBulk(props.index)}
            class={props._id == props.bid ? "tab active" : "tab"}
          >
            <span class="view_detial">View Detail</span>
          </a>
        </div>
      </ul>
    </div>
  );
};
module.exports = BulkItem;



