import { http } from "Helpers/axios.helper.js";

export default class MessageService {
  static Send(data) {
    const url = "/api/message/send";
    return http.post(url, data);
  }

  static getList() {
    const url = "/api/message/list";
    return http.get(url);
  }

  static getDetail(id) {
    const url = "/api/message/detail/" + id;
    return http.get(url);
  }
  static removeMessages(id) {
    const url = "/api/message/remove/" + id;
    return http.get(url);
  }
  static blockChat(id) {
    const url = "/api/message/blockchat/" + id;
    return http.get(url);
  }
  static unBlockChat(id) {
    const url = "/api/message/unblockchat/" + id;
    return http.get(url);
  }
}



