import React from "react";
const storage = props => {
  return (
    <div class="row palletwrap">
      <div class="col-xs-12">
        <div class="form-group clearfix">
          <label class="lbl clearfix">Avilable Storage</label>
          <div class="row">
            <div class="col-xs-12 col-xss-6 col-sm-4">
              <label class="ch-radioBox ">
                <input
                  type="radio"
                  data-href="#PalletStorage"
                  name="StorageType"
                  value="Pallet"
                  onChange={props.onRadio}
                  checked={props.model.StorageType === "Pallet"}
                />
                <span>Pallet Storage</span>
              </label>
            </div>
            <div class="col-xs-12 col-xss-6 col-sm-4">
              <label class="ch-radioBox">
                <input
                  type="radio"
                  data-href="#BulkStorage"
                  name="StorageType"
                  value="Bulk"
                  onChange={props.onRadio}
                  checked={props.model.StorageType === "Bulk"}
                />
                <span>BULK Storage</span>
              </label>
            </div>
            <div class="col-xs-12 col-xss-6 col-sm-4">
              <label class="ch-radioBox ">
                <input
                  type="radio"
                  data-href="#Bulk_Pallet_Storage"
                  name="StorageType"
                  value="Bulk_Pallet"
                  onChange={props.onRadio}
                  checked={props.model.StorageType === "Bulk_Pallet"}
                />
                <span>Both (Pallet/Bulk)</span>
              </label>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = storage;



