import React from "react";
import VInput from "Components/common/VInput";
import { Link } from "react-router-dom";
import { Select } from "valuelink/tags";
import MetaTags from 'react-meta-tags';
const addBankdetail = props => {
  const ALertModal = {
    ModalCss: props.ShowAlert ? "in" : "",
    ModalStyle: { display: props.ShowAlert ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };
  const SuccessModel = {
    ModalCss: props.ShowSuccess ? "in" : "",
    ModalStyle: { display: props.ShowSuccess ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };
  return (
    <section className="sbsWarpper">
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div className="wHeight innerWarp">
        <div className="cell bg-theme">
          <div className="infoBlk text-center">
            <span className="imgBlk center-block margin-bottom-8x">
              <img
                src="/Content/images/payment.png"
                alt="payment"
                className="img-responsive"
              />
            </span>
            <h2 className="margin-bottom-6x">Add bank details</h2>
            <p>
              Please add your bank details for seamless transactions on
              LogistCompare. Don't worry, your details are safe with us.
            </p>
          </div>
        </div>
        <div className="cell">
          <div className="formWarp">
            <h3 className="margin-bottom-8x">Add Bank details</h3>

            <div className="form-group">
              <VInput
                className="form-control"
                name="Name"
                placeholder="Bank name"
                maxLength="100"
                valueLink={props.validationLink.nameLink}
                type="text"
              />
            </div>
            <div className="form-group">
              <VInput
                className="form-control"
                name="BeneficiaryName"
                placeholder="Beneficiary name"
                maxLength="100"
                valueLink={props.validationLink.beneficiaryLink}
                type="text"
              />
            </div>
            <div className="form-group">
              <input
                className="form-control"
                name="BusinessName"
                placeholder="Business name"
                maxLength="100"
                onChange={props.change}
                type="text"
              />
            </div>
            <div className="form-group">
              <VInput
                className="form-control"
                name="SortCode"
                placeholder="Sort code"
                maxLength="6"
                valueLink={props.validationLink.sortCodeLink}
                type="text"
              />
            </div>
            <div className="form-group">
              <VInput
                className="form-control"
                name="AccountNumber"
                placeholder="Account number"
                maxLength="8"
                valueLink={props.validationLink.accountNumberLink}
                type="text"
              />
            </div>
            <div className="form-group">
              <VInput
                className="form-control"
                name="BirthDate"
                placeholder="DOB (yyyy-mm-dd)"
                valueLink={props.validationLink.birthDateLink}
                type="text"
              />
            </div>
            <div className="form-group">
              <VInput
                className="form-control"
                name="PostalCode"
                placeholder="PostalCode"
                maxLength="10"
                valueLink={props.validationLink.postCodeLink}
                onBlur={props.addLatLng}
                type="text"
              />
            </div>
            <div className="form-group">
              <VInput
                className="form-control"
                name="Address"
                placeholder="Enter your address"
                maxLength="100"
                valueLink={props.validationLink.addressLink}
                type="text"
              />
            </div>
            <div className="form-group">
              <VInput
                className="form-control"
                name="City"
                placeholder="City"
                maxLength="100"
                valueLink={props.validationLink.cityLink}
                type="text"
              />
            </div>
            <div className="form-group">
              <VInput
                className="form-control"
                name="Region"
                placeholder="Region"
                maxLength="100"
                valueLink={props.validationLink.regionLink}
                type="text"
              />
            </div>

            {/* <div className="form-group">
              <div className="multiSelectBlk">
                <select
                  className="multiSelectDrop"
                  name="PaymentType"
                  title="Payment type"
                  onChange={props.change}
                >
                  <option value="0">Option 1</option>
                  <option value="1">Option 2</option>
                  <option value="2">Option 3</option>
                  <option value="3">Option 4</option>
                  <option value="4">Option 5</option>
                </select>
              </div>
            </div> */}
            <div className="form-group">
              <input
                className="button button-border button-action button-uppercase button-min-width margin-top-3x margin-right-2x"
                value="Save"
                type="button"
                onClick={props.onSubmit}
              />
              <Link
                to="/warehouse/list"
                className="button button-default button-uppercase button-min-width margin-top-3x"
              >
                Back
              </Link>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + ALertModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={ALertModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={ALertModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Alert !</h2>
                <p>{ALertModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Ok"
                    onClick={ALertModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + SuccessModel.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={SuccessModel.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap" />
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Success !</h2>
                <p>Detail Saved Successfully</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <Link
                    to="/warehouse/list"
                    className="button button-border button-primary button-uppercase margin-top-3x"
                  >
                    Ok
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
module.exports = addBankdetail;



