import React from "react";
import VInput from "Components/common/VInput";
import MetaTags from 'react-meta-tags';
const ChangePassword = props => {
  const SuccessModal = {
    ModalCss: props.ShowSuccessModal ? "in" : "",
    ModalStyle: { display: props.ShowSuccessModal ? "block" : "none" },
    ModalBody: props.Message,
    onClose: props.onClose
  };
  const ALertModal = {
    ModalCss: props.ShowAlertModal ? "in" : "",
    ModalStyle: { display: props.ShowAlertModal ? "block" : "none" },
    ModalBody: props.Message,
    onClose: props.onClose
  };
  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="header">
        <h2>Settings</h2>
      </div>
      <div class="mainContent boxScroll">
        <div class="findStorageInner innerPadding">
          <div class="formWarpInner">
            <form>
              <div class="row">
                <div class="col-xs-12">
                  <div class="form-group">
                    <label>Change Password</label>
                    <div class="row">
                      <div class="col-xs-12 col-sm-6 col-md-3 margin-bottom-2x">
                        <VInput
                          type="password"
                          class="form-control"
                          name="OldPassword"
                          valueLink={props.links.oldPasswordLink}
                          placeholder="Old Password"
                        />
                      </div>
                      <div class="col-xs-12 col-sm-6 col-md-3 margin-bottom-2x">
                        <VInput
                          type="password"
                          class="form-control"
                          name="NewPassword"
                          valueLink={props.links.passwordLink}
                          placeholder="New Password"
                        />
                      </div>
                      <div class="col-xs-12 col-sm-6 col-md-3 margin-bottom-2x">
                        <VInput
                          type="password"
                          class="form-control"
                          name="ConfirmPassword"
                          valueLink={props.links.confirmPasswordLink}
                          placeholder="Confirm Passowrd"
                        />
                      </div>
                      <div class="col-xs-12">
                        <input
                          type="button"
                          class="button button-primary margin-top-2x"
                          value="Update"
                          onClick={props.handleSubmit}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + SuccessModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={SuccessModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={SuccessModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Success</h2>
                <p>
                  {SuccessModal.ModalBody}
                </p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <button
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    onClick={SuccessModal.onClose}
                  >
                    OK
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div
        class={"modal fade " + ALertModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={ALertModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={ALertModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Alert !</h2>
                <p>
                  {ALertModal.ModalBody}
                </p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Ok"
                    onClick={ALertModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = ChangePassword;



