import React from "react";
import Rating from "Components/common/starRating.js";
import GoogleMap from "Components/map/googlemap.js";
import { Marker } from "react-google-maps";
import MapModal from "Components/common/mapModal.js";
import Detail from "Components/detailModal/detailModal.container.js";
import ReviewModal from "Components/rating/reviewModal.js";
import Formula from "Services/formula.service.js";
import MetaTags from 'react-meta-tags';
const savesearch = props => {
  let warehouseImages = [];
  const list = props.model.Result.map((val, key) => {
    warehouseImages = [...val.warehouse_detail[0].Images];
    if (warehouseImages.length == 0) {
      warehouseImages.push("/Content/images/warehouse/w1.jpg");
    }
    const images = warehouseImages.map((img, k) => {
      return (
        <li key={k}>
          <img src={img} alt="1" class="img-responsive" onError={e => {
            e.target.src = "/Content/images/warehouse/w1.jpg";
          }} />
        </li>
      );
    });
    let Totalcost = 0;
    if (val.warehouse_detail[0].StorageType == "Pallet") {
      Totalcost = parseFloat(
        parseFloat(val.warehouse_detail[0].Pallet.ApxPrice) *
        parseFloat(val.warehouse_detail[0].Pallet.Total)
      ).toFixed(2);
    } else if (val.warehouse_detail[0].StorageType == "Bulk") {
      let space = 0;
      if (
        val.warehouse_detail[0].Bulk.ApxPriceType == "Meter" &&
        val.warehouse_detail[0].CapacityType != "Meter"
      ) {
        space = Formula.SqFeetToSqMeter(
          val.warehouse_detail[0].Bulk.TotalSpace
        );
      } else {
        space = val.warehouse_detail[0].Bulk.TotalSpace;
      }
      Totalcost = parseFloat(
        parseFloat(val.warehouse_detail[0].Bulk.ApxPrice) * parseFloat(space)
      ).toFixed(2);

    } else {
      let bulkspace = 0;
      if (
        val.warehouse_detail[0].Bulk.ApxPriceType == "Meter" &&
        val.warehouse_detail[0].CapacityType != "Meter"
      ) {
        bulkspace = Formula.SqFeetToSqMeter(
          val.warehouse_detail[0].Bulk.TotalSpace
        );
      } else {
        bulkspace = val.warehouse_detail[0].Bulk.TotalSpace;
      }
      const bulkCost = parseFloat(
        parseFloat(val.warehouse_detail[0].Bulk.ApxPrice) *
        parseFloat(bulkspace)
      ).toFixed(2);

      const palletCost = parseFloat(
        parseFloat(val.warehouse_detail[0].Pallet.ApxPrice) *
        parseFloat(val.warehouse_detail[0].Pallet.Total)
      ).toFixed(2);
      Totalcost = bulkCost + palletCost;

    }
    Totalcost = isNaN(Totalcost) ? 0 : Totalcost;
    const messageLink = "/user/message/" + val.warehouse_detail[0].UserId;
    const bookingLink =
      "/user/quotes?wid=" +
      val.warehouse_detail[0]._id +
      "&type=" +
      val.warehouse_detail[0].StorageType;

    return (
      <div class="resultWarp margin-bottom-6x" key={key}>
        <div class="cell">
          <ul class="slickSingle">{images}</ul>
        </div>
        <div class="cell">
          <div class="__content">
            <span
              style={{ cursor: "pointer" }}
              data-toggle="modal"
              data-target="#warehouseDetail"
              onClick={() => {
                props.getDetail(val.WarehouseId);
              }}
            >
              <h3>{val.warehouse_detail[0].Name}</h3>
            </span>
            <div class="row">
              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
                <label>Space Availabilty</label>
                <p>
                  {parseFloat(val.warehouse_detail[0].AvailableSpace).toFixed(
                    2
                  )}{" "}
                  sq Meter
                </p>
              </div>
              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
                <label>Date Searched</label>
                <p>10/02-2017</p>
              </div>
              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
                <label>Location</label>
                <p>
                  <a
                    href="#mapViewTab"
                    data-toggle="tab"
                    onClick={() => {
                      props.onMapView(val.warehouse_detail[0]._id);
                    }}
                  >
                    View on map <i class="fa fa-angle-right" />
                  </a>
                </p>
              </div>
              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
                <label>Capacity</label>
                <p>{val.warehouse_detail[0].Capacity} sq Meter</p>
              </div>
              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
                <span
                  data-toggle="modal"
                  data-target="#viewRatingModal"
                  onClick={() => {
                    props.getRating(val.WarehouseId);
                  }}
                >
                  <label style={{ cursor: "pointer" }}>Rating</label>
                  <div class="ratingBlk">
                    <Rating input={val.warehouse_detail[0].AvgReviews} />
                  </div>
                </span>
              </div>
              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
                <label>Approx Cost</label>
                <div class="dropdown">
                  <div class="costDropdown">
                    <a
                      href="javascript:void(0);"
                      class="dropdown-toggle"
                      data-toggle="dropdown"
                    >
                      &pound;{Totalcost}
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="cell button-cell">
          <a
            href={bookingLink}
            class="button button-action button-min-width margin-top-2x margin-bottom-2x"
          >
            Get quote
          </a>
          <a
            href={messageLink}
            class="button button-default button-min-width margin-top-2x margin-bottom-2x"
          >
            Message
          </a>
        </div>
      </div>
    );
  });
  const map = props.model.isShowMap ? <MapModal {...props} /> : "";
  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="header">
        <h2>Saved Search</h2>
      </div>
      <div class="mainContent boxScroll">
        <div class="sStorageWarp quoteWarp">
          <div class="tab-content">
            <div class="tab-pane fade in active" id="resultViewTab">
              {list}
            </div>
          </div>
        </div>
      </div>
      {map}
      {props.model.isShowDetail && <Detail {...props} />}
      <ReviewModal {...props} />
    </div>
  );
};

export default savesearch;



