import { http } from "Helpers/axios.helper.js";

export default class PaymentService {
  static AddBankDetails(bankdetail) {
    const url = "/api/mango/createlegaluser"; //"/api/user/addbankdetails";
    return http.post(url, bankdetail);
  }
  static CreatePayment(paymentDetail) {
    const url = "/api/mango/savecard"; //"/api/user/addbankdetails";
    return http.post(url, paymentDetail);
  }
  static SaveSubscription(transactionId) {
    let data = {
      tid: transactionId
    };
    return http.post("/api/mango/updatesubscription", data);
  }
  static BuySubscription() {
    return http.get("/api/mango/buysubscription");
  }
  static CheckValidityandSpace(data) {
    return http.post("/api/warehouse/spaceavailability", data);
  }
  static GetBankInformation() {
    return http.get("/api/user/bankdetail");
  }
  static CreateBankPayment(transactionId, paymentMode) {
    let data = {
      tid: transactionId,
      Method: paymentMode
    };
    return http.post("/api/subscription/create", data);
  }
}



