import React from "react";
import Flatpickr from "react-flatpickr";
import Images from "./images.js";
const bulk = props => {
  let imagesList = "";
  if (props.model.StorageType != "Pallet") imagesList = <Images {...props} />;

  return (
    <div class="palletShow" id="BulkStorage">
      <div class="row">
        <div class="col-xs-12">
          <div class="pallet-heading">
            <h2 class="titlePallet">
              Bulk Storage{" "}
              <span>
                (Please enter exact dimensions or just Total Space required){" "}
              </span>
            </h2>
            <div class="pending-wrap">
              <label>Pending space</label>
              <div class="pending-box">
                <p>
                  {props.model.PendingSpace}
                  {" sq" + props.model.CapacityType}
                </p>
              </div>
            </div>
          </div>
          <div class="form-group">
            <div class="row">
              <div class="col-xs-12 col-xss-6 col-sm-6 col-md-3 form-group">
                <label> Max Length</label>
                <div class="row Row-2">
                  <div class="col-xs-8">
                    <input
                      type="text"
                      class="form-control"
                      placeholder=""
                      id="MaxLength"
                      name="Bulk"
                      value={props.model.Bulk.MaxLength}
                      onChange={props.change}
                      onKeyPress={props.onkeyPress}
                      onPaste={props.onPaste}
                    />
                  </div>
                  <div class="col-xs-4">
                    <div class="row Row-2">
                      <select
                        class="form-control"
                        title=""
                        id="MaxLengthType"
                        name="Bulk"
                        value={props.model.Bulk.MaxLengthType}
                        onChange={props.change}
                        disabled
                      >
                        <option value="Meter">Meters</option>
                        <option value="Feet">Feet</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-xs-12 col-xss-6 col-sm-6 col-md-3 form-group">
                <label> Max Width</label>
                <div class="row Row-2">
                  <div class="col-xs-8">
                    <input
                      type="text"
                      class="form-control"
                      placeholder=""
                      id="MaxWidth"
                      name="Bulk"
                      value={props.model.Bulk.MaxWidth}
                      onChange={props.change}
                      onKeyPress={props.onkeyPress}
                      onPaste={props.onPaste}
                    />
                  </div>
                  <div class="col-xs-4">
                    <div class="row Row-2">
                      <select
                        class="form-control"
                        title=""
                        id="MaxWidthType"
                        name="Bulk"
                        value={props.model.Bulk.MaxWidthType}
                        onChange={props.change}
                        disabled
                      >
                        <option value="Meter">Meters</option>
                        <option value="Feet">Feet</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-xs-12 col-xss-6 col-sm-6 col-md-3 form-group">
                <label> Max Height</label>
                <div class="row Row-2">
                  <div class="col-xs-8">
                    <input
                      type="text"
                      class="form-control"
                      placeholder=""
                      id="MaxHeight"
                      name="Bulk"
                      value={props.model.Bulk.MaxHeight}
                      onChange={props.change}
                      onKeyPress={props.onkeyPress}
                      onPaste={props.onPaste}
                    />
                  </div>
                  <div class="col-xs-4">
                    <div class="row Row-2">
                      <select
                        class="form-control"
                        title=""
                        id="MaxHeightType"
                        name="Bulk"
                        value={props.model.Bulk.MaxHeightType}
                        onChange={props.change}
                        disabled
                      >
                        <option value="Meter">Meters</option>
                        <option value="Feet">Feet</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-xs-12 col-xss-6 col-sm-6 col-md-3 form-group">
                <label> Max Weight</label>
                <div class="row Row-2">
                  <div class="col-xs-8">
                    <input
                      type="text"
                      class="form-control"
                      placeholder=""
                      id="MaxWeight"
                      name="Bulk"
                      value={props.model.Bulk.MaxWeight}
                      onChange={props.change}
                      onKeyPress={props.onkeyPress}
                      onPaste={props.onPaste}
                    />
                  </div>
                  <div class="col-xs-4">
                    <div class="row Row-2">
                      <select
                        class="form-control"
                        title=""
                        id="MaxWeightType"
                        name="Bulk"
                        value={props.model.Bulk.MaxWeightType}
                        onChange={props.change}
                      >
                        <option value="kgs">Kgs</option>
                        <option value="Lbs">Lbs</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col-xs-12 col-sm-4">
                <div class="form-group">
                  <label class="lbl">Availability</label>
                  <div class="__dateRang">
                    <div class="input-group">
                      <Flatpickr
                        className="form-control"
                        id="AvailabilityFrom"
                        name="Bulk"
                        data-min-date="today"
                        value={props.model.Bulk.AvailabilityFrom}
                        onChange={(_, str) =>
                          props.onflatpicker("Bulk.AvailabilityFrom", str)}
                      />

                      <span class="input-group-addon">to</span>
                      <Flatpickr
                        className="form-control"
                        id="AvailabilityTo"
                        name="Bulk"
                        data-min-date="today"
                        value={props.model.Bulk.AvailabilityTo}
                        onChange={(_, str) =>
                          props.onflatpicker("Bulk.AvailabilityTo", str)}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-xs-12 col-sm-4 form-group">
                <div class="col-xs-12">
                  <label>Appx pricing </label>
                </div>
                <div class="col-xs-12">
                  <div class="col-xs-8">
                    <div class="input-group">
                      <span class="input-group-addon" id="basic-addon1">
                        &pound;
                      </span>
                      <input
                        type="text"
                        class="form-control"
                        placeholder=""
                        id="ApxPrice"
                        name="Bulk"
                        value={props.model.Bulk.ApxPrice}
                        onChange={props.change}
                        onKeyPress={props.onkeyPress}
                        onPaste={props.onPaste}
                      />
                    </div>
                  </div>
                  <div class="col-xs-4" style={{ padding: "0" }}>
                    <select
                      class="form-control"
                      title=""
                      id="ApxPriceType"
                      name="Bulk"
                      value={props.model.Bulk.ApxPriceType}
                      onChange={props.change}
                      disabled
                    >
                      <option value="Meter">Sq Meter</option>
                      <option value="Feet">Sq Feet</option>
                    </select>
                  </div>
                </div>
              </div>
              <div class="col-xs-12 col-sm-4 form-group">
                <label>Total Space </label>
                <input
                  type="text"
                  class="form-control"
                  placeholder=""
                  id="TotalSpace"
                  name="Bulk"
                  value={props.model.Bulk.TotalSpace}
                  onChange={props.change}
                  onKeyPress={props.onkeyPress}
                  onPaste={props.onPaste}
                />
              </div>
            </div>
            <div class="row">
              <div class="col-xs-12 form-group">
                <label>Desc</label>
                <textarea
                  class="form-control"
                  rows="6"
                  placeholder=""
                  id="Description"
                  name="Bulk"
                  value={props.model.Bulk.Description}
                  onChange={props.change}
                />
              </div>
            </div>
            {/*images*/}
            {imagesList}
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = bulk;



