import React from "react";
import Flatpickr from "react-flatpickr";

const pallet = props => {
  return (
    <div class="tab-pane fade in active" id="PalletsSection">
      <div class="formWarpInner">
        <form>
          <div class="row">
            <div class="col-xs-12 col-sm-6 col-md-3">
              <div class="form-group">
                <label>Type of pallets</label>
                <div class="">
                  <select
                    class="form-control"
                    title={props.model.search.Pallet.Type.Name}
                    name="Pallet"
                    id="Type"
                    value={props.model.search.Pallet.Type.Value}
                    onChange={props.change}
                  >
                    <option id="EURO" value="0.96">
                      EURO : 0.80 x1.20 (W x L)
                    </option>
                    <option id="STD1" value="1.20">
                      STD1 : 1.00 x1.20 (W x L)
                    </option>
                    <option id="STD2" value="1.44">
                      STD2 : 1.20x1.20(W x L)
                    </option>
                  </select>
                </div>
              </div>
            </div>
            <div class="col-xs-12 col-sm-6 col-md-3">
              <div class="form-group">
                <label>Number of pallets</label>
                <input
                  type="text"
                  class="form-control"
                  placeholder=""
                  name="Pallet"
                  id="Total"
                  value={props.model.search.Pallet.Total}
                  onChange={props.change}
                  onKeyPress={props.onkeyPressWithoutDot}
                  onPaste={props.onPaste}
                />
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col-xs-12 col-sm-6 col-md-3 form-group">
              <label>Pallets Height (Per Pallet)</label>
              <div class="row Row-2">
                <div class="col-xs-8">
                  <input
                    type="text"
                    class="form-control"
                    placeholder=""
                    id="MaxHeight"
                    name="MaxHeight"
                    value={props.model.search.MaxHeight}
                    onChange={props.change}
                    onKeyPress={props.onkeyPress}
                    onPaste={props.onPaste}
                  />
                </div>
                <div class="col-xs-4">
                  <div class="">
                    <select
                      class="form-control"
                      id="MaxHeightType"
                      name="MaxHeightType"
                      value={props.model.search.MaxHeightType}
                      onChange={props.change}
                    >
                      <option value="Meter">Meters</option>
                      <option value="Feet">Feet</option>
                    </select>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-xs-12 col-sm-6 col-md-3 form-group">
              <label>Pallets Weight (Per Pallet)</label>
              <div class="row Row-2">
                <div class="col-xs-8">
                  <input
                    type="text"
                    class="form-control"
                    placeholder=""
                    id="MaxWeight"
                    name="MaxWeight"
                    value={props.model.search.MaxWeight}
                    onChange={props.change}
                    onKeyPress={props.onkeyPress}
                    onPaste={props.onPaste}
                  />
                </div>
                <div class="col-xs-4">
                  <div class="">
                    <select
                      class="form-control"
                      title="kgs"
                      name="MaxWeightType"
                      id="MaxWeightType"
                      value={props.model.search.MaxWeightType}
                      onChange={props.change}
                    >
                      <option value="Kgs">kgs</option>
                      <option val="Lbs">Lbs</option>
                    </select>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col-xs-12 col-sm-6 col-md-3 form-group">
              <label>Start date to end date</label>
              <div class="has-feedback">
                <Flatpickr
                  className="form-control"
                  id="AvailabilityFrom"
                  name="AvailabilityFrom"
                  placeholder="From"
                  value={props.model.search.AvailabilityFrom}
                  data-min-date="today"
                  onChange={(_, str) =>
                    props.onflatpicker("AvailabilityFrom", str)}
                />
              </div>
            </div>
            <div class="col-xs-12 col-sm-6 col-md-3 form-group">
              <label>&nbsp;</label>
              <div class="has-feedback">
                <Flatpickr
                  className="form-control"
                  id="AvailabilityTo"
                  name="AvailabilityTo"
                  placeholder="To"
                  value={props.model.search.AvailabilityTo}
                  data-min-date="today"
                  onChange={(_, str) =>
                    props.onflatpicker("AvailabilityTo", str)}
                />
              </div>
            </div>
          </div>
          <div class="form-group">
            <input
              type="button"
              onClick=""
              class="button button-primary"
              value="Search"
              onClick={props.next}
            />
          </div>
        </form>
      </div>
    </div>
  );
};

module.exports = pallet;



