import React from "react";
import Contact from "./contactus.js";
import ContactUsService from "Services/contactus.service.js";
import Link from "valuelink";

const NameRegex = /^[a-zA-Z\s]*$/;
const EmailRegex = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

const initialState = {
  Name: "",
  Email: "",
  Subject: "",
  Message: "",
  ShowAlertModal: false,
  ModalBody: "",
  SuccessModal: false
};
let links = {};
export default class ContactUsContainer extends React.Component {
  constructor() {
    super();
    this.state = initialState;
  }
  reset() {
    this.setState(initialState);
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  sendMessage() {
    if (this.valid()) {
      ContactUsService.sendMessage(this.state).then(response => {
        if (response.data.status) {
          this.setState({
            SuccessModal: true,
            ModalBody: response.data.Message
          });
        } else {
          this.setState({
            ShowAlertModal: true,
            ModalBody: response.data.Message
          });
        }
      });
    }
  }
  CloseModal() {
    this.setState({
      ShowAlertModal: false,
      SuccessModal: false,
      ModalBody: ""
    });
    this.reset();
  }
  render() {
    links = {
      nameLink: Link.state(this, "Name")
        .check(x => x, "Name is Required")
        .check(x => NameRegex.test(x), "Enter Characters only"),
      emailLink: Link.state(this, "Email")
        .check(x => x, "Email is Required")
        .check(x => EmailRegex.test(x), "Email not valid"),
      subjectLink: Link.state(this, "Subject").check(
        x => x,
        "Subject is required"
      ),
      messageLink: Link.state(this, "Message").check(
        x => x,
        "Message is required"
      )
    };
    const contactProps = {
      sendMessage: this.sendMessage.bind(this),
      SuccessModal: this.state.SuccessModal,
      ShowAlertModal: this.state.ShowAlertModal,
      ModalBody: this.state.ModalBody,
      onClose: this.CloseModal.bind(this),
      links
    };
    return <Contact {...contactProps} />;
  }
}



