import { http } from "Helpers/axios.helper.js";

export default class CustomerService {
  static getCustomers() {
    const url = "/api/quote/getrequestedusers";
    return http.get(url);
  }
  static getActivities() {
    const url = "/api/search/getactivities";
    return http.get(url);
  }
  static dd() {
    const url = "/api/search/getactivities";
    return http.get(url);
  }
}



