import React from "react";

const Modal = (props) => {
   
    const SuccessModal = {
        ModalCss: props.ShowSuccessModal ? "in" : "",
        ModalStyle: { display: props.ShowSuccessModal ? "block" : "none" },
        ModalBody: props.Message,
        onClose: props.onClose,      
    }
    const ALertModal = {
        ModalCss: props.ShowAlertModal ? "in" : "",
        ModalStyle: { display: props.ShowAlertModal ? "block" : "none" },
        ModalBody: props.Message,
        onClose: props.onClose
    }

    return (
        <div>
              <div class={"modal fade " + SuccessModal.ModalCss} id="myModal-ALert" tabIndex="-1" role="dialog" aria-labelledby="myModalLabel-Alert" style={SuccessModal.ModalStyle}>
                <div class="modal-dialog" role="document">
                    <div class="modal-content forgot">
                        <div class="closewrap">
                            <button type="button" class="close" data-dismiss="modal" onClick={SuccessModal.onClose}>&times;</button>
                        </div>
                        <div class="modal-body forgot-password">

                            <div class="headpassword">
                                <h2>Success</h2>
                                <p>{SuccessModal.ModalBody}</p>
                            </div>

                            <div class="formWarp password">

                                <div class="form-group">
                                    <input type="button" class="button button-border button-primary button-uppercase margin-top-3x" value="Ok" onClick={ALertModal.onClose} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
         
            <div class={"modal fade " + ALertModal.ModalCss} id="myModal-ALert" tabIndex="-1" role="dialog" aria-labelledby="myModalLabel-Alert" style={ALertModal.ModalStyle}>
                <div class="modal-dialog" role="document">
                    <div class="modal-content forgot">
                        <div class="closewrap">
                            <button type="button" class="close" data-dismiss="modal" onClick={ALertModal.onClose}>&times;</button>
                        </div>
                        <div class="modal-body forgot-password">

                            <div class="headpassword">
                                <h2>Alert !</h2>
                                <p>{ALertModal.ModalBody}</p>
                            </div>

                            <div class="formWarp password">

                                <div class="form-group">
                                    <input type="button" class="button button-border button-primary button-uppercase margin-top-3x" value="Ok" onClick={ALertModal.onClose} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )

}
module.exports = Modal;



