import React from "react";
import Pellet from "./pellet.js";
import Bulk from "./Bulk.js";
const QuoteForm = props => {
  let quoteForm = null;

  if (props.State.IsBulk) {
    quoteForm = <Bulk {...props} />;
  } else {
    quoteForm = <Pellet {...props} />;
  }
  return (
    <div class="cell">
      <div class="curvedTabs margin-top-2x margin-bottom-8x">
        <ul>
          <li class={props.State.IsBulk ? "" : "active"}>
            <a href="javascript:void(0)">Pallets</a>
          </li>
          <li class={props.State.IsBulk ? "active" : ""}>
            <a href="javascript:void(0)">Bulk</a>
          </li>
        </ul>
      </div>
      <div class="tab-content">
        {quoteForm}
      </div>
    </div>
  );
};
module.exports = QuoteForm;



