export default class formula {
  static MeterToFeet(number) {
    const num = parseFloat(number);
    return Number((num * 3.28084).toFixed(2));
  }
  static SqMeterToSqFeet(number) {
    const num = parseFloat(number);
    return Number((num * 10.7639).toFixed(2));
  }

  static FeetToMeter(number) {
    const num = parseFloat(number);
    return Number((num / 3.28084).toFixed(2));
  }
  static SqFeetToSqMeter(number) {
    const num = parseFloat(number);
    return Number((num / 10.7639).toFixed(2));
  }
  static KgToPounds(number) {
    const num = parseFloat(number);
    return Number((num * 2.2).toFixed(2));
  }
  static PoundsToKg(number) {
    const num = parseFloat(number);
    return Number((num / 2.2).toFixed(2));
  }
  static KgtoLb(number) {
    const num = parseFloat(number);
    return Number((num * 2.2).toFixed(2));
  }
  static LbtoKg(number) {
    const num = parseFloat(number);
    return parseInt((num / 2.2).toFixed(2));
  }
}



