import React from "react";
const type = props => {
  const style = { textTransform: "capitalize" };

  const chkBoxes = Object.entries(
    props.model.WarehouseType
  ).map(([key, val], i) => {
    return (
      <label key={i} class="ch-checkMark" style={style}>
        <input
          type="checkbox"
          id={key}
          name="type"
          checked={val}
          onChange={props.oncheckBox}
        />
        <span>
          {key}
        </span>
      </label>
    );
  });
  return (
    <div class="form-group clearfix">
      <label class="lbl clearfix">Warehouse type</label>
      <div>
        {chkBoxes}
      </div>
    </div>
  );
};
module.exports = type;



