import { http } from "Helpers/axios.helper.js";

export default class BlogService {
  static all() {
    const url = "/api/blog/getAllBlogList";
    return http.get(url);
  }
  static getSkipBlockList(skipNo) {
    const url = "/api/blog/blockLIstBypage/" + skipNo;
    return http.get(url);
  }

  static getBlog(Id) {
    const url = "/api/blog/getBlogById/" + Id;
    return http.get(url);
  }
}



