import React from "react";

const PelletItem = props => {
  return (
    <div class="bCateBlk">
      <div class="blk_heading">
        <h4>{props.Category}</h4>
      </div>
      <ul>
        <li>
          <label>Quantity</label>
          <p>{props.TotalPallets}</p>
        </li>
        {props.HeigthPerPellet == null || props.HeigthPerPellet == "" ? (
          ""
        ) : (
          <li>
            <label>Height ({props.HeightType})</label>
            <p>{props.HeigthPerPellet + " " + props.HeightType}</p>
          </li>
        )}
        {props.TotalWeight == null || props.TotalWeight == "" ? (
          ""
        ) : (
          <li>
            <label>Weight (LBs/Kgs)</label>
            <p>{props.TotalWeight + " " + props.WeightType}</p>
          </li>
        )}
        <li>
          <label>Total space required (sq meter/sq feet)</label>
          <p>{props.TotalSpace + " sq " + props.HeightType}</p>
        </li>
        <li>
          <label>Start Date</label>
          <p>{props.FromDate}</p>
        </li>
        <li>
          <label>End Date</label>
          <p>{props.ToDate}</p>
        </li>
        <div class="bottm_view margin-top-4x">
          <a
            href="javascript:void(0)"
            onClick={() => props.EditPallet(props.index)}
            class={props._id == props.pid ? "tab active" : "tab"}
          >
            <span class="view_detial">View Detail</span>
          </a>
        </div>
      </ul>
    </div>
  );
};
module.exports = PelletItem;



