import React from "react";
import Rating from "Components/common/starRating.js";
import ConfirmBox from "Components/common/confirmbox.js";
import MapModal from "Components/common/mapModal.js";
import Helper from "Helpers/utility.helper.js";
import Formula from "Services/formula.service.js";
import { Link } from "react-router-dom";
import MetaTags from 'react-meta-tags';
const WareList = props => {
  const ware = props.model.wareObj.map((item, i) => {
    const images = item.Images.map((val, k) => (
      <li key={k} class="">
        <img
          src={val}
          alt="1"
          class="img-responsive"
          onError={e => {
            e.target.src = "/Content/images/warehouse/w1.jpg";
          }}
        />
      </li>
    ));
    if (images.length < 1) {
      images.push(
        <li key="1">
          <img
            src="/Content/images/warehouse/w1.jpg"
            alt="1"
            class="img-responsive"
          />
        </li>
      );
    }

    let AvailabilityTime = "";
    if (item.StorageType == "Pallet")
      AvailabilityTime = (
        <p>
          {Helper.ConvertDateToString(item.Pallet.AvailabilityFrom)} -{" "}
          {Helper.ConvertToDateToString(item.Pallet.AvailabilityTo)}
        </p>
      );
    else if (item.StorageType == "Bulk")
      AvailabilityTime = (
        <p>
          {Helper.ConvertDateToString(item.Bulk.AvailabilityFrom)} -{" "}
          {Helper.ConvertToDateToString(item.Bulk.AvailabilityTo)}
        </p>
      );
    else
      AvailabilityTime = (
        <p>
          {Helper.ConvertDateToString(item.Pallet.AvailabilityFrom)} -{" "}
          {Helper.ConvertToDateToString(item.Pallet.AvailabilityTo)}
          <br />
          {Helper.ConvertDateToString(item.Bulk.AvailabilityFrom)} -{" "}
          {Helper.ConvertToDateToString(item.Bulk.AvailabilityTo)}
        </p>
      );

    const Capacity =
      item.CapacityType == "Meter"
        ? item.Capacity
        : Helper.RoundOffCapacity(Formula.SqMeterToSqFeet(item.Capacity));
    const edit = "/warehouse/edit/" + item._id;
    const detail = "/warehouse/detail/" + item._id;
    return (
      <div class="tab-pane fade in active" id="resultViewTab" key={i}>
        <div class="resultWarp margin-bottom-6x">
          <div class="cell">
            {images && <ul class="slickSingle">{images}</ul>}
          </div>
          <div class="cell">
            <div class="__content">
              <div class="__contentHead">
                <h3>
                  <Link to={detail}>{item.Name}</Link>
                </h3>
                <span class="__actions">
                  <a href={edit} class="editLink">
                    <i class="icon-edit-pencil" />
                  </a>
                  <a
                    href="javascript:void(0);"
                    class="trashLink margin-left-1x"
                    onClick={() => props.onpopup(item._id)}
                  >
                    <i class="icon-trash" />
                  </a>
                </span>
              </div>
              <div class="row">
                <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-3 form-group">
                  <label>Space Availability</label>
                  <p>
                    {item.CapacityType == "Meter"
                      ? item.AvailableSpace
                      : Formula.SqMeterToSqFeet(item.AvailableSpace)}{" "}
                    sq {item.CapacityType}
                  </p>
                </div>
                <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-3 form-group">
                  <label>Time Availablity</label>
                  {AvailabilityTime}
                </div>
                <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-3 form-group">
                  <label>Location</label>
                  <p>
                    <a
                      href="#mapViewTab"
                      data-toggle="tab"
                      onClick={() => {
                        props.onMapView(item._id);
                      }}
                    >
                      View on map <i class="fa fa-angle-right" />
                    </a>
                  </p>
                </div>
                <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-3 form-group">
                  <label>Capacity</label>
                  <p>
                    {Capacity} sq {item.CapacityType}
                  </p>
                </div>
              </div>
              <div class="row">
                <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-3 form-group">
                  <label>Rating</label>
                  <div class="ratingBlk">
                    <Rating input={item.AvgReviews} />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  });

  const ALertModal = {
    ModalCss: props.model.ShowAlertModal ? "in" : "",
    ModalStyle: { display: props.model.ShowAlertModal ? "block" : "none" },
    onConfirm: props.ondelete,
    onClose: props.CloseModal,
    Message: "Are you sure to delete the warehouse ?"
  };
  const map = props.model.isShowMap ? <MapModal {...props} /> : "";
  if (ware.length == 0)
    ware.push(
      <div class="no-result-found" key="123">
        <p>No Result Found</p>
      </div>
    );
  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="align-center">
        <h2>
          Make sure you enter your{" "}
          <a href="javascript:void(0)" onClick={props.redirect}>
            bank details
          </a>{" "}
          to receive your payments.
        </h2>
      </div>
      <div class="header-warehouse">
        <div class="header headerWarehouse">
          <h2>Warehouses</h2>
        </div>
        <div class="clearfix warehouseHeader">
          <a href="/warehouse/add" class="margin-top-4x">
            <span class="addWarehouses">
              <i class="icon-plus ico-t2" />
            </span>
            <span class="gray">Add New Warehouse</span>
          </a>
        </div>
      </div>
      <div class="mainContent boxScroll">
        <div class="sStorageWarp wh_Warp">
          <div class="tab-content">{ware}</div>
        </div>
      </div>
      <ConfirmBox {...ALertModal} />
      {map}
    </div>
  );
};
module.exports = WareList;



