import React from "react";
import SearchCustomer from "./customersearch.js";
import CustomerQuotes from "./customerquotes.js";
import ReviewModal from "Components/rating/reviewModal.js";
import MetaTags from 'react-meta-tags';
const Customer = props => {
  let CustomersList = (
    <div class="no-result-found">
      <p>No Result Found</p>
    </div>
  );
  let SearchCustomerList = (
    <div class="no-result-found">
      <p>No Result Found</p>
    </div>
  );
  if (props.IsActivityTab) {
    if (props.customerActivity.length > 0) {
      SearchCustomerList = props.customerActivity.map((x, i) => {
        return (
          <SearchCustomer
            getWarehouseDetail={props.getWarehouseDetail}
            index={i}
            key={i}
            {...x}
          />
        );
      });
    }
  } else {
    if (props.customerList.length > 0) {
      CustomersList = props.customerList.map((x, i) => {
        return (
          <CustomerQuotes
            getWarehouseDetail={props.getWarehouseDetail}
            index={i}
            key={i}
            {...x}
          />
        );
      });
    }
  }
  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      {" "}<div class="header">
        <h2>Customers</h2>
      </div>
      <div class="mainContent boxScroll">
        <div class="sStorageWarp paymentWrap">
          <div class="row">
            <div class="col-xs-12 col-sm-8 col-md-8 customers">
              <div class="curvedTabs margin-top-2x margin-bottom-8x">
                <ul>
                  <li class={props.IsActivityTab ? "active" : ""}>
                    <a
                      href="javascript:void(0)"
                      onClick={() => props.getcustomerActivities()}
                    >
                      People who Searched
                    </a>
                  </li>
                  <li class={props.IsActivityTab ? "" : "active"}>
                    <a
                      href="javascript:void(0)"
                      onClick={() => props.getcustomer()}
                    >
                      People who asked for Quote
                    </a>
                  </li>
                </ul>
              </div>
            </div>
            <div class="col-xs-12">
              <div class="tab-content">
                <div
                  class={
                    props.IsActivityTab
                      ? "tab-pane fade in active"
                      : "tab-pane fade"
                  }
                  id="BulkSection"
                >
                  {SearchCustomerList}
                </div>
                <div
                  class={
                    props.IsActivityTab
                      ? "tab-pane fade"
                      : "tab-pane fade in active"
                  }
                  id="PalletsSection"
                >
                  {CustomersList}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <ReviewModal {...props} />
    </div>
  );
};
module.exports = Customer;



