import React from "react";
import Flatpickr from "react-flatpickr";
import GoogleMap from "Components/map/googlemap.js";
import { Marker } from "react-google-maps";
import MetaTags from 'react-meta-tags';
const INPUT_STYLE = {
  width: `240px`,
  height: `32px`
};
const find = props => {
  var countMsg = "";
  if (props.model.totalRecords > 0) {
    countMsg = props.model.totalRecords + " warehouse found";
  } else if (props.model.totalRecords == 0) {
    countMsg = (
      <p>
        Can't find what you need? Ask us to find you a
        <a style={{ cursor: "pointer" }} onClick={props.redirect}>
          {" "}
          solution.
        </a>
      </p>
    );
  }

  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="header">
        <h2>Select Storage</h2>
      </div>
      <div class="mainContent boxScroll">
        <div class="findStorageInner">
          <div class="cell">
            <div id="map">
              <GoogleMap center={props.center}>
                {props.places.length &&
                  props.places.map(([lat, lng], i) => (
                    <Marker position={{ lat, lng }} />
                  ))}
              </GoogleMap>
            </div>
          </div>
          <div class="cell">
            <div class="curvedTabs margin-top-2x margin-bottom-8x">
              <ul>
                <li class={props.model.search.Type == "Pallet" ? "active" : ""}>
                  <a
                    href=""
                    data-toggle="tab"
                    name="Type"
                    value="Pallet"
                    onClick={props.change}
                  >
                    Pallet
                  </a>
                </li>
                <li class={props.model.search.Type == "Bulk" ? "active" : ""}>
                  <a
                    href=""
                    data-toggle="tab"
                    name="Type"
                    value="Bulk"
                    onClick={props.change}
                  >
                    Bulk
                  </a>
                </li>
              </ul>
            </div>
            <div class="tab-content">
              <div class="tab-pane fade in active">
                <div class="formWarpInner">
                  <form>
                    <div class="form-group">
                      <div style={{ textAlign: "center" }}>{countMsg}</div>
                    </div>
                    <div class="form-group">
                      <label>Location</label>
                      <input
                        ref={props.setRef}
                        type="text"
                        class="form-control"
                        placeholder="City/PostCode/County"
                        name="Location"
                        value={props.model.search.Location}
                        onChange={props.change}
                        onKeyPress={props.keyPress}
                        autocomplete="off"
                      />
                    </div>
                    <div class="form-group">
                      <label>Distance (Warehouse Radius)</label>
                      <div class="multiSelectBlk">
                        <select
                          class="form-control"
                          title=""
                          name="Distance"
                          value={props.model.search.Distance}
                          onChange={props.change}
                        >
                          <option value="5">5 Miles</option>
                          <option value="10">10 Miles</option>
                          <option value="20">20 Miles</option>
                          <option value="30">30 Miles</option>
                          <option value="50">50 Miles</option>
                        </select>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-xs-12 col-sm-6 form-group">
                        <label>From (Start date)</label>
                        <div class="has-feedback">
                          <Flatpickr
                            className="form-control"
                            id="Startdate"
                            name="Startdate"
                            placeholder="From"
                            data-min-date="today"
                            value={props.model.search.Startdate}
                            onChange={(_, str) =>
                              props.onflatpicker("Startdate", str)}
                            onKeyPress={props.keyPress}
                          />
                        </div>
                      </div>
                      <div class="col-xs-12 col-sm-6 form-group">
                        <label>To (End Date)</label>
                        <div class="has-feedback">
                          <Flatpickr
                            className="form-control"
                            id="EndDate"
                            name="EndDate"
                            data-min-date="today"
                            value={props.model.search.EndDate}
                            placeholder="To"
                            onChange={(_, str) =>
                              props.onflatpicker("EndDate", str)}
                            onKeyPress={props.keyPress}
                          />
                        </div>
                      </div>
                    </div>
                    <div class="form-group">
                      <input
                        type="button"
                        class="button button-primary margin-right-2x"
                        value="Search"
                        onClick={() => props.search()}
                      />
                      <input
                        type="button"
                        class={props.model.totalRecords > 0 ? "button button-action" : "button button-gray"}
                        value="Next"
                        style={{ marginLeft: "25%" }}
                        onClick={() => props.next()}
                      />
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

module.exports = find;



