import React from "react";
import Helpers from "../../helpers/utility.helper.js";
import { ShareButtons, ShareCounts, generateShareIcon } from "react-share";

const {
  FacebookShareButton,
  LinkedinShareButton,
  TwitterShareButton
} = ShareButtons;

const FacebookIcon = generateShareIcon("facebook");
const TwitterIcon = generateShareIcon("twitter");
const LinkedinIcon = generateShareIcon("linkedin");

const BlogItem = props => {
  let date = Helpers.GetDateFromString(props.CreatedDate);
  let shareUrl = "http://logistcompare.com/blog/" + props._id;
  let title = props.Name;
  const UrlDescription = props.UrlDescription;
  return (
    <div class="row blogRow">
      <div class="col-sm-1 blog-date wow fadeInLeft">
        <div class="dateWrap">
          <span>{date.getDate()}</span>
          <p> {date.getMonth() + 1}</p>
        </div>
        {/* <div class="dateIcon">
          <span> {date.getMonth() + 1}</span>
        </div> */}
      </div>
      <div class="col-xs-12 col-sm-11 wow fadeInLeft">
        <h3 class="Titleblog">{props.Name}</h3>
        <div class="blogPosted">
          {/* <p>
            Posted by <strong>{props.By}</strong>
          </p> */}
        </div>
        <div class="blogText">
          <div class="blog-list-img">
            <img
              src={props.BlogImage}
              onError={e => {
                e.target.src = "/Content/images/no-image.jpg";
              }}
              class="img-responsive"
            />
          </div>
          <div
            dangerouslySetInnerHTML={{
              __html: props.Description.substring(0, 500) + "..."
            }}
          />
        </div>
        <div class="bottomBlog">
          <a
            href="javascript:void(0)"
            onClick={() => props.ViewBlog(UrlDescription)}
            class="readMore"
          >
            Read more...
          </a>
          <div class="BlogSocialIcon margin-top-4x">
            <FacebookShareButton url={shareUrl} quote={title}>
              <FacebookIcon size={32} round />
            </FacebookShareButton>
            <TwitterShareButton url={shareUrl} title={title}>
              <TwitterIcon size={32} round />
            </TwitterShareButton>
            <LinkedinShareButton
              url={shareUrl}
              title={title}
              windowWidth={750}
              windowHeight={600}
            >
              <LinkedinIcon size={32} round />
            </LinkedinShareButton>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = BlogItem;



