import React from "react";
import VInput from "Components/common/VInput";
import Helper from "../../../helpers/utility.helper.js";
const Bulk = props => {
  const daysRemaining = Helper.getDaysDiffrence(props.State.QuoteValidTill);
  const editLink =
    "/user/quotes/" +
    props.State.QuoteId +
    "?wid=" +
    props.State.WarehouseId +
    "&type=Bulk&action=edit";
  return (
    <form>
      <div id="PalletStorage" class="open-form" style={{ display: "block" }}>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Space Available</label>
            <input
              type="text"
              class="form-control"
              placeholder="220 Sqft"
              value={
                parseFloat(props.State.TotalSpace).toFixed(
                  2
                ) +
                " sq" +
                props.State.WarehouseDetail.CapacityType
              }
              readOnly="readOnly"
            />
          </div>
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Time Available</label>
            <input
              type="text"
              class="form-control"
              placeholder=""
              value={
                Helper.ConvertDateToString(
                  props.State.WarehouseDetail.Bulk.AvailabilityFrom
                ) +
                " - " +
                Helper.SetEndDate(
                  props.State.WarehouseDetail.Bulk.AvailabilityTo
                )
              }
              readOnly="readOnly"
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-12 form-group">
            <label>Goods category</label>
            <input
              type="text"
              class="form-control"
              placeholder="Electronics"
              value={props.State.Bulk.Category}
              readOnly="readOnly"
            />
          </div>
        </div>

        <div class="row Row-10">
          <div class="col-xs-12 form-group">
            <label>Load Description / Additional Information</label>
            <textarea
              class="form-control"
              placeholder="Load Description / Additional Information "
              readOnly="readOnly"
              value={props.State.Bulk.Description}
            />
          </div>
        </div>

        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group ">
            <label>
              Total Space Required <sub>*</sub>
            </label>
            <input
              type="text"
              class="form-control"
              placeholder="320 Sqft"
              readOnly="readOnly"
              value={
                props.State.Bulk.TotalSpace +
                " sq " +
                props.State.Bulk.TotalSpaceType
              }
            />
          </div>
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Total Items</label>
            <input
              type="text"
              class="form-control"
              placeholder="100"
              readOnly="readOnly"
              value={props.State.Bulk.TotalItem}
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>Total Weight (Kgs/Lbs)</label>
            <input
              type="text"
              class="form-control"
              placeholder="500Kgs"
              readOnly="readOnly"
              value={props.State.Bulk.TotalWeight}
            />
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <label>From (Start date)</label>
            <div class="has-feedback">
              <input
                type="text"
                class="form-control"
                placeholder="30/02/2017"
                value={Helper.ConvertDateToString(props.State.Bulk.FromDate)}
                readOnly="readOnly"
              />
              <span class="form-control-feedback">
                <i class="fa fa-calendar" />
              </span>
            </div>
          </div>
          <div class="col-xs-12 col-sm-6 form-group">
            <label>To (End Date)</label>
            <div class="has-feedback">
              <input
                type="text"
                class="form-control"
                placeholder="30/05/2017"
                value={Helper.ConvertDateToString(props.State.Bulk.ToDate)}
                readOnly="readOnly"
              />
              <span class="form-control-feedback">
                <i class="fa fa-calendar" />
              </span>
            </div>
          </div>
        </div>

        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group costBreak">
            <label>Final Cost</label>

            <div class="btn-group show-on-hover">
              <span class="form-control">
                &pound;{props.State.Bulk.CostBreakDown.FinalCost}
              </span>
            </div>
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-6 form-group">
            <div class="form-group return">
              <a href={editLink}>
                <i class="fa fa-arrow-left" aria-hidden="true" />Return to
                bookings form
              </a>
            </div>
          </div>
        </div>
        <div class="row Row-10">
          <div class="col-xs-12 col-sm-12">
            <p class="confirm" style={{ display: "block", marginTop: "-20px" }}>
              *Validity - {daysRemaining} days from booking date
            </p>
          </div>
        </div>
      </div>
    </form>
  );
};
module.exports = Bulk;



