import React from "react";
import VInput from "Components/common/VInput";
import MetaTags from 'react-meta-tags';
const addressDetail = props => {
  return (
    <section className="sbsWarpper">
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div className="wHeight innerWarp">
        <div className="cell bg-theme">
          <div className="infoBlk text-center">
            <span className="imgBlk center-block margin-bottom-8x">
              <img
                src="/Content/images/provider.png"
                alt="provider"
                className="img-responsive"
              />
            </span>
            <h2 className="margin-bottom-6x">List Space details</h2>
            <p>
              After you have submitted the details of the warehouse, this will
              be available to customers through LogistCompare.
            </p>
          </div>
        </div>
        <div className="cell">
          <div className="formWarp">
            <h3 className="margin-bottom-8x">Add Details</h3>
            <div className="form-group">
              <VInput
                className="form-control"
                name="Name"
                placeholder="Provider/Warehouse  name*"
                maxLength="100"
                valueLink={props.validationLink.nameLink}
                type="text"
              />
            </div>
            <div className="form-group">
              <VInput
                className="form-control"
                name="PostCode"
                placeholder="Post code*"
                maxLength="20"
                type="text"
                valueLink={props.validationLink.postCodeLink}
                onBlur={props.addLatLng}
              />
            </div>
            <div className="form-group">
              <VInput
                className="form-control"
                name="City"
                placeholder="City*"
                maxLength="100"
                type="text"
                valueLink={props.validationLink.cityLink}
              />
            </div>
            <div className="form-group">
              {/* <VInput
                className="form-control"
                name="Address"
                placeholder="Address*"
                type="text"
                maxLength="100"
                valueLink={props.validationLink.addressLink}
              /> */}
              <input
                type="text"
                className="form-control"
                name="Address"
                placeholder="Address*"
                value={props.model.Address}
                onChange={props.handleChange}
                ref={props.setRef}
              />
            </div>

            <div className="form-group">
              <VInput
                className="form-control"
                name="Website"
                placeholder="Website"
                maxLength="200"
                type="text"
                valueLink={props.validationLink.websiteLink}
              />
            </div>
            <div className="form-group">
              <label className="ch-checkMark">
                <input
                  type="checkbox"
                  name="Terms"
                  onChange={props.handleChange}
                />
                <span>
                  <a target="_blank" href="/termsandconditions">
                    Approve terms and conditions
                  </a>
                </span>
              </label>
            </div>
            <div className="form-group">
              <input
                className="button button-border button-action button-uppercase button-min-width margin-top-3x margin-right-2x"
                value="Submit"
                type="button"
                onClick={props.handleSubmit}
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
module.exports = addressDetail;



