import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Quotes from "./confirmedquote.js";
import WarehouseService from "Services/warehouse.service.js";
import QuoteService from "Services/quote.service.js";
import ConversionService from "Services/formula.service.js";
import Helper from "../../../helpers/utility.helper.js";
import Link from "valuelink";
import StripeService from "Services/stripe.service.js";
import Loader from "Components/common/loader.js";
import PaymentService from "Services/payment.service.js";
import UserService from "Services/user.service.js";
import MangoService from "Services/mangoPay.service.js";
let stripeLinks = {};

const NameRegex = /^[a-zA-Z\s]*$/;
const NumberRegex = /^\d+$/;
const DOBRegex = /([12]\d{3}-(0[1-9]|1[0-2])-(0[1-9]|[12]\d|3[01]))/;
const costBreakDown = {
  HandlingIn: "",
  HandlingOut: "",
  Storage: "",
  FinalCost: ""
};
export default class ConfirmedQuoteContainer extends Component {
  constructor() {
    super();
    const tokenObj = {
      number: "",
      exp_month: "",
      exp_year: "",
      cvc: ""
    };
    this.state = {
      WarehouseId: "",
      Bulk: {},
      Pallet: {},
      Bulks: [],
      Pallets: [],
      WarehouseDetail: null,
      IsBulk: false,
      isLoading: true,
      ShowSuccessModal: false,
      ShowAlert: false,
      ShowAlertModal: false,
      ModalBody: "",
      CostBreakDown: { ...costBreakDown },
      TotalCostBreakDown: {},
      QuoteValidTill: null,
      ShowModel: false,
      Price: "",
      tokenObj: tokenObj,
      QuoteId: "",
      BirthDate: "",
      CardStatus: false,
      TotalCapacity: 0,
      TotalPalletRequested: 0
    };
    this.onChange = this.onChange.bind(this);
    this.Submit = this.Submit.bind(this);
    this.openPopup = this.openPopup.bind(this);
    this.GetConversion = this.GetConversion.bind(this);
    this.SaveTransactionDetail = this.SaveTransactionDetail.bind(this);
    this.RegisterCard = this.RegisterCard.bind(this);
  }
  componentWillMount() {
    if (this.props.match.params.id) {
      this.getItemList(this.props.match.params.id);
    }
  }
  GetConversion(wareObj) {
    if (wareObj.CapacityType == "Feet") {
      wareObj.Capacity =
        Math.ceil(ConversionService.SqMeterToSqFeet(wareObj.Capacity) * 10) /
        10;
      wareObj.Bulk.TotalSpace = ConversionService.SqMeterToSqFeet(
        wareObj.Bulk.TotalSpace
      );
      wareObj.PendingSpace = ConversionService.SqMeterToSqFeet(
        wareObj.PendingSpace
      );
      wareObj.AvailableSpace = ConversionService.SqMeterToSqFeet(
        wareObj.AvailableSpace
      );
    }
    //bulk conversion
    if (wareObj.Bulk.MaxWidthType == "Feet") {
      wareObj.Bulk.MaxWidth = isNaN(
        ConversionService.MeterToFeet(wareObj.Bulk.MaxWidth)
      )
        ? ""
        : ConversionService.MeterToFeet(wareObj.Bulk.MaxWidth);
    }
    if (wareObj.Bulk.MaxHeightType == "Feet") {
      wareObj.Bulk.MaxHeight = isNaN(
        ConversionService.MeterToFeet(wareObj.Bulk.MaxHeight)
      )
        ? ""
        : ConversionService.MeterToFeet(wareObj.Bulk.MaxHeight);
    }
    if (wareObj.Bulk.MaxLengthType == "Feet") {
      wareObj.Bulk.MaxLength = isNaN(
        ConversionService.MeterToFeet(wareObj.Bulk.MaxLength)
      )
        ? ""
        : ConversionService.MeterToFeet(wareObj.Bulk.MaxLength);
    }
    if (wareObj.Bulk.MaxWeightType == "Lbs") {
      wareObj.Bulk.MaxWeight = ConversionService.KgtoLb(wareObj.Bulk.MaxWeight);
    }
    //pallet conversion
    if (wareObj.Pallet.MaxHeightType == "Feet") {
      wareObj.Pallet.MaxHeight = isNaN(
        ConversionService.MeterToFeet(wareObj.Pallet.MaxHeight)
      )
        ? ""
        : ConversionService.MeterToFeet(wareObj.Pallet.MaxHeight);
    }

    if (wareObj.Pallet.MaxWeightType == "Lbs") {
      wareObj.Pallet.MaxWeight = ConversionService.KgtoLb(
        wareObj.Pallet.MaxWeight
      );
    }

    return wareObj;
  }
  valid() {
    const errorObjects = Object.values(stripeLinks).filter(x => !!x.error);
    if (errorObjects.length) {
      const hasParent = !!errorObjects[0].parent;
      if (hasParent) {
        const parent = errorObjects[0].parent.key,
          key = errorObjects[0].key;
        const obj = this.state[parent];
        obj[key] = undefined;
        this.setState({ [parent]: obj });
      } else {
        const key = errorObjects[0].key;
        //.map(x => x.key)[0];
        this.setState({ [key]: undefined });
      }

      return false;
    } else return true;
  }

  getItemList(id) {
    let totalPalletRequested = 0;
    QuoteService.GetItems(id).then(response => {
      response.data.quote.Pallets.map((x, i) => {
        x = this.ConvertStandardUnitsToPalletUnits(x);
        totalPalletRequested = totalPalletRequested + parseInt(x.TotalPallets);
      });
      response.data.quote.Bulks.map((x, i) => {
        x = this.ConvertStandardUnitsToBulkUnits(x);
      });
      WarehouseService.Get(response.data.quote.WarehouseId).then(warehouse => {
        let totalSpaceAvailable = 0;
        let wDetail = this.GetConversion(warehouse.data);
        if (response.data.quote.StorageType == "Pallet") {
          let warehouseAvailableSpace =
            parseFloat(wDetail.Pallet.Total) *
            parseFloat(wDetail.Pallet.Type.Value);
          warehouseAvailableSpace =
            wDetail.CapacityType == "Feet"
              ? ConversionService.SqMeterToSqFeet(warehouseAvailableSpace)
              : warehouseAvailableSpace;
          totalSpaceAvailable =
            warehouseAvailableSpace -
            parseFloat(response.data.quote.TotalCapacity);
        } else {
          let warehouseAvailableSpace = parseFloat(wDetail.Bulk.TotalSpace);
          totalSpaceAvailable =
            warehouseAvailableSpace -
            parseFloat(response.data.quote.TotalCapacity);
        }
        this.setState({
          WarehouseDetail: wDetail,
          WarehouseId: response.data.quote.WarehouseId,
          isLoading: false,
          Bulks: response.data.quote.Bulks,
          Pallets: response.data.quote.Pallets,
          QuoteId: id,
          IsBulk: response.data.quote.StorageType == "Pallet" ? false : true,
          Pallet:
            response.data.quote.StorageType == "Pallet"
              ? response.data.quote.Pallets[0]
              : {},
          Bulk:
            response.data.quote.StorageType == "Bulk"
              ? response.data.quote.Bulks[0]
              : {},
          UserId: response.data.quote.UserId,
          TotalCostBreakDown: response.data.quote.TotalCostBreakDown,
          QuoteValidTill: response.data.quote.QuoteValidTill,
          Price: response.data.quote.TotalCostBreakDown.FinalCost,
          CardStatus: response.data.CardStatus,
          TotalCapacity: response.data.quote.TotalCapacity,
          TotalSpace: totalSpaceAvailable,
          TotalPalletRequested:
            response.data.quote.StorageType == "Pallet"
              ? totalPalletRequested
              : 0
        });
      });
    });
  }
  getWareHouseDetail(warehouseId) { }

  /*-------Pallet Quotes -------------*/

  editPallet(index) {
    let selectedPallet = this.state.Pallets[index];
    if (selectedPallet.CostBreakDown.hasOwnProperty("HandlingIn")) {
      this.setState({
        Pallet: selectedPallet,
        CostBreakDown: selectedPallet.CostBreakDown
      });
    } else {
      this.setState({
        Pallet: selectedPallet,
        CostBreakDown: { ...costBreakDown }
      });
    }
  }
  ConvertPalletUnitsToStandardUnits(palletQuote) {
    if (palletQuote.WeightType == "Lbs") {
      palletQuote.TotalWeight =
        palletQuote.TotalWeight == null || palletQuote.TotalWeight == ""
          ? ""
          : ConversionService.PoundsToKg(palletQuote.TotalWeight);
      palletQuote.WeightPerPellet =
        palletQuote.WeightPerPellet == null || palletQuote.WeightPerPellet == ""
          ? ""
          : ConversionService.PoundsToKg(palletQuote.WeightPerPellet);
    }
    if (this.state.WarehouseDetail.CapacityType.toLowerCase() == "feet") {
      palletQuote.TotalSpace = ConversionService.SqFeetToSqMeter(
        palletQuote.TotalSpace
      );
    }
    if (palletQuote.HeightType.toLowerCase() == "feet") {
      palletQuote.HeigthPerPellet = palletQuote.HeigthPerPellet =
        palletQuote.HeigthPerPellet == null || palletQuote.HeigthPerPellet == ""
          ? ""
          : ConversionService.FeetToMeter(palletQuote.HeigthPerPellet);
    }
    return palletQuote;
  }
  ConvertStandardUnitsToPalletUnits(palletQuote) {
    if (palletQuote.WeightType == "Lbs") {
      palletQuote.TotalWeight =
        palletQuote.TotalWeight == null || palletQuote.TotalWeight == ""
          ? ""
          : ConversionService.KgToPounds(palletQuote.TotalWeight);
      palletQuote.WeightPerPellet =
        palletQuote.WeightPerPellet == null || palletQuote.WeightPerPellet == ""
          ? ""
          : ConversionService.KgToPounds(palletQuote.WeightPerPellet);
    }
    if (palletQuote.TotalSpaceType.toLowerCase() == "feet") {
      palletQuote.TotalSpace = ConversionService.SqMeterToSqFeet(
        palletQuote.TotalSpace
      );
    }
    if (palletQuote.HeightType.toLowerCase() == "feet") {
      palletQuote.HeigthPerPellet =
        palletQuote.HeigthPerPellet == null || palletQuote.HeigthPerPellet == ""
          ? ""
          : ConversionService.MeterToFeet(palletQuote.HeigthPerPellet);
    }
    return palletQuote;
  }
  /*-------------------end------------------------ */

  /*--------------Bulk------------------- */

  editBulk(index) {
    let selectedBulk = this.state.Bulks[index];
    if (selectedBulk.CostBreakDown.hasOwnProperty("HandlingIn")) {
      this.setState({
        Bulk: selectedBulk,
        CostBreakDown: selectedBulk.CostBreakDown
      });
    } else {
      this.setState({
        Bulk: selectedBulk,
        CostBreakDown: { ...costBreakDown }
      });
    }
  }
  ConvertBulkUnitsToStandardUnits(bulkQuote) {
    if (bulkQuote.WeightType == "Lbs") {
      bulkQuote.TotalWeight =
        bulkQuote.TotalWeight == "" || bulkQuote.TotalWeight == null
          ? ""
          : ConversionService.PoundsToKg(bulkQuote.TotalWeight);
      bulkQuote.WeightPerItem =
        bulkQuote.WeightPerItem == "" || bulkQuote.WeightPerItem == null
          ? ""
          : ConversionService.PoundsToKg(bulkQuote.WeightPerItem);
    }
    if (bulkQuote.TotalSpaceType.toLowerCase() == "feet") {
      bulkQuote.TotalSpace = ConversionService.SqFeetToSqMeter(
        bulkQuote.TotalSpace
      );
    }
    if (bulkQuote.HeightType.toLowerCase() == "feet") {
      bulkQuote.Height =
        bulkQuote.Height == "" || bulkQuote.Height == null
          ? ""
          : ConversionService.FeetToMeter(bulkQuote.Height);
    }
    if (bulkQuote.LengthType.toLowerCase() == "feet") {
      bulkQuote.Length = ConversionService.FeetToMeter(bulkQuote.Length);
    }
    if (bulkQuote.WidthType.toLowerCase() == "feet") {
      bulkQuote.Width = ConversionService.FeetToMeter(bulkQuote.Width);
    }
    return bulkQuote;
  }
  ConvertStandardUnitsToBulkUnits(bulkQuote) {
    if (bulkQuote.WeightType == "Lbs") {
      bulkQuote.TotalWeight =
        bulkQuote.TotalWeight == "" || bulkQuote.TotalWeight == null
          ? ""
          : ConversionService.KgToPounds(bulkQuote.TotalWeight);
      bulkQuote.WeightPerItem =
        bulkQuote.WeightPerItem == "" || bulkQuote.WeightPerItem == null
          ? ""
          : ConversionService.KgToPounds(bulkQuote.WeightPerItem);
    }
    if (bulkQuote.TotalSpaceType.toLowerCase() == "feet") {
      bulkQuote.TotalSpace = ConversionService.SqMeterToSqFeet(
        bulkQuote.TotalSpace
      );
    }
    if (bulkQuote.HeightType.toLowerCase() == "feet") {
      bulkQuote.Height =
        bulkQuote.Height == "" || bulkQuote.Height == null
          ? ""
          : ConversionService.MeterToFeet(bulkQuote.Height);
    }
    if (bulkQuote.LengthType.toLowerCase() == "feet") {
      bulkQuote.Length = ConversionService.MeterToFeet(bulkQuote.Length);
    }
    if (bulkQuote.WidthType.toLowerCase() == "feet") {
      bulkQuote.Width = ConversionService.MeterToFeet(bulkQuote.Width);
    }
    return bulkQuote;
  }
  /*-------------end--------------------- */

  onChange(e) {
    const { name, value } = e.target;

    this.setState({
      tokenObj: Object.assign({}, this.state.tokenObj, { [name]: value })
    });
  }

  openPopup() {
    if (!this.state.CardStatus)
      this.setState({ ShowModel: true, ShowAlertModal: false });
    else {
      let capacityRequested = this.state.TotalCapacity;
      if (this.state.WarehouseDetail.CapacityType.toLowerCase() == "feet") {
        capacityRequested = ConversionService.SqFeetToSqMeter(
          this.state.TotalCapacity
        );
      }
      const type = "Quotes";
      let paymentDetail = {
        cardDetail: this.state.tokenObj,
        Amount: parseFloat(this.state.Price),
        PayeeId: this.state.WarehouseDetail.UserId,
        QuoteId: this.state.QuoteId,
        BirthDate: this.state.BirthDate,
        QuoteValidDate: this.state.QuoteValidTill,
        WarehouseId: this.state.WarehouseId,
        IsBulk: this.state.IsBulk,
        CapacityRequested: capacityRequested,
        TotalPalletRequested: this.state.IsBulk
          ? 0
          : this.state.TotalPalletRequested
      };
      let currentDate = new Date();
      currentDate.setHours(0, 0, 0, 0);
      let validDate = new Date(paymentDetail.QuoteValidDate);
      validDate.setHours(0, 0, 0, 0);
      if (validDate < currentDate) {
        this.setState({
          ShowAlert: true,
          ShowModel: false,
          ModalBody: "Quote validity expired"
        });
        return false;
      }
      this.setState({ isLoading: true });
      let userDetail = Helper.DecodeToken(localStorage.getItem("userinfo"));
      PaymentService.CheckValidityandSpace(paymentDetail).then(response => {
        if (!response.data.Status) {
          this.setState({
            ShowAlert: true,
            ShowModel: false,
            ModalBody: response.data.Message,
            isLoading: false
          });
          return false;
        }
        UserService.GetUserMangoDetail(userDetail.Id).then(user => {
          if (user.data.status) {
            this.SaveTransactionDetail(userDetail.Id, paymentDetail);
          }
        });
      });

      // PaymentService.CreatePayment(paymentDetail)
      //   .then(response => {
      //     if (!response.data.Status) {
      //       this.setState({
      //         ShowAlert: true,
      //         ShowModel: false,
      //         ModalBody: response.data.Message,
      //         isLoading: false
      //       });
      //     } else {
      //       this.setState({
      //         ShowModel: false
      //       });
      //       QuoteService.completePayment(this.state.QuoteId)
      //         .then((response, err) => {
      //           // this.props.history.push("/user/myquotes");
      //           QuoteService.BookSpace(this.state.QuoteId)
      //             .then((response1, err1) => {

      //             })
      //             .catch(err => console.log(err1));
      //           this.setState({
      //             ShowSuccessModal: true,
      //             ModalBody:
      //             "Payment for this booking has been confirmed thank you.",
      //             isLoading: false
      //           });

      //         })
      //         .catch(err => console.log(err));
      //     }
      //   })
      //   .catch(error => {
      //     alert(error);
      //   });
    }
  }
  closePopup() {
    this.setState({ ShowModel: false });
  }
  SaveTransactionDetail(userId, paymentDetail) {
    MangoService.SaveTransactionDetail(userId, paymentDetail).then(
      transaction => {
        this.setState({
          ShowModel: false
        });
        QuoteService.completePayment(this.state.QuoteId)
          .then((response, err) => {
            // this.props.history.push("/user/myquotes");
            QuoteService.BookSpace(this.state.QuoteId)
              .then((response1, err1) => { })
              .catch(err => console.log(err1));
            this.setState({
              ShowSuccessModal: true,
              ModalBody:
                "Payment for this booking has been confirmed thank you.",
              isLoading: false
            });
          })
          .catch(err => console.log(err));
      }
    );
  }
  RegisterCard(userDetail, mangoDetail, paymentDetail) {
    MangoService.RegisterCard(mangoDetail.MangoUserId).then(response => {
      MangoService.updateCardDetails(
        mangoDetail.MangoUserId,
        paymentDetail,
        response.data.registrationData
      )
        .then(data => {
          if (data.Status == "VALIDATED") {
            MangoService.CreatePreAuth(
              userDetail.Id,
              data.CardId,
              paymentDetail
            ).then(paymentResponse => {
              if (paymentResponse.data.Status) {
                paymentDetail.Amount = parseFloat(paymentDetail.Amount) - 0.1;
                UserService.UpdateMangoDetails(
                  userDetail,
                  mangoDetail.MangoUserId,
                  data.CardId
                ).then(user => {
                  if (user.data.Status) {
                    this.SaveTransactionDetail(userDetail.Id, paymentDetail);
                  }
                });
              } else {
                this.setState({
                  ShowAlert: true,
                  ShowModel: false,
                  ModalBody: paymentResponse.data.Message,
                  isLoading: false
                });
              }
            });
          } else {
            let errorMessage = "";
            let errorCode = data.RegistrationData.replace(
              "errorCode=",
              ""
            ).trim();
            console.log(errorCode);
            switch (errorCode) {
              case "105202":
              case "02625":
                errorMessage = "Invalid card number";
                break;
              case "02626":
                errorMessage = "Please enter a valid date";
                break;
              case "02627":
                errorMessage = "Please enter a valid CVC number";
                break;
              case "02624":
                errorMessage = "Card Expired,Please try with other card";
                break;
              default:
                errorMessage = "Internal Server Error,Please try again";
                break;
            }
            this.setState({
              ShowAlert: true,
              ShowModel: false,
              ModalBody: errorMessage,
              isLoading: false
            });
            return false;
          }
        })
        .catch(err => {
          let errorMessage = "";
          let errorCode = err.ResultCode;
          console.log(err.ResultCode);
          switch (errorCode) {
            case "105202":
            case "02625":
              errorMessage = "Invalid card number";
              break;
            case "02626":
            case "105203":
              errorMessage = "Please enter a valid date";
              break;
            case "105204":
            case "02627":
              errorMessage = "Please enter a valid CVC number";
              break;
            case "02624":
            case "02627":
              errorMessage = "Card Expired,Please try with other card";
              break;
            default:
              errorMessage = "Internal Server Error,Please try again";
              break;
          }
          this.setState({
            ShowAlert: true,
            ShowModel: false,
            ModalBody: errorMessage,
            isLoading: false
          });
          return false;
        });
    });
  }
  Submit(price) {
    if (this.valid()) {
      let capacityRequested = this.state.TotalCapacity;
      if (this.state.WarehouseDetail.CapacityType.toLowerCase() == "feet") {
        capacityRequested = ConversionService.SqFeetToSqMeter(
          this.state.TotalCapacity
        );
      }
      let paymentDetail = {
        cardDetail: this.state.tokenObj,
        Amount: parseFloat(this.state.Price),
        PayeeId: this.state.WarehouseDetail.UserId,
        QuoteId: this.state.QuoteId,
        BirthDate: this.state.BirthDate,
        QuoteValidDate: this.state.QuoteValidTill,
        WarehouseId: this.state.WarehouseId,
        IsBulk: this.state.IsBulk,
        CapacityRequested: capacityRequested,
        TotalPalletRequested: this.state.IsBulk
          ? 0
          : this.state.TotalPalletRequested
      };
      let currentDate = new Date();
      currentDate.setHours(0, 0, 0, 0);
      let validDate = new Date(paymentDetail.QuoteValidDate);
      validDate.setHours(0, 0, 0, 0);
      if (validDate < currentDate) {
        this.setState({
          ShowAlert: true,
          ShowModel: false,
          ModalBody: "Quote validity expired"
        });
        return false;
      }
      this.setState({ isLoading: true });
      let userDetail = Helper.DecodeToken(localStorage.getItem("userinfo"));
      PaymentService.CheckValidityandSpace(paymentDetail).then(response => {
        if (!response.data.Status) {
          this.setState({
            ShowAlert: true,
            ShowModel: false,
            ModalBody: response.data.Message,
            isLoading: false
          });
          return false;
        }
        UserService.GetUserMangoDetail(userDetail.Id).then(user => {
          if (user.data.status) {
            this.RegisterCard(userDetail, user.data.MangoDetail, paymentDetail);
          } else {
            MangoService.CreateNaturalUser(userDetail, paymentDetail).then(
              response => {
                if (response.data.status) {
                  this.RegisterCard(
                    userDetail,
                    response.data.MangoDetail,
                    paymentDetail
                  );
                }
              }
            );
          }
        });
      });
      // PaymentService.CreatePayment(paymentDetail)
      //   .then(response => {
      //     if (!response.data.Status) {
      //       this.setState({
      //         ShowAlert: true,
      //         ShowModel: false,
      //         ModalBody: response.data.Message,
      //         isLoading: false
      //       });
      //     } else {
      //       this.setState({
      //         ShowModel: false
      //       });
      //       QuoteService.completePayment(this.state.QuoteId)
      //         .then((response, err) => {
      //           QuoteService.BookSpace(this.state.QuoteId)
      //             .then((response1, err1) => {

      //             })
      //             .catch(err => console.log(err1));
      //           this.setState({
      //             ShowSuccessModal: true,
      //             ModalBody:
      //             "Payment for this booking has been confirmed thank you.",
      //             isLoading: false
      //           });

      //         })
      //         .catch(err => console.log(err));
      //     }
      //   })
      //   .catch(error => {
      //     alert(error);
      //   });
    }
  }

  CloseModal() {
    this.setState({
      ShowAlert: false,
      ShowAlertModal: false,
      ModalBody: "",
      ShowSuccessModal: false
    });
  }
  render() {
    stripeLinks = {
      numberLink: Link.state(this, "tokenObj")
        .at("number")
        .check(x => x, "card number is required")
        .check(x => NumberRegex.test(x), "Enter numbers only")
        .check(x => x.trim(), "Space not allowed"),
      monthLink: Link.state(this, "tokenObj")
        .at("exp_month")
        .check(x => x, "Month is required")
        .check(x => NumberRegex.test(x), "Enter number only"),
      yearLink: Link.state(this, "tokenObj")
        .at("exp_year")
        .check(x => x, "Year is required")
        .check(x => NumberRegex.test(x), "Enter number only"),
      cvcLink: Link.state(this, "tokenObj")
        .at("cvc")
        .check(x => x, "CVC is required")
        .check(x => NumberRegex.test(x), "Enter number only"),
      birthDateLink: Link.state(this, "BirthDate")
        .check(x => x, "DOB is required")
        .check(x => DOBRegex.test(x), "Please enter a valid DOB (yyyy-mm-dd)")
    };
    const quoteObject = {
      State: this.state,
      model: this.state,
      onChange: this.onChange,
      Submit: this.Submit,
      EditPallet: this.editPallet.bind(this),
      EditBulk: this.editBulk.bind(this),
      CloseModal: this.CloseModal.bind(this),
      openPopup: this.openPopup,
      closePopup: this.closePopup.bind(this),
      onClose: this.CloseModal.bind(this),
      stripeLinks
    };
    if (this.state.isLoading) return <Loader />;
    else return <Quotes {...quoteObject} />;
  }
}



