import React from "react";
import BulkItem from "./bulkItem.js";
import PelletItem from "./pelletItem.js";
const QuoteList = props => {
  let NoResult = true;
  let quoteList = null;
  let quoteButton = null;
  if (props.State.IsBulk) {
    if (props.State.Bulks.length > 0) {
      NoResult = false;
      quoteList = props.State.Bulks.map((x, i) => {
        return (
          <BulkItem
            EditBulk={props.EditBulk}
            deleteBulk={props.deleteBulk}
            key={i}
            index={i}
            bid={props.State.Bulk._id}
            {...x}
          />
        );
      });
    }
  } else {
    if (props.State.Pallets.length > 0) {
      NoResult = false;
      quoteList = props.State.Pallets.map((x, i) => {
        return (
          <PelletItem
            EditPallet={props.EditPallet}
            deletePallet={props.deletePallet}
            key={i}
            index={i}
            pid={props.State.Pallet._id}
            {...x}
          />
        );
      });
    }
  }
  if (quoteList) {
    quoteButton = (
      <input
        type="button"
        class="button button-primary margin-top-4x margin-bottom-4x"
        value={props.State.Action == "edit" ? "Quote Again" : "Get Quote"}
        onClick={props.getQuotes}
      />
    );
  }
  return (
    <div class="cell">
      <div class="bookingFormWarp">
        <h2 class="h2Title">Booking Form</h2>
        {/*<!-- // If No Booking yet added in a list then this div will show // -->*/}
        <div
          class="noBooking text-center margin-bottom-6x"
          style={{ display: NoResult ? "block" : "none" }}
        >
          <i class="icon-plus-circle" />
          <h3>No categories added</h3>
        </div>
        {/*<!-- // If No Booking yet added in a list then this div will show // -->*/}
        {quoteList}
        {quoteButton}
        {/*<!-- the days should be auto changed from 30 to 10 or 45 this is for developer note Thanks -->*/}
        {/*onClick="location.href='quotaion-form.html';"*/}
      </div>
    </div>
  );
};
module.exports = QuoteList;



