import React from "react";
import Ratings from "./reviewList.js";
import Rating from "Components/common/starRating.js";
const reviewModal = props => {
  return (
    <div class="modal fade modalWarpper wDetailModal" id="viewRatingModal">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h4>
              {props.model.Detail.Name}
            </h4>
            <button type="button" class="close" data-dismiss="modal">
              <i class="icon-close ico-t2" />
            </button>
          </div>
          <div class="modal-body">
            <div class="ratingBlk margin-bottom-6x">
              <label class="lbl">Rating</label>
              <Rating input={props.model.Detail.AvgReviews} />
            </div>
            <div class="row">
              <div class="col-xs-12">
                <Ratings {...props} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default reviewModal;



