import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Profile from "./profile.js";
import UserService from "../../services/user.service.js";
import Helper from "Helpers/utility.helper.js";
import Modal from "./profile.modal.js";
import Link from "valuelink";

const NameRegex = /^[a-zA-Z]*$/;
let links = {};
export default class ViewProfileContainer extends Component {
  constructor() {
    super();

    let user = Helper.DecodeToken(localStorage.getItem("userinfo"));
    this.state = {
      FirstName: user.FirstName,
      LastName: user.LastName,
      Email: user.Email,
      ProfilePicture: user.ProfilePicture,
      UserId: user.Id,
      isEdit: false,
      ShowSuccessModal: false,
      ShowAlertModal: false
    };
  }
  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  submitProfile() {
    if (this.valid()) {
      UserService.UpdateProfile(this.state)
        .then(response => {
          localStorage.setItem("userinfo", response.data.accesstoken);
          this.setState({
            isEdit: false
          });
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
  editprofile() {
    this.setState({
      isEdit: true
    });
  }
  cancelEdit() {
    this.setState({
      isEdit: false
    });
  }
  uploadImage(e) {
    const formData = new FormData();
    const { files, name } = e.target;
    if (
      files[0].type.includes("image") &&
      !files[0].type.includes("ico") &&
      !files[0].type.includes("gif") &&
      !files[0].type.includes("svg")
    ) {
      formData.append("image", files[0]);
      UserService.UploadImage(formData)
        .then(response => {
          this.setState({
            ProfilePicture: response.data
          });
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
  render() {
    links = {
      firstNameLink: Link.state(this, "FirstName")
        .check(x => x, "First Name is required")
        .check(x => NameRegex.test(x), "Special characters not allowed"),
      lastNameLink: Link.state(this, "LastName")
        .check(x => x, "LastName is required")
        .check(x => NameRegex.test(x), "Special characters not allowed")
    };
    const userProfile = {
      userDetail: this.state,
      isEdit: this.state.isEdit,
      onEdit: this.editprofile.bind(this),
      onSubmit: this.submitProfile.bind(this),
      onCancel: this.cancelEdit.bind(this),
      onChange: this.handleChange.bind(this),
      UploadImage: this.uploadImage.bind(this),
      links
    };
    const modalObject = {};
    return (
      <div>
        <Profile {...userProfile} />
        <Modal {...modalObject} />
      </div>
    );
  }
}



