import React from "react";
import WarehouseService from "Services/warehouse.service.js";
import Detail from "./detail.js";
import ConversionService from "Services/formula.service.js";

const viewModel = {
  Name: "",
  Capacity: "",
  CapacityType: "",
  Address: "",
  City: "",
  PostCode: "",
  Location: {
    type: "Point",
    coordinates: []
  },
  Website: "",
  Starthrs: "",
  Endhrs: "",
  About: "",
  StorageType: "Pallet",
  Services: {},
  WarehouseType: {},
  Security: {},
  Images: [],
  PendingSpace: "",
  UserId: "",
  Bulk: {
    MaxLength: "",
    MaxLengthType: "",
    MaxWidth: "",
    MaxWidthType: "",
    MaxWeight: "",
    MaxWeightType: "",
    MaxHeight: "",
    MaxHeightType: "",
    AvailabilityFrom: "",
    AvailabilityTo: "",
    ApxPrice: "",
    ApxPriceType: "",
    TotalSpace: "",
    Description: ""
  },
  Pallet: {
    Type: { Name: "", Value: "" },
    Total: "",
    MaxHeight: "",
    MaxHeightType: "",
    ApxPrice: "",
    AvailabilityTo: "",
    AvailabilityFrom: "",
    Images: ""
  }
};
class warehouseDetail extends React.Component {
  constructor() {
    super();
    this.state = {
      Detail: { ...viewModel },
      Reviews: [],
      // RatingModel: {
      //   Id: "",
      //   Rating: "",
      //   Description: ""
      // },
      center: {
        lat: -25.363882,
        lng: 131.044922
      }
    };
  }

  componentWillMount() {
    WarehouseService.Get(this.props.match.params.id)
      .then(response => {
        this.getReview(this.props.match.params.id);
        let warehouseDetail = response.data;
        warehouseDetail.Capacity =
          warehouseDetail.CapacityType == "Feet"
            ? ConversionService.SqMeterToSqFeet(warehouseDetail.Capacity)
            : warehouseDetail.Capacity;
        this.setState({
          Detail: { ...warehouseDetail },
          center: {
            lat: response.data.Location.coordinates[0],
            lng: response.data.Location.coordinates[1]
          }
        });
      })
      .catch(error => {
        console.log(error);
      });
  }
  getReview(id) {
    let reviews = [];
    WarehouseService.GetReviews(id)
      .then(respose => {
        this.setState({ Reviews: respose.data });
      })
      .catch(err => {
        console.log(err);
      });
  }
  componentDidUpdate() {
    const syncMain = $(".slickSyncMain"),
      syncThumbs = $(".slickSyncThumbs");
    syncMain.not(".slick-initialized").slick({
      slidesToShow: 1,
      slidesToScroll: 1,
      arrows: false,
      dots: false,
      infinite: false,
      fade: false,
      adaptiveHeight: true,
      asNavFor: ".slickSyncThumbs"
    });
    syncThumbs.not(".slick-initialized").slick({
      slidesToShow: 5,
      slidesToScroll: 1,
      asNavFor: ".slickSyncMain",
      dots: false,
      infinite: false,
      adaptiveHeight: true,
      focusOnSelect: true,
      nextArrow:
        '<a href="javascript:void(0);" class="slick-next"><i class="fa fa-angle-right"></i></a>',
      prevArrow:
        '<a href="javascript:void(0);" class="slick-prev"><i class="fa fa-angle-left"></i></a>',
      responsive: [
        {
          breakpoint: 500,
          settings: {
            slidesToShow: 3
          }
        },
        {
          breakpoint: 400,
          settings: {
            slidesToShow: 2
          }
        }
      ]
    });
  }
  render() {
    const model = {
      model: this.state
    };
    return <Detail {...model} />;
  }
}
module.exports = warehouseDetail;



