import React from "react";
import VInput from "Components/common/VInput";
import { Select, TextArea } from "valuelink/tags";
import Flatpickr from "react-flatpickr";
import Helper from "../../helpers/utility.helper.js";

const Bulk = props => {
  let button = (
    <input
      type="button"
      class="button button-primary margin-top-1x margin-bottom-1x"
      value="Add Category"
      onClick={props.addBulk}
    />
  );
  if (props.State.Bulk._id) {
    button = (
      <input
        type="button"
        class="button button-primary margin-top-1x margin-bottom-1x"
        value="Update Category"
        onClick={() => props.updateBulk(props.State.Bulk._id)}
      />
    );
  }
  return (
    <div class="tab-pane fade in active" id="BulkSection">
      <div class="formWarpInner bulkForm">
        <form>
          <div
            id="PalletStorage"
            class="open-form"
            style={{ display: "block" }}
          >
            <div class="row Row-10">
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Space Available</label>
                <input
                  type="text"
                  class="form-control border-green"
                  placeholder=""
                  value={
                    parseFloat(props.State.TotalSpace).toFixed(2) +
                    " sq " +
                    props.State.WarehouseDetail.CapacityType
                  }
                  readOnly="readonly"
                />
              </div>
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Time Available</label>
                <input
                  type="text"
                  class="form-control border-green"
                  placeholder=""
                  value={
                    Helper.ConvertFromDateToString(
                      props.State.WarehouseDetail.Bulk.AvailabilityFrom
                    ) +
                    " - " +
                    Helper.ConvertToDateToString(
                      props.State.WarehouseDetail.Bulk.AvailabilityTo
                    )
                  }
                  readOnly="readonly"
                />
              </div>
            </div>
            <div class="row Row-10">
              <div class="col-xs-12 col-sm-12 form-group">
                <label>
                  Goods category<sub>*</sub>
                </label>

                <div class="multiSelectBlk">
                  <Select
                    class="multiSelectDrop"
                    title=""
                    valueLink={props.bulkLinks.bulkCategoryLink}
                  >
                    <option value="Textile, Apparel and Luxury Goods">
                      Textile, Apparel and Luxury Goods
                    </option>
                    <option value="Household products">
                      Household products
                    </option>
                    <option value="Electronics">Electronics</option>
                    <option value="Leisure products">Leisure products</option>
                    <option value="Paper and forest products">
                      Paper and forest products
                    </option>
                    <option value="Footwear">Footwear</option>
                    <option value="Furniture">Furniture</option>
                    <option value="Plastic goods">Plastic goods</option>
                    <option value="Rubber products">Rubber products</option>
                    <option value="Toys">Toys</option>
                    <option value="Cosmetics">Cosmetics</option>
                    <option value="Metalworking products">
                      Metalworking products
                    </option>
                    <option value="Beverages">Beverages</option>
                    <option value="Marketing material">
                      Marketing material
                    </option>
                    <option value="Data archive material">
                      Data archive material
                    </option>
                  </Select>
                </div>
              </div>
            </div>

            <div class="row Row-10">
              <div class="col-xs-12 form-group">
                <label>Load Description / Additional Information</label>
                <TextArea
                  class="form-control"
                  placeholder=""
                  valueLink={props.bulkLinks.bulkDescriptionLink}
                />
              </div>
            </div>
            <div class="row Row-10">
              <div class="col-xs-12 col-sm-6 form-group">
                <label>
                  From (Start date)<sub>*</sub>
                </label>
                <div class="has-feedback">
                  <Flatpickr
                    type="text"
                    class="form-control"
                    placeholder=""
                    value={props.State.Bulk.FromDate}
                    data-min-date="today"
                    onChange={(_, str) =>
                      props.onbulkflatPicker("FromDate", str)
                    }
                  />
                  <span class="form-control-feedback">
                    <i class="fa fa-calendar" />
                  </span>
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 form-group">
                <label>
                  To (End Date)<sub>*</sub>
                </label>
                <div class="has-feedback">
                  <Flatpickr
                    type="text"
                    class="form-control"
                    placeholder=""
                    value={props.State.Bulk.ToDate}
                    data-min-date="today"
                    onChange={(_, str) => props.onbulkflatPicker("ToDate", str)}
                  />
                  <span class="form-control-feedback">
                    <i class="fa fa-calendar" />
                  </span>
                </div>
              </div>
            </div>
            <div class="row Row-10">
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Length (Meters/feet)</label>
                <div class="row Row-10">
                  <div class="col-xs-6 col-sm-6 form-group">
                    <VInput
                      type="text"
                      class="form-control"
                      placeholder=""
                      name="Length"
                      valueLink={props.bulkLinks.bulkLengthLink}
                      onChange={props.bulkChange}
                      onKeyPress={props.onkeyPress}
                    />
                  </div>
                  <div class="col-xs-6 col-sm-6 form-group">
                    <div class="multiSelectBlk">
                      <select
                        class="multiSelectDrop"
                        name="LengthType"
                        title=""
                        value={props.State.Bulk.LengthType}
                        disabled
                      >
                        <option value="meter">Meters</option>
                        <option value="feet">feet</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Height (Meters/feet)</label>
                <div class="row Row-10">
                  <div class="col-xs-6 col-sm-6 form-group">
                    <VInput
                      type="text"
                      class="form-control"
                      placeholder=""
                      valueLink={props.bulkLinks.bulkHeightLink}
                      onKeyPress={props.onkeyPress}
                    />
                  </div>
                  <div class="col-xs-6 col-sm-6 form-group">
                    <div class="multiSelectBlk">
                      <select
                        class="multiSelectDrop"
                        title=""
                        name="HeightType"
                        value={props.State.Bulk.HeightType}
                        disabled
                      >
                        <option value="meter">Meters</option>
                        <option value="feet">feet</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row Row-10">
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Width (Meters/feet)</label>
                <div class="row Row-10">
                  <div class="col-xs-6 col-sm-6 form-group">
                    <VInput
                      type="text"
                      class="form-control"
                      name="Width"
                      placeholder=""
                      valueLink={props.bulkLinks.bulkWidthLink}
                      onChange={props.bulkChange}
                      onKeyPress={props.onkeyPress}
                    />
                  </div>
                  <div class="col-xs-6 col-sm-6 form-group">
                    <div class="multiSelectBlk">
                      <select
                        class="multiSelectDrop"
                        name="WidthType"
                        title=""
                        value={props.State.Bulk.WidthType}
                        disabled
                      >
                        <option value="meter">Meters</option>
                        <option value="feet">feet</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Weight (Kgs/Lbs)</label>
                <div class="row Row-10">
                  <div class="col-xs-6 col-sm-6 form-group">
                    <VInput
                      type="text"
                      class="form-control"
                      name="WeightPerItem"
                      placeholder=""
                      valueLink={props.bulkLinks.bulkWeightLink}
                      onChange={props.bulkChange}
                      onKeyPress={props.onkeyPress}
                    />
                  </div>
                  <div class="col-xs-6 col-sm-6 form-group">
                    <div class="multiSelectBlk">
                      <select
                        class="multiSelectDrop"
                        name="WeightType"
                        title=""
                        value={props.State.Bulk.WeightType}
                        onChange={props.bulkChange}
                        disabled
                      >
                        <option value="Kgs">Kgs</option>
                        <option value="Lbs">Lbs</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="row Row-10">
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Items</label>
                <VInput
                  type="text"
                  class="form-control"
                  placeholder=""
                  name="TotalItems"
                  valueLink={props.bulkLinks.totalItemsLink}
                  onKeyPress={props.onkeyPressWithoutDot}
                  onChange={props.bulkChange}
                />
              </div>
            </div>
            <div class="row Row-10">
              <div class="col-xs-12 col-sm-6 form-group">
                <label>
                  Total Space Required <sub>*</sub>
                </label>
                <VInput
                  type="text"
                  class="form-control"
                  placeholder=""
                  name="TotalSpace"
                  valueLink={props.bulkLinks.totalSpaceLink}
                  onChange={props.bulkChange}
                  onKeyPress={props.onkeyPress}
                />
              </div>
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Total Weight (Kgs/Lbs)</label>
                <VInput
                  type="text"
                  class="form-control"
                  name="TotalWeight"
                  placeholder=""
                  valueLink={props.bulkLinks.totalWeightLink}
                  onChange={props.bulkChange}
                  disabled
                />
              </div>
            </div>
            <div class="row Row-10">
              <div
                class="col-xs-12 col-sm-6 form-group add"
                style={{ marginTop: "5px" }}
              >
                {button}
              </div>
            </div>

            <div class="form-group">
              <p class="messageShow">
                <sub>*</sub> Please Fill Mandatory Feilds
              </p>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};
module.exports = Bulk;



