import React, { Component } from 'react';

const NameRegex = /^[a-zA-Z]*$/;
const EmailRegex = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
const PasswordRegex = /^(?=.*\d)(?=.*[A-Z])(?=.*[_@./?$&+,:;=?@#|'<>.^*()%!-])[0-9a-zA-Z_@./?$&+,:;=?@#|'<>.^*()%!-]{6,}$/;
const validation = (props) => {
    var Message = "";
    var IsValid=true;
    switch (props.Field) {
        case "FirstName":
            if (props.value == "" || props.value == undefined) {
                Message = "First Name is required";
            }
            else if (!NameRegex.test(props.value)) {
                Message = "Please enter characters only";
            }
            break;
        case "LastName":
            if (props.value == "" || props.value == undefined) {
                Message = "Last Name is required";
            }
            else if (!NameRegex.test(props.value)) {
                Message = "Please enter characters only";
            }
            break;
        case "Email":
            if (props.value == "" || props.value == undefined) {
                Message = "Email is required";
            }
            else if (!EmailRegex.test(props.value)) {
                Message = "Please enter valid Email";
            }
            break;
        case "Password":
            if (props.value == "" || props.value == undefined) {
                Message = "Password is required";
            }
            else if (!PasswordRegex.test(props.value)) {
                Message = "Password must be 8-20 characters long, includes atleast one uppercase character,numeral,special character";
            }
            break;
        case "ConfirmPassword":
            if (props.value != props.match) {
                Message = "Confirm password does not match";
            }
            break;
    }
    return (
        <span>{Message}</span>
    )
}



