import React from "react";
import Detail from "./detail.js";
import Messagebox from "Components/common/messagebox.js";
import MetaTags from 'react-meta-tags';
const list = props => {
  const searchObj = (
    <div class="searchBlk">
      <i class="fa fa-search searchIcon" />
      <input
        type="text"
        name="SearchText"
        value={props.searchText}
        onChange={props.search}
        placeholder="Search"
        class="form-control"
      />
    </div>
  );

  let listMessage = "";
  if (Object.keys(props.model.messageList).length) {
    listMessage = props.model.messageList.map((item, i) => {
      if (!item._id.Blocked && props.model.IsBlocked == "active") {
        return (
          <li key={i} class="left clearfix">
            <span class="chat-img pull-left img-circle">
              <img
                onClick={() => {
                  props.detail(item._id.ReceiverId);
                }}
                src={item._id.ProfilePic}
                onError={e => {
                  e.target.src = "/Content/images/default-user.jpg";
                }}
                alt="User Avatar"
                class="img-responsive"
              />
            </span>
            <div class="chat-body clearfix">
              <div class="header_sec">
                <strong
                  onClick={() => {
                    props.detail(item._id.ReceiverId);
                  }}
                  class="primary-font"
                >
                  {item._id.Name}
                </strong>

                <div class="blockWrap dropdown">
                  <strong
                    class="pull-right dropdown ellipsisIcon dropdown-toggle"
                    type="button"
                    id="dropdownMenu2"
                    data-toggle="dropdown"
                    aria-haspopup="true"
                    aria-expanded="false"
                  >
                    <i class="fa fa-ellipsis-h" aria-hidden="true" />
                  </strong>
                  <ul
                    class="dropdown-menu blockDrop pull-right"
                    aria-labelledby="dropdownMenu3"
                  >
                    <li
                      onClick={() => {
                        props.blockChat(item._id.ReceiverId);
                      }}
                    >
                      <a href="#">Block</a>
                    </li>
                    <li
                      onClick={() => {
                        props.removeMessage(item._id.ReceiverId);
                      }}
                    >
                      <a href="#">Delete</a>
                    </li>
                  </ul>
                </div>
              </div>
              <div class="contact_sec">
                <strong class="primary-font" />{" "}
                <span class="badge pull-right" />
              </div>
            </div>
          </li>
        );
      } else {
        listMessage = (
          <div class="no-result-found">
            <p>No Active list</p>
          </div>
        );
      }
      if (item._id.Blocked && props.model.IsBlocked == "block") {
        return (
          <li class="left clearfix">
            <span class="chat-img pull-left img-circle">
              <img
                src={item._id.ProfilePic}
                onError={e => {
                  e.target.src = "/Content/images/default-user.jpg";
                }}
                alt="User Avatar"
              />
            </span>
            <div class="chat-body clearfix">
              <div class="header_sec">
                <strong class="primary-font">{item._id.Name}</strong>

                <div class="blockWrap dropdown">
                  <strong
                    class="pull-right dropdown ellipsisIcon dropdown-toggle"
                    type="button"
                    id="dropdownMenu2"
                    data-toggle="dropdown"
                    aria-haspopup="true"
                    aria-expanded="false"
                  >
                    <i class="fa fa-ellipsis-h" aria-hidden="true" />
                  </strong>
                  <ul
                    class="dropdown-menu blockDrop pull-right"
                    aria-labelledby="dropdownMenu3"
                  >
                    <li
                      onClick={() => {
                        props.activeChat(item._id.ReceiverId);
                      }}
                    >
                      <a href="#">Active</a>
                    </li>
                  </ul>
                </div>
              </div>
              <div class="contact_sec">
                <strong class="primary-font" />
                <span class="badge pull-right" />
              </div>
            </div>
          </li>
        );
      } else {
        listMessage = (
          <div class="no-result-found">
            <p>No Block list</p>
          </div>
        );
      }
    });
  }
  if (props.model.messageList.length == 0)
    listMessage = (
      <div class="no-result-found">
        <p>No Result Found</p>
      </div>
    );

  return (
    <section class="msgWarpper">
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="header">
        <h2>Messages</h2>
      </div>
      <div class="mainContent mainMessageWrapper">
        <div class="innerPadding">
          <div class="row ChatDesignRoom">
            <div class="col-xs-12 col-md-5">
              <div class="__userListing">
                <div
                  href="javascript:void(0);"
                  class="__toggleList hidden-md hidden-lg"
                >
                  User List
                  <a href="javascript:void(0);" class="dropDown" />
                </div>
                <div class="__inner">
                  <div class="usersWarp padding-top-4x padding-bottom-4x">
                    {searchObj}
                  </div>
                  <div class="settingWrap dropdown">
                    <div
                      class="icon-gear dropdown-toggle"
                      type="button"
                      id="dropdownMenu2"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false"
                    />
                    <ul
                      class="dropdown-menu custom"
                      aria-labelledby="dropdownMenu2"
                    >
                      <li
                        onClick={() => {
                          props.activeList("active");
                        }}
                      >
                        <a href="#">Active List</a>
                      </li>
                      <li
                        onClick={() => {
                          props.blockList("block");
                        }}
                      >
                        <a href="#">Blocked List</a>
                      </li>
                    </ul>
                  </div>
                  <div class="chat_sidebar">
                    <div class="InnerBoxChat">
                      <div class="member_list boxScroll">
                        <ul class="list-unstyled">{listMessage}</ul>
                      </div>
                    </div>
                  </div>
                </div>{" "}
              </div>{" "}
            </div>
            {<Detail {...props} />}
          </div>{" "}
        </div>{" "}
      </div>
    </section>
  );
};
module.exports = list;



