import React, { Component } from "react";
import SavedSearch from "./space.js";
import ReportService from "Services/report.service.js";
import Loader from "Components/common/loader.js";
export default class spaceContainer extends Component {
  constructor() {
    super();
    this.state = {
      Duration: "Month",
      Type: "Space",
      Description: "",
      ReportList: [],
      isLoading: true
    };
    this.createConfig = this.createConfig.bind(this);
    this.onchange = this.onchange.bind(this);
    this.GetReportList = this.GetReportList.bind(this);
    this.SaveReport = this.SaveReport.bind(this);
  }
  createConfig(legendPosition, colorName, color, labels, data) {
    return {
      type: "line",
      data: {
        labels: labels,
        datasets: [
          {
            label: this.state.Type,
            data: data,
            backgroundColor: color(window.chartColors[colorName])
              .alpha(0.5)
              .rgbString(),
            borderColor: window.chartColors[colorName],
            borderWidth: 3
          }
        ]
      },
      options: {
        responsive: true,
        legend: {
          position: legendPosition
        },
        scales: {
          xAxes: [
            {
              display: true,
              scaleLabel: {
                display: true,
                labelString: this.state.Duration
              }
            }
          ],
          yAxes: [
            {
              display: true,
              scaleLabel: {
                display: true,
                labelString: "Value"
              }
            }
          ]
        },
        title: {
          display: false,
          text: "Legend Position: " + legendPosition
        },
        hover: {
          mode: "label"
        },
        tooltips: {
          enabled: true,
          callbacks: {
            label: function(tooltipItem, data) {
              var text =
                data.datasets[tooltipItem.datasetIndex].label == "Revenue"
                  ? "£ " + tooltipItem.yLabel
                  : tooltipItem.yLabel;
              return (
                data.datasets[tooltipItem.datasetIndex].label + ": " + text
              );
            }
          }
        }
      }
    };
  }
  showChart(labels, data) {
    const self = this;
    const color = Chart.helpers.color;
    [
      {
        id: "chart-legend-top",
        legendPosition: "top",
        color: "blue"
      }
    ].forEach(function(details) {
      var ctx = document.getElementById(details.id).getContext("2d");
      var config = self.createConfig(
        details.legendPosition,
        details.color,
        color,
        labels,
        data
      );
      new Chart(ctx, config);
    });
  }
  onchange(e) {
    e.preventDefault();
    const { name, value } = e.target;
    if (name == "Duration") {
      this.GenerateReport(this.state.Type, value);
    } else if (name == "Type") {
      this.GenerateReport(value, this.state.Duration);
      this.GetReportList(value);
    }
    this.setState({ [name]: value });
  }
  GenerateReport(type, duration) {
    this.setState({ isLoading: true });
    if (type == "Space") {
      ReportService.GetSpaceReport(duration)
        .then(response => {
          const data = response.data.map(val =>
            parseFloat(val.Space).toFixed(2)
          );
          const labels = response.data.map(val => {
            const valDate = new Date(val.Date[0]);
            if (duration == "Day") {
              return valDate.getHours() + ":00";
            } else if (duration == "Hour") {
              return valDate.getHours() + ":" + valDate.getMinutes();
            } else {
              return (
                valDate.getDate() +
                "/" +
                parseInt(valDate.getMonth() + 1) +
                "/" +
                valDate.getFullYear()
              );
            }
          });
          this.showChart(labels, data);
          this.setState({ isLoading: false });
        })
        .catch(error => {
          console.log(error);
        });
    } else if (type == "Revenue") {
      ReportService.GetRevenueReport(duration)
        .then(response => {
          const data = response.data.map(val => {
            return parseFloat(val.Space).toFixed(2);
          });
          const labels = response.data.map(val => {
            const valDate = new Date(val.Date[0]);
            if (duration == "Day") {
              return valDate.getHours() + ":00";
            } else if (duration == "Hour") {
              return valDate.getHours() + ":" + valDate.getMinutes();
            } else {
              return (
                valDate.getDate() +
                "/" +
                parseInt(valDate.getMonth() + 1) +
                "/" +
                valDate.getFullYear()
              );
            }
          });
          this.showChart(labels, data);
          this.setState({ isLoading: false });
        })
        .catch(error => {
          console.log(error);
        });
    } else if (type == "Rating") {
      ReportService.GetRatingReport(duration)
        .then(response => {
          const data = response.data.map(val =>
            parseFloat(val.Space).toFixed(2)
          );
          const labels = response.data.map(val => {
            const valDate = new Date(val.Date[0]);
            if (duration == "Day") {
              return valDate.getHours() + ":00";
            } else if (duration == "Hour") {
              return valDate.getHours() + ":" + valDate.getMinutes();
            } else {
              return (
                valDate.getDate() +
                "/" +
                parseInt(valDate.getMonth() + 1) +
                "/" +
                valDate.getFullYear()
              );
            }
          });
          this.showChart(labels, data);
          this.setState({ isLoading: false });
        })
        .catch(error => {
          console.log(error);
        });
    }
  }

  SaveReport() {
    const data = {
      Type: this.state.Type,
      Description: this.state.Description.trim()
    };
    if (data.Description == "") {
      return false;
    }
    ReportService.SaveReport(data)
      .then(reponse => {
        const reptList = this.state.ReportList;
        reptList.push(reponse.data);
        this.setState({ ReportList: reptList, Description: "" });
      })
      .catch(err => {
        console.log(err);
      });
  }
  GetReportList(type) {
    this.setState({ isLoading: true });
    ReportService.GetReport(type)
      .then(response => {
        this.setState({ ReportList: response.data, isLoading: false });
      })
      .catch(err => {
        console.log(err);
      });
  }

  componentDidMount() {
    this.GenerateReport("Space", "Month");
    this.GetReportList("Space");
  }
  render() {
    const model = {
      model: this.state,
      onchange: this.onchange,
      SaveReport: this.SaveReport
    };
    return <SavedSearch {...model} />;
  }
}



