import React from "react";
import ProfileDetail from "./profiledetail.js";
import EditProfile from "./editprofile.js";
import MetaTags from 'react-meta-tags';
const Profile = (props) => {
    let view = null;

    if (!props.isEdit) {
        view = <ProfileDetail {...props}></ProfileDetail>;
    } else {
        view = <EditProfile {...props}></EditProfile>;
    }

    return (
        <div>
            <MetaTags>
                <title>Logist Compare</title>
            </MetaTags>
            <div class="header">
                <h2>Profile</h2>
            </div>
            {view}
        </div>
    )
}
module.exports = Profile;


