import React from "react";
import BulkItem from "./providerbulkItem.js";
import PelletItem from "./providerpalletItem.js";
import VInput from "Components/common/VInput";
import Flatpickr from "react-flatpickr";
const QuoteList = props => {
  let quoteButton = null;
  let quoteList = null;
  if (props.State.IsBulk) {
    if (props.State.Bulks.length > 0) {
      quoteList = props.State.Bulks.map((x, i) => {
        return (
          <BulkItem
            EditBulk={props.EditBulk}
            key={i}
            index={i}
            {...x}
            bid={props.State.Bulk._id}
          />
        );
      });
    }
  } else {
    if (props.State.Pallets.length > 0) {
      quoteList = props.State.Pallets.map((x, i) => {
        return (
          <PelletItem
            EditPallet={props.EditPallet}
            key={i}
            index={i}
            {...x}
            pid={props.State.Pallet._id}
          />
        );
      });
    }
  }

  return (
    <div class="cell">
      <div class="bookingFormWarp">
        <h2 class="h2Title">Booking Form</h2>
        {quoteList}

        <div class="row Row-10">
          <div class="costBreak">
            <div class="form-group row">
              <label for="example-text-input" class="col-2 col-form-label">
                Total Cost To Customer
              </label>
              <div class="col-10">
                <span class="form-control">
                  &pound; {props.State.TotalCostBreakDown.FinalCost}
                </span>
              </div>
            </div>
          </div>
          <div class="costBreak">
            <div class="form-group row">
              <label for="example-text-input" class="col-2 col-form-label">
                Quote Valid Till
              </label>
              <div class="col-10">
                {/* <VInput
                  type="text"
                  class="form-control"
                  placeholder="No of days"
                  valueLink={props.links.quoteValidDaysLink}
                /> */}
                <Flatpickr
                  type="text"
                  class="form-control"
                  placeholder=""
                  value={props.State.QuoteValidTill}
                  data-min-date="today"
                  onChange={(_, str) =>
                    props.onflatPicker("QuoteValidTill", str)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = QuoteList;



