import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Customer from "./customer.js";
import CustomerService from "Services/customer.service.js";
import WarehouseService from "Services/warehouse.service.js";
import Helper from "../../helpers/utility.helper.js";
import Loader from "Components/common/loader.js";
const viewModel = {
  Name: "",
  Capacity: "",
  CapacityType: "",
  Address: "",
  City: "",
  PostCode: "",
  Website: "",
  Starthrs: "",
  Endhrs: "",
  About: "",
  StorageType: "Pallet",
  Services: {},
  WarehouseType: {},
  Security: {},
  Images: [],
  PendingSpace: "",
  UserId: "",
  lat: "",
  lng: "",
  Bulk: {
    MaxLength: "",
    MaxLengthType: "",
    MaxWidth: "",
    MaxWidthType: "",
    MaxWeight: "",
    MaxWeightType: "",
    MaxHeight: "",
    MaxHeightType: "",
    AvailabilityFrom: "",
    AvailabilityTo: "",
    ApxPrice: "",
    ApxPriceType: "",
    TotalSpace: "",
    Description: ""
  },
  Pallet: {
    Type: { Name: "", Value: "" },
    Total: "",
    MaxHeight: "",
    MaxHeightType: "",
    ApxPrice: "",
    AvailabilityTo: "",
    AvailabilityFrom: "",
    Images: ""
  }
};
const reviewModel = {
  Id: "",
  Rating: "",
  Description: ""
};
export default class CustomerContainer extends Component {
  constructor() {
    super();
    this.state = {
      Customers: [],
      Activities: [],
      IsActivity: true,
      isLoading: true,
      Reviews: [],
      isShowRating: false,
      RatingModel: { ...reviewModel },
      Detail: { ...viewModel }
    };
    this.getWarehouseDetail = this.getWarehouseDetail.bind(this);
    this.CloseRating = this.CloseRating.bind(this);
  }
  CloseRating() {
    this.setState({ isShowRating: false });
  }
  getWarehouseDetail(id) {
    WarehouseService.Get(id)
      .then(respose => {
        WarehouseService.GetReviews(id)
          .then(res => {
            this.setState({
              Detail: respose.data,
              Reviews: res.data,
              isShowRating: true
            });
          })
          .catch(error => {
            console.log(error);
          });
      })
      .catch(err => {
        console.log(err);
      });
  }
  componentWillMount() {
    this.getActivities();
  }
  getCustomer() {
    this.setState({ isLoading: true });
    CustomerService.getCustomers().then(response => {
      this.setState({
        Customers: response.data,
        IsActivity: false,
        isLoading: false
      });
    });
  }
  getActivities() {
    this.setState({ isLoading: true });
    CustomerService.getActivities().then(response => {
      this.setState({
        Activities: response.data,
        IsActivity: true,
        isLoading: false
      });
    });
  }
  render() {
    const customerObject = {
      model: this.state,
      customerList: this.state.Customers,
      customerActivity: this.state.Activities,
      IsActivityTab: this.state.IsActivity,
      getcustomerActivities: this.getActivities.bind(this),
      getcustomer: this.getCustomer.bind(this),
      getWarehouseDetail: this.getWarehouseDetail,
      CloseRating: this.CloseRating
    };
    if (this.state.isLoading) return <Loader />;
    else return <Customer {...customerObject} />;
  }
}



