import React from "react";
import Helper from "../../../helpers/utility.helper.js";
import Rating from "Components/common/starRating.js";
const QuoteItem = props => {
  const messageLink = "/user/message/" + props.WarehouseDetail.UserId;
  let button = {};
  if (props.Status == 1) {
    button = (
      <a
        href="javascript:void(0);"
        class="button button-action button-min-width margin-top-2x margin-bottom-2x"
      >
        Pending
      </a>
    );
  } else if (props.Status == 5) {
    button = (
      <a
        href="javascript:void(0);"
        class="button button-action button-min-width margin-top-2x margin-bottom-2x"
      >
        Pending
      </a>
    );
  } else {
    button = (
      <a
        href={"/user/quotepayment/" + props._id}
        class="button button-action button-min-width margin-top-2x margin-bottom-2x"
      >
        Book
      </a>
    );
  }
  let images = {};
  images = props.WarehouseDetail.Images.map((item, index) => {
    return (
      <li key={index}>
        {" "}
        <img
          src={item}
          key={index}
          alt="1"
          class="img-responsive"
          onError={e => {
            e.target.src = "/Content/images/default-user.jpg";
          }}
        />
      </li>
    );
  });
  if (images.length < 1) {
    images.push(
      <li key="1">
        <img
          src="/Content/images/warehouse/w1.jpg"
          alt="1"
          class="img-responsive"
        />
      </li>
    );
  }
  return (
    <div class="resultWarp margin-bottom-6x">
      <div class="cell">
        <ul class="slickSingle">{images}</ul>
      </div>
      <div class="cell">
        <div class="__content">
          <span style={{ cursor: "pointer" }}>
            <h3
              data-toggle="modal"
              data-target="#warehouseDetail"
              onClick={() => {
                props.getDetail(props.WarehouseDetail._id);
              }}
            >
              {props.WarehouseDetail.Name}
            </h3>
          </span>
          <div class="row">
            <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
              <label>Capacity</label>
              <p>{props.TotalCapacity}</p>
            </div>
            <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
              <label>Date Requested</label>
              <p>{Helper.ConvertDateToString(props.BookedOn)}</p>
            </div>
            <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
              <label>Location</label>
              <p>
                <a
                  href="#mapViewTab"
                  data-toggle="tab"
                  onClick={() => {
                    props.onMapView(props.WarehouseDetail._id);
                  }}
                >
                  View on map <i class="fa fa-angle-right" />
                </a>
              </p>
            </div>
            <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
              <span
                data-toggle="modal"
                data-target="#viewRatingModal"
                onClick={() => {
                  props.getWarehouseDetail(props.WarehouseDetail._id);
                }}
              >
                <label style={{ cursor: "pointer" }}>Rating</label>
                <div class="ratingBlk">
                  <Rating input={props.WarehouseDetail.AvgReviews} />
                </div>
              </span>
            </div>
            <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
              <label>Final Cost</label>
              <div class="dropdown">
                <div class="costDropdown">
                  <a
                    href="javascript:void(0);"
                    class="dropdown-toggle"
                    data-toggle="dropdown"
                  >
                    &pound; {props.TotalCostBreakDown.FinalCost}
                  </a>
                  <ul class="dropdown-menu">
                    <li>
                      <a href="javascript:void(0);">
                        Handling In &pound;{props.TotalCostBreakDown.HandlingIn}
                      </a>
                    </li>
                    <li>
                      <a href="javascript:void(0);">
                        Handling Out &pound;{
                          props.TotalCostBreakDown.HandlingOut
                        }
                      </a>
                    </li>
                    <li>
                      <a href="javascript:void(0);">
                        Storage &pound;{props.TotalCostBreakDown.Storage}
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="cell button-cell">
        {button}
        <a
          href={messageLink}
          class="button button-default button-min-width margin-top-2x margin-bottom-2x"
        >
          Message
        </a>
      </div>
    </div>
  );
};
module.exports = QuoteItem;



