import React from "react";
import VInput from "Components/common/VInput";
import { Link } from "react-router-dom";
const Register = props => {
  const displayNone = {
    display: props.ProfileImage == "" ? "none" : "block"
  };
  const displayBlock = {
    display: props.ProfileImage != "" ? "none" : "block"
  };
  const loginLink = {
    fontSize: "18px",
    textDecoration: "underline"
  };
  const errorMessage = {
    color: "red"
  };
  const textColor = {
    color: "white",
    textDecoration: "underline"
  };
  let classobj = {};
  let IsListSpace = false;
  if (props.path == "/warehouse/register") {
    IsListSpace = true;
    classobj = {
      fillcolor: "#3ab54a",
      theme: "bg-theme",
      wrapper: "regWarpper1",
      loginUrl: "/warehouse/login"
    };
  } else {
    classobj = {
      fillcolor: "#0070bb",
      theme: "bg-theme-alt",
      wrapper: "",
      loginUrl: "/findspace/login"
    };
  }

  return (
    <div class="reg-account">
      <div className={"regWarpper" + " " + classobj.wrapper}>

        <span className="regShape regShape2">
          {/*<svg
          className="svg1 translateY"
          x="0px"
          y="0px"
          width="2000px"
          height="2000px"
          viewBox="0 0 2000 2000"
          enableBackground="new 0 0 2000 2000"
        >
          <path
            id="mPath1"
            fill="#ffffff"
            d="M2000,0c0,0-906,252.556-1999,321v1679h1999V0z"
          />
        </svg>*/}
          <svg
            className="svg2 active"
            x="0px"
            y="0px"
            width="2000px"
            height="2000px"
            viewBox="0 0 2000 2000"
            enableBackground="new 0 0 2000 2000"
            xmlSpace="preserve"
          >
            {/*xml:space="preserve"*/}
            <path
              id="mPath2"
              fill={classobj.fillcolor}
              d="M2000,0c0,0-906,252.556-1999,321v1679h1999V0z"
            />
          </svg>
          <div className="hidden-md hidden-lg" />
          <div className="svg2 active hidden-md hidden-lg" />
          <div className="buttonWarp">
            <a
              href="javascript:void(0);"
              className="button button-border button-fade button-uppercase SW-Button SW-Button1 hide"
            >
              sign up
          </a>
          </div>
        </span>
        <div className="container">
          <div className="row">
            <div className="col-xs-12 col-md-5 col-lg-4">
              <div id="signUp" className="formOuterWarp">
                <h2>Sign Up</h2>
                <div className="formWarp">
                  <div className="form-group text-center profileImage-container">
                    <div className="uploadeWarp" style={displayBlock}>
                      <span className="userUpload">
                        <i className="icon-user-o ico-t4" />
                        <span className={"addbtn" + " " + classobj.theme}>
                          <i className="icon-plus" />
                        </span>
                      </span>
                    </div>
                    <input
                      id="profileImage"
                      type="file"
                      onChange={props.UploadImage}
                    />

                    <div className="uploadeWarp">
                      <div class="userUpload" style={displayNone}>
                        <img
                          src={props.ProfileImage}
                          alt="Logist Compare"
                          class="img-responsive"
                        />
                      </div>
                    </div>
                  </div>
                  <div className="form-group">
                    <VInput
                      type="text"
                      className="form-control"
                      name="FirstName"
                      placeholder="First Name*"
                      maxLength="50"
                      valueLink={props.links.firstNameLink}
                    />
                  </div>
                  <div className="form-group">
                    <VInput
                      type="text"
                      className="form-control"
                      name="LastName"
                      placeholder="Last Name*"
                      maxLength="50"
                      valueLink={props.links.lastNameLink}
                    />
                  </div>
                  <div className="form-group">
                    <VInput
                      type="text"
                      className="form-control"
                      name="Email"
                      placeholder="Email*"
                      maxLength="100"
                      valueLink={props.links.emailLink}
                    />
                  </div>
                  <div className="form-group">
                    <VInput
                      type="password"
                      className="form-control"
                      name="Password"
                      placeholder="Password*"
                      maxLength="20"
                      valueLink={props.links.passwordLink}
                    />
                  </div>
                  <div className="form-group">
                    <VInput
                      type="password"
                      className="form-control"
                      name="ConfirmPassword"
                      placeholder="Confirm Password*"
                      valueLink={props.links.confirmPasswordLink}
                    />
                  </div>

                  <div className="form-group register">
                    <label className="ch-checkMark">
                      <input
                        type="checkbox"
                        name="Terms"
                        value="false"
                        onChange={props.onChange}
                      />
                      <span>
                        {" "}
                        I agree to{" "}
                        <a
                          href="/termsandconditions"
                          target="_blank"
                          style={textColor}
                        >
                          Terms and Conditions
                      </a>
                      </span>
                    </label>
                  </div>
                  <div className="form-group">
                    <input
                      type="button"
                      className="button button-border button-fade button-uppercase margin-top-3x"
                      value="Sign up"
                      onClick={props.onClick}
                    />
                  </div>
                  <div className="form-group loginAccount">
                    <p>
                      Already have an account?{" "}
                      <Link
                        to={classobj.loginUrl}
                        style={loginLink}
                        className="SW-Button SW-Button2"
                      >
                        {" "}
                        Login Here
                    </Link>
                    </p>
                  </div>
                </div>
              </div>
            </div>

            <div class="col-xs-12 col-md-6 col-md-offset-1 col-lg-6 col-lg-offset-2 rightPart signUpactive">
              <div class="row">
                <div class="col-xs-12 rightPart-tital"><h4>Contact Us</h4></div>
                <div class="col-xs-12 col-lg-6 form-group">
                  <div class="cInfo">
                    <i class="icon-email-o" />
                    <h5>INFO</h5>
                    <a
                      href="mailto:feedback@logistcompare.com"
                      style={{ color: "white" }}
                    >
                      feedback@logistcompare.com
                  </a>
                  </div>
                </div>
                <div class="col-xs-12 col-lg-6 form-group">
                  <div class="cInfo">
                    <i class="icon-phone-o" />
                    <h5>PHONE</h5>
                    <span>(+44) 07956 664569</span>
                  </div>
                </div>
              </div>
              <div class="__video">
                <h3>
                  How to{" "}
                  {props.path == "/warehouse/register" ? (
                    "Register?"
                  ) : (
                      "find space"
                    )}
                </h3>
                <iframe
                  id="findframe"
                  width="100%"
                  height="300"
                  src={props.State.Videos}
                  frameBorder="0"
                  allowFullScreen
                />
              </div>

              {IsListSpace ? (<div class="row howtofind"><div class="col-xs-12 rightPart-tital"><h4>How to List space?</h4></div>
                <div class="col-xs-12 form-group">
                  <ol type="1">
                    <li>Login with your email and password</li>
                    <li>Add warehouse details and services provided</li>
                    <li>Upload images of your warehouse</li>
                    <li>When completed, submit form</li>
                  </ol>

                </div>
              </div>
              ) :
                (<div class="row howtofind"><div class="col-xs-12 rightPart-tital"><h4>How to Find space?</h4></div>
                  <div class="col-xs-12 form-group">
                    <ol type="1">
                      <li>Login with your email and password</li>
                      <li>Select type of storage required, desired location and dates</li>
                      <li>Enter your storage specifications</li>
                      <li>Get warehouses options and costs at the touch of button</li>
                    </ol>

                  </div> </div>)
              }

            </div>
          </div>
        </div>

        <div
          className="modal fade"
          id="myModal"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="myModalLabel-2"
        >
          <div className="modal-dialog" role="document">
            <div className="modal-content forgot">
              <div className="closewrap confirm-close">
                <button type="button" className="close" data-dismiss="modal">
                  <i className="fa fa-times" aria-hidden="true" />
                </button>
              </div>

              <div className="confirm-mail-head">
                <h2>E-mail Confirmation</h2>
                <p>
                  We have sent mail to info@demo.com to confrim the validity of
                  our email address.After receiving the mail follow the link
                  provided to complete your registration
              </p>
              </div>

              <div className="modal-body confirm-mail">
                <div className="confirm">
                  <p>
                    Didn’t receive confirmation link?{" "}
                    <a href="">Resend confirmation mail</a>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="we-offer">
        <div class="container">
          <div class="row">
            {IsListSpace ? (<div class="col-xs-12 list-offer">
              <h4>Special benefits we offer</h4>

              <ul>
                <li>
                  <span><img src="/Content/images/Promote.png" alt="" /> </span>
                  <p>Promote your service online</p>
                </li>
                <li>
                  <span><img src="/Content/images/Reach.png" alt="" /> </span>
                  <p>Reach customers directly</p>
                </li>
                <li>
                  <span><img src="/Content/images/List1.png" alt="" /> </span>
                  <p> List available space online</p>
                </li>
                <li>
                  <span><img src="/Content/images/billing.png" alt="" /> </span>
                  <p>Have automated billing</p>
                </li>
              </ul>

            </div>) : (<div class="col-xs-12">
              <h4>Special benefits we offer</h4>

              <ul>
                <li>
                  <span><img src="/Content/images/instantly.png" alt="" /> </span>
                  <p>Add capacity instantly</p>
                </li>
                <li>
                  <span><img src="/Content/images/reviews.png" alt="" /> </span>
                  <p>Access user reviews prior to booking the services</p>
                </li>
                <li>
                  <span><img src="/Content/images/Communicate.png" alt="" /> </span>
                  <p>Communicate with warehouse providers by using our chat services</p>
                </li>
                <li>
                  <span><img src="/Content/images/secure.png" alt="" /> </span>
                  <p>Have access to an easy and secure booking service</p>
                </li>
              </ul>

            </div>)}
          </div>
        </div>
      </div>

    </div>
  );
};
module.exports = Register;



