import React from "react";
import Helper from "Helpers/utility.helper.js";
import Rating from "Components/common/starRating.js";
const BookingItem = props => {
  let images = [];
  let warehouseImages = [];
  if (props.WarehouseDetail)
    warehouseImages = [...props.WarehouseDetail.Images];
  if (warehouseImages.length == 0) {
    warehouseImages.push("/Content/images/warehouse/w1.jpg");
  }
  images = warehouseImages.map((item, index) => {
    return (
      <li key={index}>
        {" "}<img src={item} alt="1" class="img-responsive" onError={e => {
          e.target.src = "/Content/images/warehouse/w1.jpg";
        }} />
      </li>
    );
  });
  return (
    <div class="resultWarp margin-bottom-6x">
      <div class="cell">
        <ul class="slickSingle">
          {images}
        </ul>
      </div>
      <div class="cell">
        <div class="__content">
          <span
            style={{ cursor: "pointer" }}
            data-toggle="modal"
            data-target="#warehouseDetail"
            onClick={() => {
              props.getDetail(props.WarehouseDetail._id);
            }}
          >
            <h3>
              {props.WarehouseDetail.Name}
            </h3>
          </span>
          <div class="row">
            <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
              <label>Capacity</label>
              <p>
                {parseInt(props.TotalCapacity)} {"sq Meter"}
              </p>
            </div>
            <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
              <label>Date Booked</label>
              <p>
                {Helper.ConvertDateToString(props.ModifiedDate)}
              </p>
            </div>
            <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
              <label>Location</label>
              <p>
                {props.WarehouseDetail.Address}
              </p>
            </div>
            <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
              <label>Cost</label>
              <p>
                &pound; {props.TotalCostBreakDown.FinalCost}
              </p>
            </div>
            <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-4 form-group">
              <span
                data-toggle="modal"
                data-target="#viewRatingModal"
                onClick={() => {
                  props.getWarehouseDetail(props.WarehouseDetail._id);
                }}
              >
                <label style={{ cursor: "pointer" }}>Rating</label>
                <div class="ratingBlk">
                  <Rating input={props.WarehouseDetail.AvgReviews} />
                </div>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = BookingItem;



