import React, { Component } from "react";
import Booking from "./providerbooking.js";
import WarehouseService from "Services/warehouse.service.js";
import QuoteService from "Services/quote.service.js";
import Loader from "Components/common/loader.js";
const viewModel = {
  Name: "",
  Capacity: "",
  CapacityType: "",
  Address: "",
  City: "",
  PostCode: "",
  Website: "",
  Starthrs: "",
  Endhrs: "",
  About: "",
  StorageType: "Pallet",
  Services: {},
  WarehouseType: {},
  Security: {},
  Images: [],
  PendingSpace: "",
  UserId: "",
  lat: "",
  lng: "",
  Bulk: {
    MaxLength: "",
    MaxLengthType: "",
    MaxWidth: "",
    MaxWidthType: "",
    MaxWeight: "",
    MaxWeightType: "",
    MaxHeight: "",
    MaxHeightType: "",
    AvailabilityFrom: "",
    AvailabilityTo: "",
    ApxPrice: "",
    ApxPriceType: "",
    TotalSpace: "",
    Description: ""
  },
  Pallet: {
    Type: { Name: "", Value: "" },
    Total: "",
    MaxHeight: "",
    MaxHeightType: "",
    ApxPrice: "",
    AvailabilityTo: "",
    AvailabilityFrom: "",
    Images: ""
  }
};
const reviewModel = {
  Id: "",
  Rating: "",
  Description: ""
};
export default class ProviderBookingContainer extends Component {
  constructor() {
    super();

    this.state = {
      quotesObj: [],
      status: 3,
      showTitle: true,
      isLoading: true,
      Reviews: [],
      isShowRating: false,
      RatingModel: { ...reviewModel },
      Detail: { ...viewModel }
    };
    this.pendingList = this.pendingList.bind(this);
    this.bookedList = this.bookedList.bind(this);
    this.getWarehouseDetail = this.getWarehouseDetail.bind(this);
    this.CloseRating = this.CloseRating.bind(this);
  }

  getBookingList(status) {
    this.setState({ isLoading: true });
    QuoteService.getBookingfromStatus(status).then(response => {
      this.setState({ quotesObj: response.data, isLoading: false });
    });
  }
  CloseRating() {
    this.setState({ isShowRating: false });
  }
  getWarehouseDetail(id) {
    WarehouseService.Get(id)
      .then(respose => {
        WarehouseService.GetReviews(id)
          .then(res => {
            this.setState({
              Detail: respose.data,
              Reviews: res.data,
              isShowRating: true
            });
          })
          .catch(error => {
            console.log(error);
          });
      })
      .catch(err => {
        console.log(err);
      });
  }
  componentWillMount() {
    if (this.props.location.pathname == "/warehouse/bookings") {
      this.setState({ showTitle: true });
    } else {
      this.setState({ showTitle: false });
    }

    this.getBookingList(this.state.status);
  }
  pendingList(status) {
    this.getBookingList(status);
    this.setState({ status: status });
  }
  bookedList(status) {
    this.getBookingList(status);
    this.setState({ status: status });
  }
  render() {
    const param = {
      model: this.state,
      pendingList: this.pendingList,
      bookedList: this.bookedList,
      getWarehouseDetail: this.getWarehouseDetail,
      CloseRating: this.CloseRating
    };
    if (this.state.isLoading) return <Loader />;
    else return <Booking {...param} />;
  }
}



