import React from "react";
import List from "./list.js";
import MessageService from "../../services/message.service.js";
import Helper from "../../helpers/utility.helper.js";
import Loader from "Components/common/loader.js";
let receiveId = "";
let isactive = "active";
export default class addMessage extends React.Component {
  constructor() {
    super();

    this.state = {
      Text: "",
      ReceiverId: "",
      Status: true,
      SenderId: "",
      SearchText: "",
      ShowAlertModal: false,
      Message: "",
      IsBlocked: "active",
      MessageBlockedList: [],
      messageDetail: {},
      messageList: {},
      maintainMessageList: {},
      CreateDate: new Date(),
      isLoading: true
    };

    this.handleChange = this.handleChange.bind(this);
    this.sendMessage = this.sendMessage.bind(this);
    this.messageDetail = this.messageDetail.bind(this);
    this.removeMessage = this.removeMessage.bind(this);
    this.filterMessageList = this.filterMessageList.bind(this);
    this.blockChat = this.blockChat.bind(this);
    this.activeList = this.activeList.bind(this);
    this.blockList = this.blockList.bind(this);
    this.activeChat = this.activeChat.bind(this);
    this.CloseModal = this.CloseModal.bind(this);
  }

  MessagesListonLoad() {
    this.setState({ isLoading: true });
    MessageService.getList().then(response => {
      this.setState({
        messageList: response.data,
        isLoading: false
      });
      //  this.setState({ maintainMessageList: response.data });
    });
  }

  CloseModal() {
    this.setState({ ShowAlertModal: false });
  }
  componentWillMount() {
    const onloadReceiveId = this.props.match.params.id;
    this.setState({ ReceiverId: onloadReceiveId });
    receiveId = onloadReceiveId;
    this.MessagesListonLoad();
    this.messageDetail(receiveId);
  }

  messageDetail(receiverId) {
    history.pushState({}, "", "/user/message/" + receiverId);
    receiveId = receiverId;
    const sendrid = Helper.DecodeToken(localStorage.getItem("userinfo")).Id;
    MessageService.getDetail(receiverId).then(response => {
      this.setState({
        messageDetail: response.data,
        SenderId: sendrid,
        ReceiverId: receiverId
      });
    });
  }
  activeChat(Id) {
    let messList = [];
    let statevalue = this.state.messageList;
    statevalue.filter(item => {
      if (item._id.ReceiverId != Id) {
        messList.push(item);
      }
      // this.setState({ ShowAlertModal: true, Message: 'successfully active' })
      MessageService.unBlockChat(Id).then(() => {
        this.messageDetail("123");
        this.setState({ messageList: messList });
        //        this.setState({ maintainMessageList: messList });
      });
    });
  }

  activeList(value) {
    isactive = value;
    this.messageDetail("123");
    this.setState({ IsBlocked: value });
    this.MessagesListonLoad();
  }

  blockList(value) {
    isactive = value;
    this.messageDetail("123");
    this.setState({ IsBlocked: value });
    this.MessagesListonLoad();
  }

  removeMessage(id) {
    MessageService.removeMessages(id).then(() => {
      // this.setState({ ShowAlertModal: true, Message: 'successfully delete the messages' })
      this.MessagesListonLoad();
      this.messageDetail(id);
    });
  }

  blockChat(id) {
    let messList = [];
    let statevalue = this.state.messageList;
    statevalue.filter(item => {
      if (item._id.ReceiverId != id) {
        messList.push(item);
      }
      // this.setState({ ShowAlertModal: true, Message: 'successfully block' })
      MessageService.blockChat(id).then(res => {
        this.messageDetail("dddd");
        this.setState({ messageList: messList });
        //   this.setState({ maintainMessageList: messList });
      });
    });
  }
  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }

  filterMessageList(e) {
    const value = e.target.value;
    this.setState({ SearchText: value });

    if (value == "" && isactive == "active") {
      this.activeList(isactive);
    }
    if (value == "" && isactive == "block") {
      this.blockList(isactive);
    }
    const newList = [];

    let statevalue = this.state.messageList;
    statevalue.filter(item => {
      if (item._id.Name.toLowerCase().match(value)) {
        newList.push(item);
      }
    });

    this.setState({ messageList: newList });
  }
  sendMessage() {
    if (this.state.Text == "") {
      this.setState({
        Text: "",
        ShowAlertModal: true,
        Message: "please enter a valid text"
      });
      return false;
    }
    MessageService.Send(this.state).then(response => {
      let statevalue = this.state.messageDetail;
      if (Object.keys(statevalue).length) {
        statevalue.push(response.data);
        this.setState({ messageDetail: statevalue });
      } else {
        let updateValue = [];
        updateValue.push(response.data);
        this.setState({ messageDetail: updateValue });
      }
      this.setState({ Text: "" });
      this.MessagesListonLoad();
    });
  }
  render() {
    const params = {
      onChange: this.handleChange,
      onClick: this.sendMessage,
      removeMessage: this.removeMessage,
      activeList: this.activeList,
      blockList: this.blockList,
      blockChat: this.blockChat,
      activeChat: this.activeChat,
      search: this.filterMessageList,
      detail: this.messageDetail,
      CloseModal: this.CloseModal,
      model: this.state
    };
    if (this.state.isLoading) return <Loader />;
    else return <List {...params} />;
  }
}



