import React from "react";

const ProfileDetail = (props) => {

    return (<div class="mainContent boxScroll">
        <div class="sStorageWarp profileWrap">
            <div class="row margin-top-20">
                <div class="col-xs-12">
                    <div class="editKey">
                        <a href="javascript:void(0)" onClick={props.onEdit}>Edit Profile <i class="fa fa-pencil" aria-hidden="true"></i></a>
                    </div>
                </div>
            </div>

            <div class="row">
                <div class="col-xs-12 col-xs-12 col-sm-12 col-lg-12">
                    <div class="ProfileBanner">
                        <img src="/Content/images/warehouse/profile_banner.jpg" alt="Logist Compare" class="img-responsive" />
                    </div>
                </div>

                <div class="col-xs-12 col-md-2 ProfileContent">
                    <div class="ProfileImg">
                        <img src={props.userDetail.ProfilePicture} onError={(e)=>{e.target.src="/Content/images/default-user.jpg"}} alt="Logist Compare" class="img-responsive" />
                    </div>
                    <div class="ProfileName">
                       {props.userDetail.FirstName+' '+props.userDetail.LastName}
                                                </div>
                </div>
                <div class="col-xs-12 col-md-10 col-lg-10">
                    <div id="signIn" class="ProfileForm">
                        <div class="profileformWarp editProfile">
                            <div class="col-xs-12 col-md-5 col-lg-5 form-group">
                                <label for="name">First Name</label>
                                <p>{props.userDetail.FirstName}</p>
                            </div>
                            <div class="col-xs-12 col-md-5 col-lg-5 form-group form-group">
                                <label for="name">Last Name</label>
                                <p>{props.userDetail.LastName}</p>
                            </div>
                            <div class="col-xs-12 col-md-5 col-lg-5 form-group form-group">
                                <label for="name">Email</label>
                                <p>{props.userDetail.Email}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>)
}
module.exports=ProfileDetail;


