import React from "react";
import Flatpickr from "react-flatpickr";
import Images from "./images.js";
const pallet = props => {
  let imagesList = "";
  const timePockerOptions = {
    minDate: "today"
  };
  if (props.model.StorageType == "Pallet") imagesList = <Images {...props} />;

  return (
    <div class="palletShow" id="PalletStorage">
      <div class="row">
        <div class="col-xs-12">
          <div class="pallet-heading">
            <h2 class="titlePallet">Pallet Storage</h2>
            <div class="pending-wrap">
              <label>Pending space</label>
              <div class="pending-box">
                <p>
                  {props.model.PendingSpace}
                  {" sq" + props.model.CapacityType}
                </p>
              </div>
            </div>
          </div>
          <div class="form-group">
            <div class="row">
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Type of Pallets</label>
                <div class="row Row-2">
                  <select
                    class="form-control"
                    title={props.model.Pallet.Type.Value}
                    default
                    title="Select Pallet"
                    id="Type"
                    name="Pallet"
                    value={props.model.Pallet.Type.Value}
                    onChange={props.change}
                  >
                    <option id="EURO" value="0.96">
                      EURO : 0.80 x1.20 (W x L)
                    </option>
                    <option id="STD1" value="1.2">
                      STD1 : 1.00 x1.20 (W x L)
                    </option>
                    <option id="STD2" value="1.44">
                      STD2 : 1.20x1.20(W x L)
                    </option>
                  </select>
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 form-group">
                <label>Number of Pallets</label>
                <div class="row Row-2">
                  <div class="col-xs-12">
                    <input
                      type="text"
                      class="form-control"
                      id="Total"
                      name="Pallet"
                      placeholder=""
                      value={props.model.Pallet.Total}
                      onChange={props.change}
                      onKeyPress={props.onkeyPressWithoutDot}
                      onPaste={props.onPaste}
                    />
                  </div>
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 col-md-4 form-group">
                <label>Max Weight</label>
                <div class="row Row-2">
                  <div class="col-xs-8">
                    <input
                      type="text"
                      class="form-control"
                      placeholder=""
                      id="MaxWeight"
                      name="Pallet"
                      value={props.model.Pallet.MaxWeight}
                      onChange={props.change}
                      onKeyPress={props.onkeyPress}
                      onPaste={props.onPaste}
                    />
                  </div>
                  <div class="col-xs-4">
                    <div class="row Row-2">
                      <select
                        class="form-control"
                        title=""
                        id="MaxWeightType"
                        name="Pallet"
                        value={props.model.Pallet.MaxWeightType}
                        onChange={props.change}
                      >
                        <option value="Kgs">Kgs</option>
                        <option value="Lbs">Lbs</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 col-md-4 form-group">
                <label>Max Height</label>
                <div class="row Row-2">
                  <div class="col-xs-8">
                    <input
                      type="text"
                      class="form-control"
                      placeholder=""
                      id="MaxHeight"
                      name="Pallet"
                      value={props.model.Pallet.MaxHeight}
                      onChange={props.change}
                      onKeyPress={props.onkeyPress}
                      onPaste={props.onPaste}
                    />
                  </div>
                  <div class="col-xs-4">
                    <div class="row Row-2">
                      <select
                        class="form-control"
                        title=""
                        id="MaxHeightType"
                        name="Pallet"
                        value={props.model.Pallet.MaxHeightType}
                        onChange={props.change}
                        disabled
                      >
                        <option value="Meter">Meters</option>
                        <option value="Feet">Feet</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 col-md-4 form-group">
                <div class="col-xs-12">
                  <label>Appx pricing </label>
                </div>
                <div class="col-xs-12 input-group">
                  <span class="input-group-addon" id="basic-addon1">
                    &pound;
                  </span>
                  <input
                    type="text"
                    class="form-control"
                    placeholder="per pallet"
                    id="ApxPrice"
                    name="Pallet"
                    value={props.model.Pallet.ApxPrice}
                    onChange={props.change}
                    onKeyPress={props.onkeyPress}
                    onPaste={props.onPaste}
                    aria-describedby="basic-addon1"
                  />
                </div>
              </div>
              <div class="col-xs-12 col-sm-6 col-md-4">
                <div class="form-group">
                  <label class="lbl">Availability</label>
                  <div class="__dateRang">
                    <div class="input-group">
                      <Flatpickr
                        className="form-control"
                        id="AvailabilityFrom"
                        name="Pallet"
                        data-min-date="today"
                        value={props.model.Pallet.AvailabilityFrom}
                        onChange={(_, str) =>
                          props.onflatpicker("Pallet.AvailabilityFrom", str)}
                      />
                      <span class="input-group-addon">to</span>
                      <Flatpickr
                        className="form-control"
                        id="AvailabilityTo"
                        name="Pallet"
                        data-min-date="today"
                        value={props.model.Pallet.AvailabilityTo}
                        onChange={(_, str) =>
                          props.onflatpicker("Pallet.AvailabilityTo", str)}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            {/*images*/}
            {imagesList}
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = pallet;



