import React from "react";
import Messagebox from "Components/common/messagebox.js";
const detail = props => {
  let detail = "";
  const ALertModal = {
    isShow: props.model.ShowAlertModal,
    onClose: props.CloseModal,
    Message: props.model.Message
  };

  if (Object.keys(props.model.messageDetail).length) {
    detail = props.model.messageDetail.map(item => {
      let dd = "AM";
      let d = new Date(item.CreateDate);
      let hh = d.getHours();
      let m = d.getMinutes();
      if (hh >= 12) {
        hh = hh - 12;
        dd = "PM";
      }
      let dateObj = hh + ":" + m + "" + dd;

      let profilePic = item.SenderInfo.ProfilePicture;

      let classObj = item.SenderId == props.model.SenderId ? "right" : "left";

      return (
        <li class={classObj + " " + "clearfix"}>
          <Messagebox {...ALertModal} />
          <span class="chat-img1 pull-left">
            <img
              src={item.SenderInfo.ProfilePicture}
              onError={e => {
                e.target.src = "/Content/images/default-user.jpg";
              }}
              alt={"user " + item.SenderInfo.FirstName}
              class="img-circle"
            />
          </span>
          <div class="chat-body1 clearfix">
            <p>{item.Text}</p>
            <div class="chat_time pull-right">{dateObj}</div>
          </div>
        </li>
      );
    });
  }
  return (
    <div class="col-xs-12 col-md-7">
      <div class="message_section">
        <div class="InnerMessageSection">
          <div class="chat_area boxScroll">
            <ul class="list-unstyled">{detail}</ul>
          </div>
          <div class="message_write">
            <div class="panel-footer">
              <div class="input-group">
                <input
                  id="btn-input"
                  type="text"
                  value={props.model.Text}
                  class="form-control input-sm"
                  name="Text"
                  onChange={props.onChange}
                  placeholder="Type your message here..."
                />

                <span class="input-group-btn send">
                  <button
                    class="button button-inverse button-circle button-small"
                    onClick={props.onClick}
                    id="btn-chat"
                  >
                    <i class="fa fa-paper-plane" aria-hidden="true" />
                  </button>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = detail;



