import React from "react";
import Rating from "Components/common/starRating.js";
import ReviewModal from "Components/rating/reviewModal.js";

let resObj = "";
const BookList = props => {
  let bookedStatus = "";
  let display = "";

  if (props.model.status == 2) {
    bookedStatus = "Waiting for customer";
    display = "none";
  } else {
    bookedStatus = "Booked";
    display = "block";
  }

  resObj = props.model.quotesObj.map((item, i) => {
    let date = new Date(item.QuotesRequested.ModifiedDate);
    let bookedDate =
      date.getDate() + "-" + date.getMonth() + "-" + date.getFullYear();
    let status = props.status == 2 ? "Waiting for customer" : "Booked";
    let fromDate =
      item.QuotesRequested.Pallets.length > 0
        ? item.QuotesRequested.Pallets[0].FromDate
        : "";
    let toDate =
      item.QuotesRequested.Pallets.length > 0
        ? item.QuotesRequested.Pallets[0].ToDate
        : "";
    return (
      <div class="row payment" key={i}>
        <div class="cell">
          <div class="col-xs-12 col-sm-12 col-lg-12">
            <div class="payment_img">
              <div class="noitfy_img">
                <img
                  src={item.Userinfo.ProfilePicture}
                  onError={e => {
                    e.target.src = "/Content/images/default-user.jpg";
                  }}
                  alt="Logist Compare"
                  class="img-responsive"
                />
              </div>
            </div>

            <div class="star-payment">
              <h2>{item.Userinfo.FirstName + " " + item.Userinfo.LastName}</h2>
              <span
                onClick={() => {
                  props.getWarehouseDetail(item._id);
                }}
              >
                <label style={{ cursor: "pointer" }}>Rating</label>
                <div class="ratingBlk">
                  <Rating input={item.AvgReviews} />
                </div>
              </span>
            </div>
          </div>
        </div>
        <div class="cell">
          <div class="__content">
            <h3>{item.Name}</h3>
            <div class="row">
              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-3 form-group">
                <label>Space Required</label>
                <p>{item.QuotesRequested.TotalCapacity} sq feet</p>
              </div>
              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-3 form-group">
                <label>Time Period</label>
                <p>
                  {fromDate} - {toDate}
                </p>
              </div>

              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-3 form-group">
                <label>Warehouse Type</label>
                <p>{item.QuotesRequested.StorageType}</p>
              </div>
              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-3 form-group">
                <label>Final Cost</label>
                <p>£ {item.QuotesRequested.TotalCostBreakDown.FinalCost}</p>
              </div>

              <div
                class="col-xs-12 col-xss-6 col-sm-6 col-lg-3 form-group"
                style={{ display: display }}
              >
                <label>Date Booked</label>
                <p>{bookedDate}</p>
              </div>

              <div class="col-xs-12 col-xss-6 col-sm-6 col-lg-3 form-group">
                <label>Status</label>
                <p>{bookedStatus}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  });
  if (props.model.quotesObj.length == 0)
    resObj = (
      <div class="no-result-found">
        <p>No Result Found</p>
      </div>
    );
  return (
    <span>
      {resObj}
      <ReviewModal {...props} />
    </span>
  );
};
module.exports = BookList;



