import React, { Component } from "react";
import Link from "valuelink";
import Formula from "Services/formula.service.js";
import WarehouseService from "Services/warehouse.service.js";
import Add from "./add.js";
import { alert, confirm } from "notie";

const viewModel = {
  Name: "",
  Capacity: "",
  CapacityType: "Meter",
  Address: "",
  City: "",
  PostCode: "",
  Location: {
    type: "Point",
    coordinates: []
  },
  Addresses: [
    {
      Address: "",
      City: "",
      PostCode: "",
      Location: {
        type: "Point",
        coordinates: []
      }
    }
  ],
  Website: "",
  Starthrs: "09:00",
  Endhrs: "18:00",
  About: "",
  StorageType: "Pallet",
  Services: {},
  WarehouseType: {},
  Security: {},
  Images: [],
  PendingSpace: "",
  AvailableSpace: "0",
  UserId: "",
  OtherSpecify: "",
  OtherService: "",
  Bulk: {
    MaxLength: "",
    MaxLengthType: "Meter",
    MaxWidth: "",
    MaxWidthType: "Meter",
    MaxWeight: "",
    MaxWeightType: "Kgs",
    MaxHeight: "",
    MaxHeightType: "Meter",
    AvailabilityFrom: "",
    AvailabilityTo: "",
    ApxPrice: "",
    ApxPriceType: "Meter",
    TotalSpace: "",
    Description: ""
  },
  Pallet: {
    Type: { Name: "STD1", Value: "1.2" },
    Total: "",
    MaxHeight: "",
    MaxHeightType: "Meter",
    MaxWeight: "",
    MaxWeightType: "Kgs",
    ApxPrice: "",
    AvailabilityTo: "",
    AvailabilityFrom: "",
    Images: ""
  }
};
let links = {};
const NameRegex = /^[\w ]+$/;
const NumberRegex = /^[+-]?\d+(\.\d+)?$/;
const NumberDotRegex = /^[0-9.,]+$/;
const urlRegex = /^(http\:\/\/|https\:\/\/)?([a-z0-9][a-z0-9\-]*\.)+[a-z0-9][a-z0-9\-]*$/;
export default class addContainer extends Component {
  constructor() {
    super();
    this.state = { ...viewModel };
    this.onchange = this.onchange.bind(this);
    this.oncheckBox = this.oncheckBox.bind(this);
    this.onUploadimage = this.onUploadimage.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onBulkCalculation = this.onBulkCalculation.bind(this);
    this.onRadio = this.onRadio.bind(this);
    this.onTotalCalculation = this.onTotalCalculation.bind(this);
    this.onflatpicker = this.onflatpicker.bind(this);
    this.removeImage = this.removeImage.bind(this);
    this.addLatLng = this.addLatLng.bind(this);
    this.onkeyPress = this.onkeyPress.bind(this);
    this.onPaste = this.onPaste.bind(this);
    this.SubmitALl = this.SubmitALl.bind(this);
    this.onkeyPressWithoutDot = this.onkeyPressWithoutDot.bind(this);
  }

  onchange(e) {
    e.preventDefault();
    const { name, value, id } = e.target;
    let objectToUpdate = {};
    const palletName = e.target.selectedOptions
      ? e.target.selectedOptions[0].id
      : undefined;

    if (name == "Pallet") {
      let aa = this.onBulkCalculation(id, value);

      if (palletName) {
        objectToUpdate = {
          Pallet: {
            ...this.state.Pallet,
            [id]: { Name: palletName, Value: value }
          },
          PendingSpace: aa.pendingSpace,
          AvailableSpace: aa.availableSpace
        };
      } else {
        objectToUpdate = {
          Pallet: { ...this.state.Pallet, [id]: value },
          PendingSpace: aa.pendingSpace,
          AvailableSpace: aa.availableSpace
        };
      }
    } else if (name == "Bulk") {
      if (id == "TotalSpace") {
        let result = this.onTotalCalculation(value);
        objectToUpdate = {
          PendingSpace: result.pendingSpace,
          AvailableSpace: result.availableSpace,
          Bulk: {
            ...this.state.Bulk,
            [id]: value,
            MaxWidth: result.dimensions,
            MaxLength: result.dimensions
          }
        };
      } else {
        let aa = this.onBulkCalculation(id, value);

        objectToUpdate = {
          Bulk: { ...this.state.Bulk, [id]: value, TotalSpace: aa.totalSpace },
          PendingSpace: aa.pendingSpace,
          AvailableSpace: aa.availableSpace
        };
      }
    } else {
      if (name == "StorageType") {
        const PendingSpace = this.state.Capacity;
        objectToUpdate = {
          PendingSpace,
          [name]: value,
          Pallet: viewModel.Pallet,
          Bulk: viewModel.Bulk
        };
      } else if (name == "Capacity") {
        let PendingSpaceo =
          this.state.PendingSpace == "" ||
            this.state.PendingSpace == null ||
            isNaN(this.state.PendingSpace)
            ? 0
            : this.state.PendingSpace;
        let Capacity =
          this.state.Capacity == "" || this.state.Capacity == undefined
            ? 0
            : this.state.Capacity;

        Capacity = parseFloat(parseFloat(Capacity).toFixed(2));
        PendingSpaceo = parseFloat(parseFloat(PendingSpaceo).toFixed(2));
        let PendingSpaceN =
          Capacity == PendingSpaceo
            ? value == "" ? "0" : value
            : PendingSpaceo + (parseFloat(value) - parseFloat(Capacity));

        PendingSpaceN = parseFloat(PendingSpaceN).toFixed(2);
        objectToUpdate = {
          PendingSpace: PendingSpaceN,
          [name]: value
        };
      } else if (name == "CapacityType") {
        objectToUpdate = {
          [name]: value,
          Bulk: {
            ...this.state.Bulk,
            MaxLengthType: value,
            MaxWidthType: value,
            MaxHeightType: value,
            ApxPriceType: value
          },
          Pallet: {
            ...this.state.Pallet,
            MaxHeightType: value
          }
        };
      } else {
        objectToUpdate = {
          [name]: value
        };
      }
    }

    this.setState(objectToUpdate);
  }
  addAddress() {
    let Addresses = [...this.state.Addresses];
    Addresses.push({
      Address: "",
      City: "",
      PostCode: "",
      Location: {
        type: "Point",
        coordinates: []
      }
    });
    this.setState({ Addresses: Addresses });
  }
  addLatLng(index) {
    const addres = this.state.Addresses[index].PostCode; //this.state.PostCode;
    const addressPostcode = this.addressPostcode;
    addressPostcode.geocode(
      {
        address: addres
      },
      (result1, status1) => {
        if (status1 == google.maps.GeocoderStatus.OK) {
          const location = [
            result1[0].geometry.location.lat(),
            result1[0].geometry.location.lng()
          ];

          addressPostcode.geocode(
            {
              location: {
                lat: parseFloat(location[0]),
                lng: parseFloat(location[1])
              }
            },
            (result, status) => {
              if (status == google.maps.GeocoderStatus.OK) {
                // const PostCode =
                //   (result[0].address_components.find(
                //     x => x.types.indexOf("postal_code") == 0
                //   ) || "").short_name || "";
                const City =
                  (result[0].address_components.find(
                    x => x.types.indexOf("locality") == 0
                  ) || ""
                  ).long_name || "";
                const Address = result[0].formatted_address;
                let addresses = [...this.state.Addresses];
                let SelectedAddress = addresses[index];
                SelectedAddress.City = City;
                SelectedAddress.Location.coordinates = location;
                SelectedAddress.Address = Address;
                addresses[index] = SelectedAddress;
                this.setState({
                  Addresses: addresses
                });
              }
            }
          );
        }
      }
    );
    // const Location = this.state.Location;
    // Location.coordinates = location;

    // this.setState({ Location, City });
  }
  onkeyPress(e) {
    let matchValue = e.target.value + e.key;
    if (!NumberDotRegex.test(e.key) || (matchValue.match(/\./g) != null && 1 < matchValue.match(/\./g).length)) {
      e.preventDefault();
    }
  }
  onkeyPressWithoutDot(e) {
    if (!NumberRegex.test(e.key)) {
      e.preventDefault();
    }
  }
  onPaste(e) {
    e.preventDefault();
  }
  onflatpicker(name, value) {
    const nameval = name.split(".")[0];

    let objectToUpdate = {};
    switch (nameval) {
      case "Pallet":
        objectToUpdate = {
          Pallet: { ...this.state.Pallet, [name.split(".")[1]]: value }
        };
        break;
      case "Bulk":
        objectToUpdate = {
          Bulk: { ...this.state.Bulk, [name.split(".")[1]]: value }
        };
        break;
      default:
        objectToUpdate = {
          [name]: value
        };
        break;
    }

    this.setState(objectToUpdate);
  }
  oncheckBox(e) {
    const { name, checked, id } = e.target;

    switch (name) {
      case "service":
        this.setState({
          Services: { ...this.state.Services, [id]: checked }
        });
        break;
      case "type":
        this.setState({
          WarehouseType: { ...this.state.WarehouseType, [id]: checked }
        });
        break;
      case "security":
        this.setState({
          Security: { ...this.state.Security, [id]: checked }
        });
        break;
    }
  }
  onUploadimage(e) {
    e.preventDefault();
    const formData = new FormData();
    const { files, name } = e.target;
    if (
      files[0].type.includes("image") &&
      !files[0].type.includes("ico") &&
      !files[0].type.includes("gif")
    ) {
      formData.append("image", files[0]);
      formData.append("name", name);
      WarehouseService.UploadImage(formData)
        .then(response => {
          const imageslist = this.state.Images;
          imageslist.push(response.data);
          this.setState({
            Images: imageslist
          });
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
  removeImage(val) {
    const Images = this.state.Images.filter(e => e !== val);
    this.setState({ Images });
  }
  onRadio(e) {
    const { name, value, id } = e.target;
    if (name == "StorageType") {
      this.setState((prevState, props) => {
        const PendingSpace = parseFloat(prevState.Capacity).toFixed(2);
        return {
          PendingSpace,
          [name]: value,
          Pallet: {
            ...viewModel.Pallet,
            MaxHeightType: this.state.CapacityType
          },
          Bulk: {
            ...viewModel.Bulk,
            MaxLengthType: this.state.CapacityType,
            MaxWidthType: this.state.CapacityType,
            MaxHeightType: this.state.CapacityType,
            ApxPriceType: this.state.CapacityType
          }
        };
      });
    }
  }
  valid() {
    const errorObjects = Object.values(links).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }

      alert({
        type: "warning",
        text: errorObjects[0].error,
        time: 3, // optional, default = 3, minimum = 1,
        position: "bottom"
      });
      return false;
    } else return true;
  }

  SubmitConversion(wareObj) {
    if (wareObj.CapacityType == "Feet") {
      wareObj.Capacity = Formula.SqFeetToSqMeter(wareObj.Capacity);
      wareObj.AvailableSpace = Formula.SqFeetToSqMeter(wareObj.AvailableSpace);
      wareObj.PendingSpace = Formula.SqFeetToSqMeter(wareObj.PendingSpace);
      wareObj.Bulk.TotalSpace = Formula.SqFeetToSqMeter(
        wareObj.Bulk.TotalSpace
      );
    }
    //bulk conversion
    if (wareObj.Bulk.MaxWidthType == "Feet") {
      wareObj.Bulk.MaxWidth = Formula.FeetToMeter(wareObj.Bulk.MaxWidth);
    }
    if (wareObj.Bulk.MaxHeightType == "Feet") {
      wareObj.Bulk.MaxHeight = Formula.FeetToMeter(wareObj.Bulk.MaxHeight);
    }
    if (wareObj.Bulk.MaxLengthType == "Feet") {
      wareObj.Bulk.MaxLength = Formula.FeetToMeter(wareObj.Bulk.MaxLength);
    }
    if (wareObj.Bulk.MaxWeightType == "Lbs") {
      wareObj.Bulk.MaxWeight = Formula.PoundsToKg(wareObj.Bulk.MaxWeight);
    }
    //pallet conversion
    if (wareObj.Pallet.MaxHeightType == "Feet") {
      wareObj.Pallet.MaxHeight = Formula.FeetToMeter(wareObj.Pallet.MaxHeight);
    }

    if (wareObj.Pallet.MaxWeightType == "Lbs") {
      wareObj.Pallet.MaxWeight = Formula.PoundsToKg(wareObj.Pallet.MaxWeight);
    }

    return wareObj;
  }

  GetConversion(wareObj) {
    if (wareObj.CapacityType == "Feet") {
      wareObj.Capacity = Formula.SqMeterToSqFeet(wareObj.Capacity);
      wareObj.Bulk.TotalSpace = Formula.SqMeterToSqFeet(
        wareObj.Bulk.TotalSpace
      );
      wareObj.AvailableSpace = Formula.SqMeterToSqFeet(wareObj.AvailableSpace);
    }
    //bulk conversion
    if (wareObj.Bulk.MaxWidthType == "Feet") {
      wareObj.Bulk.MaxWidth = Formula.MeterToFeet(wareObj.Bulk.MaxWidth);
    }
    if (wareObj.Bulk.MaxHeightType == "Feet") {
      wareObj.Bulk.MaxHeight = Formula.MeterToFeet(wareObj.Bulk.MaxHeight);
    }
    if (wareObj.Bulk.MaxLengthType == "Feet") {
      wareObj.Bulk.MaxLength = Formula.MeterToFeet(wareObj.Bulk.MaxLength);
    }
    if (wareObj.Bulk.MaxWeightType == "Lbs") {
      wareObj.Bulk.MaxWeight = Formula.KgtoLb(wareObj.Bulk.MaxWeight);
    }
    //pallet conversion
    if (wareObj.Pallet.MaxHeightType == "Feet") {
      wareObj.Pallet.MaxHeight = Formula.MeterToFeet(wareObj.Pallet.MaxHeight);
    }

    if (wareObj.Pallet.MaxWeightType == "Lbs") {
      wareObj.Pallet.MaxWeight = Formula.KgtoLb(wareObj.Pallet.MaxWeight);
    }

    return wareObj;
  }
  onSubmit() {
    if (this.valid()) {
      if (this.state.PendingSpace >= 0) {
        let wareObj = Object.assign({}, this.state);

        wareObj = this.SubmitConversion(wareObj);

        WarehouseService.Add(wareObj)
          .then(response => {
            this.props.history.push("/warehouse/list");
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        alert({
          type: "warning",
          text: "Required storage is exceeding warehouse capacity",
          time: 3, // optional, default = 3, minimum = 1,
          position: "bottom"
        });
      }
    }
  }

  SubmitALl(wareObj) {
    WarehouseService.Add(wareObj)
      .then(response => {
        this.props.history.push("/warehouse/list");
      })
      .catch(err => {
        console.log(err);
      });
  }

  onBulkCalculation(column, value) {
    const Capacity =
      this.state.Capacity == "" || this.state.Capacity == null
        ? 0
        : Number(parseFloat(this.state.Capacity).toFixed(2));
    let totalSpace = 0;
    let pallettotalSpace = 0;
    let pendingSpace = 0;
    let availableSpace = 0;

    //bulk variables
    let width =
      column == "MaxWidth"
        ? value
        : this.state.Bulk.MaxWidth == "" || this.state.Bulk.MaxWidth == null
          ? 0
          : this.state.Bulk.MaxWidth;
    let length =
      column == "MaxLength"
        ? value
        : this.state.Bulk.MaxLength == "" || this.state.Bulk.MaxLength == null
          ? 0
          : this.state.Bulk.MaxLength;

    //Pallet variables
    let area =
      column == "Type"
        ? value
        : this.state.Pallet.Type.Value == "" ||
          this.state.Pallet.Type.Value == null
          ? 0
          : this.state.Pallet.Type.Value;
    let total =
      column == "Total"
        ? value
        : this.state.Pallet.Total == "" || this.state.Pallet.Total == null
          ? 0
          : this.state.Pallet.Total;

    if (
      this.state.CapacityType == "Feet" &&
      this.state.Bulk.MaxLengthType != "Feet"
    )
      length = Formula.MeterToFeet(length);

    if (
      this.state.CapacityType != "Feet" &&
      this.state.Bulk.MaxLengthType == "Feet"
    )
      length = Formula.FeetToMeter(length);

    if (
      this.state.CapacityType == "Feet" &&
      this.state.Bulk.MaxWidthType != "Feet"
    )
      width = Formula.MeterToFeet(width);

    if (
      this.state.CapacityType != "Feet" &&
      this.state.Bulk.MaxWidthType == "Feet"
    )
      width = Formula.FeetToMeter(width);

    if (this.state.CapacityType == "Feet") {
      let areatotal = area * total;
      pallettotalSpace = Formula.SqMeterToSqFeet(areatotal);
    } else {
      pallettotalSpace = area * total;
    }

    totalSpace = parseFloat(width * length);
    availableSpace = Number(
      parseFloat(totalSpace + pallettotalSpace).toFixed(2)
    );
    pendingSpace = parseFloat(parseFloat(Capacity - availableSpace).toFixed(2));
    totalSpace = parseFloat(totalSpace).toFixed(2);
    return { totalSpace, pendingSpace, availableSpace, value };
  }

  onTotalCalculation(value) {
    //const pendingSpace = parseFloat(this.state.PendingSpace - value).toFixed(2);
    value = value == "" ? "0" : value;
    const Capacity =
      this.state.Capacity == "" || this.state.Capacity == null
        ? 0
        : Number(parseFloat(this.state.Capacity).toFixed(2));
    let totalSpace = 0;
    let pallettotalSpace = 0;
    let pendingSpace = 0;
    let availableSpace = 0;

    //bulk variables
    let width = 0;
    let length = 0;

    //Pallet variables
    let area =
      this.state.Pallet.Type.Value == "" || this.state.Pallet.Type.Value == null
        ? 0
        : this.state.Pallet.Type.Value;

    let total =
      this.state.Pallet.Total == "" || this.state.Pallet.Total == null
        ? 0
        : this.state.Pallet.Total;

    if (this.state.CapacityType == "Feet") area = Formula.SqMeterToSqFeet(area);

    pallettotalSpace = area * total;

    totalSpace = 0;
    availableSpace = Number(
      parseFloat(totalSpace + pallettotalSpace + parseFloat(value)).toFixed(2)
    );
    pendingSpace = parseFloat(parseFloat(Capacity - availableSpace).toFixed(2));
    totalSpace = parseFloat(totalSpace).toFixed(2);
    const dimensions = parseFloat(Math.sqrt(value)).toFixed(2);
    return { dimensions, pendingSpace, availableSpace };
  }

  componentWillMount() {
    if (!this.props.match.params.id) {
      const data = {
        Services: {},
        WarehouseType: {},
        Security: {}
      };
      WarehouseService.Services()
        .then(response => {
          this.setState({ Services: response.data });
        })
        .catch(error => {
          console.log(error);
        });
      WarehouseService.Types()
        .then(response => {
          this.setState({ WarehouseType: response.data });
        })
        .catch(error => {
          console.log(error);
        });
      WarehouseService.Security()
        .then(response => {
          this.setState({ Security: response.data });
        })
        .catch(error => {
          console.log(error);
        });
    } else {
      WarehouseService.Get(this.props.match.params.id)
        .then(response => {
          const wareObj = this.GetConversion(response.data);
          this.setState({ ...wareObj });
        })
        .catch(error => {
          console.log(error);
        });
    }
  }

  componentDidMount() {
    this.addressPostcode = new google.maps.Geocoder();

    // this.addressAutocomplete = new google.maps.places.Autocomplete(
    //   this.AddressAutocomplete,
    //   { types: ["(cities)"] }
    // );

    //this.addressAutocomplete.addListener("place_changed", this.addLatLng);
  }
  onAddressChange(e) {

    const { name, value } = e.target;
    let index = 0;
    if (name.indexOf("PostCode") > -1) {
      index = parseInt(name.replace("PostCode", ""));
    }
    if (name.indexOf("Address") > -1) {
      index = parseInt(name.replace("Address", ""));
    }
    if (name.indexOf("City") > -1) {
      index = parseInt(name.replace("City", ""));
    }
    let addresses = [...this.state.Addresses];
    let SelectedAddress = addresses[index];
    SelectedAddress[name.replace(index.toString(), "")] = value;
    addresses[index] = SelectedAddress;
    this.setState({ Addresses: addresses });
  }
  removeAddress(index) {
    let addresses = [...this.state.Addresses];
    addresses.splice(index, 1);
    this.setState({ Addresses: addresses });
  }
  render() {
    links = {
      nameLink: Link.state(this, "Name")
        .check(x => x, "Warehouse Name is Required")
        .check(x => x.trim(), "Warehouse name is required"),
      // .check(
      //   x => NameRegex.test(x),
      //   "Warehouse Name requires Chatacters only"
      // ),
      capacityLink: Link.state(this, "Capacity")
        .check(x => x, "Warehouse Capacity Required")
        .check(
          x => NumberRegex.test(x),
          "Warehouse Capacity requires Number only"
        ),
      // addressLink: Link.state(this, "Address").check(
      //   x => x,
      //   "Address Required"
      // ),
      // cityLink: Link.state(this, "City").check(x => x, "City Required"),
      // postCodeLink: Link.state(this, "PostCode").check(
      //   x => x,
      //   "PostCode Required"
      // ),
      websiteLink: Link.state(this, "Website")
      // .check(x => x, "Website url Required")
      // .check(x => urlRegex.test(x), "url only")
    };

    const params = {
      model: this.state,
      change: this.onchange,
      oncheckBox: this.oncheckBox,
      onSubmit: this.onSubmit,
      onUploadimage: this.onUploadimage,
      onRadio: this.onRadio,
      onflatpicker: this.onflatpicker,
      removeImage: this.removeImage,
      onkeyPress: this.onkeyPress,
      onPaste: this.onPaste,
      addLatLng: this.addLatLng,
      onkeyPressWithoutDot: this.onkeyPressWithoutDot,
      links,
      addAddresses: this.addAddress.bind(this),
      RemoveAddress: this.removeAddress.bind(this),
      setRef: el => (this.AddressAutocomplete = el),
      onAddressChange: this.onAddressChange.bind(this)
    };

    return <Add {...params} />;
  }
}



