import React from "react";
import MetaTags from 'react-meta-tags';
const notification = props => {
  const list = props.model.Result.map((item, key) => {
    let type = "";
    if (props.model.IsProvider) {
      if (item.Type == "Quotes") {
        type = (
          <a href={"/warehouse/quotes/" + item.TypeId} key={key}>
            <i class="fa fa-quote-right" aria-hidden="true" />Quotation
          </a>
        );
      } else if (item.Type == "Rating") {
        type = (
          <a href="javascript:void(0);" data-toggle="modal" key={key}>
            <i class="fa fa-star" aria-hidden="true" />Rate us
          </a>
        );
      } else if (item.Type == "Payment") {
        type = (
          <a href="/warehouse/payments" key={key}>
            <i class="fa fa-cog" />Payment
          </a>
        );
      }
    } else {
      if (item.Type == "Quotes") {
        type = (
          <a href={"/user/quotepayment/" + item.TypeId} key={key}>
            <i class="fa fa-quote-right" aria-hidden="true" />Quotation
          </a>
        );
      } else if (item.Type == "Rating") {
        type = (
          <a href="javascript:void(0);" data-toggle="modal" key={key}>
            <i class="fa fa-star" aria-hidden="true" />Rate us
          </a>
        );
      } else if (item.Type == "Payment") {
        type = (
          <a href="javascript:void(0);" key={key}>
            <i class="fa fa-cog" />Payment
          </a>
        );
      }
    }

    return (
      <div
        class="row notification"
        key={key}
        style={{ background: item.Status == 0 ? "bisque" : "" }}
      >
        <div class="left_img">
          <div class="noitfy_img">
            <img
              src={item.Sender.Image}
              alt="Logist Compare"
              class="img-responsive"
              onError={e => {
                e.target.src = "/Content/images/default-user.jpg";
              }}
            />
          </div>
        </div>

        <div class="right_text">
          <div class="notify_text">
            <h2>{item.Sender.Name}</h2>
            <p>{item.Text}</p>
          </div>
        </div>

        <div class="notificationRight">{type}</div>
      </div>
    );
  });
  if (list.length == 0)
    list.push(
      <div class="no-result-found">
        <p>No Notifications Found</p>
      </div>
    );
  else if (props.model.showLoadmore)
    list.push(
      <div class="no-result-found">
        <button
          class="btn btn-primary btn-round-lg btn-lg"
          onClick={() => {
            props.Loadmore(props.model.Result.length + 10);
          }}
        >
          Load more
        </button>
      </div>
    );
  return (
    <div>
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="header">
        <h2>Notification</h2>
      </div>
      <div class="mainContent boxScroll">
        <div class="sStorageWarp notifyWrap">{list}</div>
      </div>
    </div>
  );
};

export default notification;



