import React from "react";

const Modal = props => {
  const loginPath = props.IsBuyer ? "/findspace/login" : "/warehouse/login";
  const verificationModal = {
    ModalCss: props.ShowverificationModal ? "in" : "",
    ModalStyle: { display: props.ShowverificationModal ? "block" : "none" },
    onClose: props.onClose,
    onResend: props.onResend
  };
  const AlertModal = {
    ModalCss: props.ShowAlertModal ? "in" : "",
    ModalStyle: { display: props.ShowAlertModal ? "block" : "none" },
    ModalBody: props.ModalBody,
    onClose: props.onClose
  };

  return (
    <div>
      <div
        className={"modal fade " + verificationModal.ModalCss}
        id="myModal"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-2"
        style={verificationModal.ModalStyle}
      >
        <div className="modal-dialog" role="document">
          <div className="modal-content forgot">
            <div
              className="closewrap confirm-close"
              onClick={verificationModal.onClose}
            >
              <button type="button" className="close" data-dismiss="modal">
                <i
                  className="fa fa-times"
                  aria-hidden="true"
                  onClick={verificationModal.onClose}
                />
              </button>
            </div>

            <div className="confirm-mail-head">
              <h2>E-mail Confirmation</h2>
              <p>
                A verification link is sent to your email. Please verify your
                account.
              </p>
            </div>
            <div className="modal-body confirm-mail">
              <div class="form-group">
                <a
                  href={loginPath}
                  class="button button-border button-primary button-uppercase margin-top-3x"
                >
                  Ok
                </a>
              </div>
              <div className="confirm">
                <p>
                  Didn’t receive confirmation link?{" "}
                  <a
                    href="javascript:void(0)"
                    onClick={verificationModal.onResend}
                  >
                    Resend confirmation mail
                  </a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + AlertModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={AlertModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={AlertModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Alert !</h2>
                <p>{AlertModal.ModalBody}</p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Ok"
                    onClick={AlertModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = Modal;



