import React from "react";
import Detail from "./blogdetail.js";
import BlogService from "Services/blog.service.js";
export default class BlogDetail extends React.Component {
  constructor() {
    super();
    this.state = {
      blogObj: {},
      metaDesc: ""
    };
  }
  componentWillMount() {
    const blogId = this.props.match.params.id;
    BlogService.getBlog(blogId).then(response => {
      this.setState({ blogObj: response.data[0] });
    });
  }
  goBack() {
    this.props.history.goBack();
  }
  componentDidUpdate() {
    if (this.state.metaDesc == "") {
      let description = document.getElementById("descriptionId").innerText.substring(0, 200);
      this.setState({ metaDesc: description });
    }
  }

  render() {
    const obj = {
      model: this.state.blogObj,
      metaDesc: this.state.metaDesc,
      goBack: this.goBack.bind(this)
    };
    return <Detail {...obj} />;
  }
}



