import React, { Component } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import BankDetail from "./addbankdetail.js";
import UserService from "../../services/user.service.js";
import PaymentService from "Services/payment.service.js";
import Helper from "Helpers/utility.helper.js";
import Link from "valuelink";
import Loader from "Components/common/loader.js";
const NameRegex = /^[a-zA-Z\s]*$/;
const NumberRegex = /^\d+$/;
const DOBRegex = /([12]\d{3}-(0[1-9]|1[0-2])-(0[1-9]|[12]\d|3[01]))/;
const initialState = {
  Name: "",
  BeneficiaryName: "",
  BusinessName: "",
  SortCode: "",
  AccountNumber: "",
  Address: "",
  City: "",
  Region: "",
  PostCode: "",
  PaymentType: "0",
  ShowAlert: false,
  ModalBody: "",
  BirthDate: "",
  ShowSuccess: false,
  isLoading: false
};
let validationLink = {};
export default class BankDetailContainer extends Component {
  constructor() {
    super();
    this.state = { ...initialState };
  }
  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }
  valid() {
    const errorObjects = Object.values(validationLink).filter(x => !!x.error);
    if (errorObjects.length) {
      const key = errorObjects.filter(x => !x.value).map(x => x.key)[0];
      if (key) {
        this.setState({ [key]: undefined });
      }
      return false;
    } else return true;
  }
  handleSubmit() {
    if (this.valid()) {
      this.setState({ isLoading: true });
      PaymentService.AddBankDetails(this.state)
        .then(response => {
          if (!response.data.Status) {
            this.setState({
              ShowAlert: true,
              ModalBody: response.data.Message,
              isLoading: false
            });
          } else {
            // this.props.history.push("/addressdetail");
            this.setState({
              ShowSuccess: true,
              isLoading: false
            });
          }
        })
        .catch(error => {
          this.setState({
            ShowAlert: true,
            ModalBody: error.Message,
            isLoading: false
          });
        });
    }
  }
  addLatLng() {
    const addres = this.state.PostCode;
    const addressPostcode = this.addressPostcode;
    addressPostcode.geocode(
      {
        address: addres
      },
      (result1, status1) => {
        if (status1 == google.maps.GeocoderStatus.OK) {
          const location = [
            result1[0].geometry.location.lat(),
            result1[0].geometry.location.lng()
          ];

          addressPostcode.geocode(
            {
              location: {
                lat: parseFloat(location[0]),
                lng: parseFloat(location[1])
              }
            },
            (result, status) => {
              if (status == google.maps.GeocoderStatus.OK) {
                // const PostCode =
                //   (result[0].address_components.find(
                //     x => x.types.indexOf("postal_code") == 0
                //   ) || "").short_name || "";
                const City =
                  (result[0].address_components.find(
                    x => x.types.indexOf("locality") == 0
                  ) || ""
                  ).long_name || "";
                const Address = result[0].formatted_address;
                const Region = City;
                this.setState({
                  City,
                  Address,
                  Region
                });
              }
            }
          );
        }
      }
    );
    // const Location = this.state.Location;
    // Location.coordinates = location;

    // this.setState({ Location, City });
  }
  componentDidMount() {
    this.addressPostcode = new google.maps.Geocoder();

    // this.addressAutocomplete = new google.maps.places.Autocomplete(
    //   this.AddressAutocomplete,
    //   { types: ["(cities)"] }
    // );

    //this.addressAutocomplete.addListener("place_changed", this.addLatLng);
  }
  CloseModal() {
    this.setState({
      ShowAlert: false,
      ModalBody: ""
    });
  }
  render() {
    validationLink = {
      nameLink: Link.state(this, "Name")
        .check(x => x, "Bank name is required")
        .check(x => NameRegex.test(x), "Characters allowed only")
        .check(x => x.trim(), "Bank Name is Required"),
      beneficiaryLink: Link.state(this, "BeneficiaryName")
        .check(x => x, "Beneficiary name is required")
        .check(x => NameRegex.test(x), "Characters allowed only"),
      sortCodeLink: Link.state(this, "SortCode")
        .check(x => x, "Sort Code is required")
        .check(x => NumberRegex.test(x), "Enter numbers only")
        .check(x => x.length >= 6, "Sort code must be of six number"),
      accountNumberLink: Link.state(this, "AccountNumber")
        .check(x => x, "Account Number is required")
        .check(x => NumberRegex.test(x), "Enter numbers only"),
      addressLink: Link.state(this, "Address").check(
        x => x,
        "Address Required"
      ),
      cityLink: Link.state(this, "City").check(x => x, "City Required"),
      regionLink: Link.state(this, "Region").check(x => x, "Region Required"),
      postCodeLink: Link.state(this, "PostCode").check(
        x => x,
        "Postal Code Required"
      ),
      paymentTypeLink: Link.state(this, "PaymentType").check(
        x => x,
        "Please select a payment type"
      ),
      birthDateLink: Link.state(this, "BirthDate")
        .check(x => x, "DOB is required")
        .check(x => DOBRegex.test(x), "Please enter a valid DOB (yyyy-mm-dd)")
    };
    const bankDetailProps = {
      change: this.handleChange.bind(this),
      onSubmit: this.handleSubmit.bind(this),
      setRef: el => (this.AddressAutocomplete = el),
      addLatLng: this.addLatLng.bind(this),
      ShowAlert: this.state.ShowAlert,
      ModalBody: this.state.ModalBody,
      onClose: this.CloseModal.bind(this),
      ShowSuccess: this.state.ShowSuccess,
      validationLink
    };
    if (this.state.isLoading) return <Loader />;
    return <BankDetail {...bankDetailProps} />;
  }
}



