import React from "react";
import VInput from "Components/common/VInput";
import MetaTags from 'react-meta-tags';
const ResetPassword = props => {
  const SuccessModal = {
    ModalCss: props.ShowSuccessModal ? "in" : "",
    ModalStyle: { display: props.ShowSuccessModal ? "block" : "none" },
    ModalBody: props.Message,
    onClose: props.onClose
  };
  const ALertModal = {
    ModalCss: props.ShowAlertModal ? "in" : "",
    ModalStyle: { display: props.ShowAlertModal ? "block" : "none" },
    ModalBody: props.Message,
    onClose: props.onClose
  };
  return (
    <div class="container">
      <MetaTags>
        <title>Logist Compare</title>
      </MetaTags>
      <div class="row">
        <div class="col-xs-12">
          <div class="addWhWarpper">
            <h2 class="h2Title margin-bottom-6x reset-title">Reset Password</h2>
            <form class="form-horizontal" method="post">
              <div class="form-group">
                <label class="col-md-4 control-label" for="piNewPass">
                  New Password
                </label>
                <div class="col-md-5">
                  <VInput
                    id="piNewPass"
                    name="NewPassword"
                    type="password"
                    placeholder=""
                    class="form-control input-md"
                    valueLink={props.links.passwordLink}
                  />
                </div>
              </div>
              <div class="form-group">
                <label class="col-md-4 control-label" for="piNewPassRepeat">
                  Confirm Password
                </label>
                <div class="col-md-5">
                  <VInput
                    id="piNewPassRepeat"
                    name="ConfirmPassword"
                    type="password"
                    placeholder=""
                    class="form-control input-md"
                    valueLink={props.links.confirmPasswordLink}
                  />
                </div>
              </div>
              <div class="form-group">
                <label class="col-md-4 control-label" for="bCancel" />
                <div class="col-md-8">
                  <button
                    id="bGodkend"
                    name="bGodkend"
                    class="btn btn-success"
                    type="submit"
                    onClick={props.submitPassword}
                  >
                    Submit
                  </button>
                  <a class="btn btn-danger btn-lg" href="/">
                    Cancel
                  </a>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div
        class={"modal fade " + SuccessModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={SuccessModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={SuccessModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Success</h2>
                <p>
                  {SuccessModal.ModalBody}
                </p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <a
                    href="/"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                  >
                    Ok
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div
        class={"modal fade " + ALertModal.ModalCss}
        id="myModal-ALert"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="myModalLabel-Alert"
        style={ALertModal.ModalStyle}
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content forgot">
            <div class="closewrap">
              <button
                type="button"
                class="close"
                data-dismiss="modal"
                onClick={ALertModal.onClose}
              >
                &times;
              </button>
            </div>
            <div class="modal-body forgot-password">
              <div class="headpassword">
                <h2>Alert !</h2>
                <p>
                  {ALertModal.ModalBody}
                </p>
              </div>

              <div class="formWarp password">
                <div class="form-group">
                  <input
                    type="button"
                    class="button button-border button-primary button-uppercase margin-top-3x"
                    value="Ok"
                    onClick={ALertModal.onClose}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
module.exports = ResetPassword;



